import React from 'react';
import { useNavigate } from 'react-router-dom';
import UseContext from '../../../../../context/useContext';
import { COLORS } from '../../../../../helpers/colors';
import { CBCard } from './CBCard';

function SecondRow({ DATA }) {
  const { aloDisco, aloMap, aloPeople } = DATA || {};
  const { adjustMany, href } = React.useContext(UseContext);
  const navigate = useNavigate();

  const onPress = () => {
    adjustMany({ nav: 'APPLICANT-VIEW', Ponent: null, quickLink: false });
    return navigate(`/${href}/applicant`);
  };

  const onclick = () => {
    adjustMany({ Ponent: null, quickLink: false });
    return navigate(`/${href}/google`);
  };

  return (
    <div className="grid-3" style={{ marginBottom: '1.5rem' }}>
      <CBCard
        title={"Meter Providers' Allocation to DISCOs"}
        icon={'fas fa-list'}
        right={true}
        info={aloMap || 0}
        txtColor={'white'}
        iconSize={'1.5rem'}
        color={COLORS._primary_300}
        onPress={() => {
          adjustMany({ nav: 'MAP-VIEW' });
          return navigate(`/${href}/view-meter`);
        }}
      />
      <CBCard
        title={'Meter Received by DISCOs'}
        right={true}
        color={COLORS._primary_400}
        icon={'fas fa-lightbulb'}
        info={aloDisco || 0}
        iconSize={'1.5rem'}
        txtColor={'white'}
        onPress={() => {
          adjustMany({ nav: 'DISCO-VIEW' });
          return navigate(`/${href}/view-meter`);
        }}
      />
      <CBCard
        title={'DISCOs Allocation to Installer/Properties'}
        info={aloPeople || 0}
        txtColor={'white'}
        icon={'fas fa-users'}
        right={true}
        color={COLORS._primary_700}
        iconSize={'1.5rem'}
        //  onPress={() => adjustMany({ Ponent: () => Appear({ onPress, onclick }), quickLink: true })}
        onPress={() => adjustMany({ errors: 'Google map is deactivated' })}
      />
    </div>
  );
}

function Appear({ onPress, onclick }) {
  return (
    <>
      {/* <h3 style={{ textAlign: 'center' }}>Notification Box</h3> */}
      <div className="flex" style={{ padding: '2rem' }}>
        <button
          className="btx"
          onClick={onPress}
          style={{
            padding: '2rem',
            margin: '0 10px',
          }}
        >
          View Meter Request Details only
        </button>

        <button
          className="btx"
          onClick={onclick}
          style={{
            padding: '2rem',
            margin: '0 10px',
            pointerEvents: 'none',
            background: 'grey',
          }}
        >
          View Meter Request Locations on Google
        </button>
      </div>
    </>
  );
}

export default SecondRow;
