import React from 'react'



function Cardio({ icon, bg, iconColor, iconSize}) {
  return (
    <div className='card-self flex-middle' style={{ 
          width: '4rem',
          height: '3.8rem',
          borderRadius: '.5rem',
          background: bg || 'white',
          margin: '1rem 0'
    }}>
      {icon && <i className={icon} style={{ 
          fontSize: iconSize || '1.5rem', 
          textAlign: 'center', 
          color: iconColor || 'white'}}
       />}
    </div>
  )
}

export default Cardio
