import React from "react";
import { BarChart } from "./LineGraph";





export default function SingleChart({ height, width, title, BODY, UTILS, LABEL, LABEL0 }){

    return(
        <div style={{ 
            background: 'white', 
            border: '1px solid #ccc',
            // height: height || 300
        }}>
            
            <div className="flex-side" style={{ 
                marginBottom: 10, 
                borderBottom: '1px solid #ccc',
                padding: '0.2rem 1rem'
            }}>
                <p style={{ padding: '0.8rem 0', 
                    fontSize: '1.2rem'}}>
                        {title || 'Graph Title'}
                </p>

            </div>

            <div style={{ 
                background: 'white', 
                padding: '0.7rem 1rem 1rem', 
            }}>

           <BarChart 
                WIDTH={height || 600}
                HEIGHT={width || 300}
                LABELS={BODY.map(e => e.name)}
               LABEL={LABEL}
                DATA={BODY.map(e => e.view)}
            />

         
            </div>

            

        </div>
        
    )
}