import * as React from 'react'
import { useNavigate } from 'react-router-dom'
import UseContext from '../../../../context/useContext'
import { styling } from '../../../../helpers/css/css'
import { tryCatchLinkUpToServer } from '../../../../options/JSDOWNLOADS'
import Structure from '../../Structure'
import DiscoSide from '../../DiscoSide'
import { BTN } from '../../../cbs/pages/dashbaord/helpers/CBCard'
import { InputIcon } from '../../../../helpers/FormInputs'
import { mainAloPage } from './allocationLogics'

function GMr() {
  const { singleItem, adjustMany, href, user } = React.useContext(UseContext)
  const navigate = useNavigate()

  const [form, setForm] = React.useState({ ...mainAloPage })

  const loadAddress = async (where) => {
    adjustMany({ loading: true })
    if (!where) {
      const res = await tryCatchLinkUpToServer({
        method: 'GET',
        urlRoute: 'user',
        data: { role: 'INSTALLER' },
        params: `search=selection&skip=ALLOCATE:${user.role}`,
      })

      if (res.success) {
        const opt = res.data.length > 0 ? res.data.map((o) => o.fullName) : []
        setForm({
          ...form,
          opt,
          nb3: res.data,
          pp: user.franchiseStates,
          form74: 0,
        })
        adjustMany({ loading: false })
      } else navigate(`/${href}/allocation`)
    } else {
      const re = await tryCatchLinkUpToServer({
        method: 'GET',
        urlRoute: 'form74',
        params: `is_certified=true&has_allocation=false&state=${where}`,
      })
      if (re.success) {
        const alo =
          re.data.length > 0 ? re.data.map((o) => o._id || o.customer_id) : []
        setForm({ ...form, form74: re.data.length, alo })
        adjustMany({ loading: false })
      } else navigate(`/${href}/allocation`)
    }
  }

  React.useEffect(() => {
    ;(async function () {
      adjustMany({
        success:
          "You won't be able to proceed if all applicant properties are not certified",
      })
      if (!singleItem) return navigate(`/${href}/dashboard`)
      loadAddress()
    })()

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const { allocateAll, piece, ed, owner, recipient, form74 } = form
  const onChange = (name, value) => setForm({ ...form, [name]: value })

  return (
    <>
      <div
        style={{
          width: '85%',
          margin: '0 auto',
          background: 'white',
          border: '1px dotted #333',
          padding: '1rem',
          marginTop: '1rem',
        }}
      >
        <h3
          style={{
            padding: 10,
            width: '100%',
            fontSize: '1.5rem',
          }}
        >
          {singleItem ? singleItem.header : ''}
        </h3>
        <p
          style={{
            padding: 10,
            width: '100%',
            fontSize: '1.3rem',
          }}
        >
          Total number in this category:
          {singleItem ? singleItem.data : ''} Meter(s)
        </p>

        <div
          className={'grid-2'}
          style={{
            background: '#f2f2f2',
            padding: '0.5rem',
          }}
        >
          <InputIcon
            title={'Locations of Installation'}
            onChange={onChange}
            name={'office'}
            value={form.office}
            select={true}
            options={form.pp}
            style={{ borderRadius: 5, flex: 1, marginTop: 1 }}
          />

          <InputIcon
            title={`Certified Properties Finder`}
            onChange={onChange}
            name={'form74'}
            value={form74}
            isEditable={true}
            style={{ borderRadius: 5, flex: 1, marginTop: 1 }}
            onPress={() => loadAddress(form.office)}
            rightIcon={'fa fa-cloud-upload'}
          />
        </div>

        <div
          className={'grid-2'}
          style={{
            background: '#f2f2f2',
            padding: '0.5rem',
          }}
        >
          <InputIcon
            title={'Select Recipient'}
            onChange={onChange}
            name={'recipient'}
            value={recipient}
            select={true}
            options={['INSTALLER']}
            style={{ borderRadius: 5, flex: 1, marginTop: 1 }}
          />

          <InputIcon
            title={recipient ? `Select ${recipient} Name` : 'Recipient Name'}
            onChange={onChange}
            name={'owner'}
            value={owner}
            select={true}
            options={form.opt}
            style={{ borderRadius: 5, flex: 1, marginTop: 1 }}
          />
        </div>

        <div
          style={{
            ...styling.flexed({}),
            background: '#f2f2f2',
            padding: '0.5rem',
            margin: '1rem 0',
          }}
        >
          <InputIcon
            title={'Enter number to be allocated'}
            onChange={onChange}
            name={'piece'}
            value={piece}
            style={{ borderRadius: 5, flex: 1, marginTop: 1 }}
            isEditable={ed}
            typed={'number'}
          />

          <div style={{ ...styling.flexed({ justify: 'center' }), flex: 1 }}>
            <input
              type={'checkbox'}
              name={'allocateAll'}
              onChange={() => {
                if (piece !== '' || !allocateAll) {
                  setForm({ ...form, allocateAll: true, piece: '', ed: true })
                } else if (allocateAll) {
                  setForm({ ...form, allocateAll: false, ed: false })
                } else onChange('allocateAll', !allocateAll)
              }}
              checked={allocateAll}
            />
            <p>Allocate all</p>
          </div>
        </div>

        <BTN
          style={{
            pointerEvents:
              form.opt[0] == null || form74 === 0 ? 'none' : 'visible',
          }}
          title={
            form.opt[0] == null || form74 === 0
              ? 'Fetching Dependencies...'
              : 'Load Applicant Data'
          }
          onPress={async () => {
            adjustMany({
              showAlerts: true,
              success: `Please note that only ${form74} number of meter(s) will be
                            allocated to ${owner} for installation`,
              proceed: async () => {
                if (!allocateAll && !piece)
                  return adjustMany({ errors: `Allocation parameter error` })
                else if (form74 > singleItem?.data)
                  return adjustMany({
                    errors: `Applicant record of ${form74} is above available meter(s)`,
                  })
                else if (!form.office)
                  return adjustMany({
                    errors: `Area Office/Service Center input is required`,
                  })

                adjustMany({ loading: true })

                const instl = form.nb3.find((a) => a.fullName === owner)

                const res = await tryCatchLinkUpToServer({
                  method: 'PUT',
                  urlRoute: `disco/${singleItem?.id}`,
                  params: `search=ALLOCATE&name=${singleItem?.name}&limit=${form74}`,
                  data: {
                    disco_allocation_to: `${instl.fullName}: ${instl.email}`,
                    allocation_customer_id: form.alo,
                  },
                })

                if (res.success) {
                  adjustMany({
                    loading: false,
                    success: res.data,
                    showAlerts: false,
                    errors: null,
                  })
                  navigate(`/${href}/allocation`)
                } else
                  adjustMany({
                    loading: false,
                    errors: res.error,
                    showAlerts: false,
                    success: null,
                  })
              },
            })
          }}
        />
      </div>
    </>
  )
}

export default function MainAloPage() {
  return (
    <>
      <Structure Side={DiscoSide} BodyPage={GMr} />
    </>
  )
}
