import React from 'react';
import { useNavigate } from 'react-router-dom';
import UseContext from '../../context/useContext';
import { IonIcon } from '../../layout/sidebar/sideHelper';

export default function CBSide() {
  const navigate = useNavigate();
  const { href } = React.useContext(UseContext);

  return (
    <div className="side-icons">
      <IonIcon
        title={'Dashboard'}
        icon={'fas fa-bar-chart'}
        onPress={() => navigate(`/${href}`)}
        name={'dashboard'}
      />

      {/* <IonIcon
        title={'Exception Report'}
        icon={'far fa-folder'}
        //   sideIcon={'fas fa-chevron-down'}
        onPress={() => navigate(`/${href}/exception`)}
        name={'exception'}
      /> */}

      {/* <IonIcon
            title={'Generate CSV Report'}
            icon={'far fa-folder'}
          //   sideIcon={'fas fa-chevron-down'}
            onPress={() => navigate(`/${href}/report`)}
            name={'report'}
       /> */}
    </div>
  );
}
