import * as React from 'react';
import UseContext from '../../../../../context/useContext';
import { HeaderItems } from '../../../../../helpers/TypedInput';
import Headers from '../../../../../layout/Headers';
import { UREL } from '../../../../../options/JSDOWNLOADS';

export const PropertyImageA = ({ form }) => {
  const { adjustMany } = React.useContext(UseContext);
  const { propertyImage } = form;

  const onSubmit = (e) => {
    e.preventDefault();
    adjustMany({ skip: 'send' });
  };

  return (
    <>
      <Headers
        HeaderItems={function () {
          return (
            <HeaderItems
              title={'CUSTOMER RECORD MODULE'}
              onPress={() => adjustMany({ skip: 'address' })}
            />
          );
        }}
        SideIcons={function () {
          return (
            <div>
              <p>Page 4 of 4 </p>
            </div>
          );
        }}
      />

      <form
        onSubmit={onSubmit}
        style={{
          background: 'white',
          margin: '0 auto',
          width: '80%',
          padding: '1.3rem 1rem 2rem',
        }}
      >
        <h3>Applicant Property Image</h3>

        <div style={{ width: '100%' }}>
          <img
            src={`${UREL}/uploads/${propertyImage}`}
            alt="Property portrait"
            style={{
              width: '60%',
              height: '40%',
              margin: '0 auto',
              marginTop: 10,
            }}
          />
        </div>

        <input
          type={'submit'}
          value={'Go Back'}
          className={'btn'}
          style={{ marginTop: 20 }}
          onClick={() => adjustMany({ skip: 'personal' })}
        />
      </form>
    </>
  );
};
