import React from 'react';
import './App.css';
import './Styles.css'
import GeneralState from './component/context/general/GeneralState';
import HelperState from './component/context/helpers/HelperState';
import UseState from './component/context/UseState';
import LOGIN from './component/routes/Login';
import PageLoadRender from './component/routes/PageLoadRender';
import AlertConfig from './component/helpers/alerts/AlertConfig';




function App() {
  const [isAuth, setIsAuth] = React.useState(null);

  const change = (name, value) => setIsAuth(value) 
  if(!isAuth) return <LOGIN name={'isAuth'} change={change} value={isAuth} />

  
  return (
    <UseState>
        <GeneralState>
          <HelperState>
            <AlertConfig />
            <PageLoadRender />
          </HelperState>
      </GeneralState>
    </UseState>
    
  );
}

export default App;
