import { useState, useEffect, useContext } from 'react';
import UseContext from '../../../../context/useContext';
import { tryCatchLinkUpToServer } from '../../../../options/JSDOWNLOADS';
import GoogleMapReact from 'google-map-react';
import InfoBox from './InfoBox';
import LocationMaker from './LocationMaker';




 function GoogleMapLoader({ center, zoom }) {
     const { adjustMany } = useContext(UseContext);

     const [touch, setTouch] = useState(null);
     const [data, setData] = useState([]);

     useEffect(() => {
        (async function(){
            adjustMany({ loading: false })
            const res = await tryCatchLinkUpToServer({
                method: 'POST', urlRoute: 'form74', data: { locationCoords: {$exists: true}},
                params: 'search=FETCH'
            });
            console.log(res)
            if(res.success){
                adjustMany({ loading: false })
                setData(res.data)
            }
        })()
        // eslint-disable-next-line react-hooks/exhaustive-deps
     }, [])


    return (
        <>
        <div style={{ 
            width: '100vw', 
            height: '100vh', 
            position: 'relative',
            
         }}>
            <GoogleMapReact 
                bootstrapURLKeys={{ key: 'AIzaSyAs6kk4KpX0SqwavFE5q5nlSiQRFNpLZiI' }}
                defaultCenter={ center }
                defaultZoom={ zoom }
            >
        
                {data && data.map((d,i) => <LocationMaker key={i}
                    lat={d.LAT} 
                    lng={d.LONG}
                    onPress={() => setTouch(d)}
                />)}

            </GoogleMapReact>
        </div>

        {touch && <InfoBox 
            onPress={() => setTouch(null)} 
            data={touch}
        />}

        </>
    )
}

GoogleMapLoader.defaultProps = {
    center: {
        lat: 9.0765,
        lng: 7.3986
    },
    zoom: 6
}



export default GoogleMapLoader