import React from 'react';
import helperReducer from './helperReducer';
import HelperContext from './helperContext';
import { CLEARITEM, CLEARMANY,
    LOCALMOVEMENT, RESTOREALL } from './types';




export default function HelperState ({ children }){

    const initialState = {
        itemize: null,
        hide: true,
        top: 0,
        side: 0,
        reloadForMe: null
    };

    const [state, dispatch] = React.useReducer(helperReducer, initialState);

    // Function Decleration;
    const saveAndContinue = (data) => {
        delete data['name']
        delete data['isModel']
        delete data['arrayItem']
        delete data['original']

        dispatch({ type: LOCALMOVEMENT, payload: data })
    }
    const restoreAll = () => dispatch({ type: RESTOREALL, payload: {} })
    const adjustOne = (name, value) => dispatch({ type: CLEARITEM, payload: { name, value } });
    const adjustMany = (value) => dispatch({ type: CLEARMANY, payload: value });

    

    // Dispatch to User;

    return <HelperContext.Provider
        value={{
            itemize: state.itemize,
            reloadForMe: state.reloadForMe,
            hide: state.hide,
            top: state.top,
            side: state.side,
            
            adjustOne, saveAndContinue, restoreAll, adjustMany
        }}
    >
        { children }
    </HelperContext.Provider>



    
}