import React from 'react'
import circleSvg from '../svgs/circle.svg';



function CardIcon({ icon, color, iconColor }) {
  return (
    <div className='card-self flex-middle' style={{ 
          background: color || 'white',
          backgroundImage: `url(${circleSvg})`,
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'right center',
          width: '4rem',
          borderRadius: '1rem',
          marginRight: '1.5rem'
    }}>
      {icon && <i className={icon} style={{ fontSize: '1.7rem', textAlign: 'center', color: iconColor || 'white'}} />}
    </div>
  )
}

export default CardIcon
