import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import UseContext from '../../../context/useContext';
import { styling } from '../../../helpers/css/css';
import { InputIcon } from '../../../helpers/FormInputs';
import { STATES } from '../../../options/coutries';
import { tryCatchLinkUpToServer } from '../../../options/JSDOWNLOADS';
import { AskMeQuestion, BTs } from '../design/Esses';
import Structure from '../Structure';






function Project(){
    const { adjustMany, href } = React.useContext(UseContext);
    const navigate = useNavigate();
    

    return(
        <>
            <AskMeQuestion
                title1={'Create DISCO User'}
                title2={'Create MAP User'}
                Press1={() => adjustMany({
                        Ponent: () => Profile({ whoami: { role: 'DISCO' },
                        cb: () => navigate(`/${href}`)
                    }), 
                    quickLink: true
                })}

                Press2={() => adjustMany({
                        Ponent: () => Profile({ whoami: { role: 'MAP' },
                        cb: () => navigate(`/${href}`)
                    }),
                    quickLink: true
                })}
            />

        </>
    )
}




export default function CreateUserAdmin(){

    return <Structure
        BodyPage={Project}
    />
}


function Profile({ whoami, cb }) {
    const { adjustMany } = React.useContext(UseContext)
    const [form, setForm] = React.useState({
        fname:whoami.fname || '', lname: whoami.lname || '', opts: [{}],
        email: whoami.email || '', telephone1: whoami.telephone1 || '', 
        projectTitle: whoami.projectTitle || '', identityID: whoami.identityID || '', 
        identityType:whoami.identityType || '', telephone2: whoami.telephone2 ||'',
        em: '', supplyArea: whoami.supplyArea || [], password: '', abbrv: '',
        franchiseStates: whoami.franchiseStates || [], fullName: whoami.fullName || ''
    });

    React.useEffect(() => {
        (async function(){
            const res = await tryCatchLinkUpToServer({
                method: 'POST', urlRoute: 'project',
                params: 'select=projectName&search=FETCH'                
            })
            
            if(res.success){
                adjustMany({ loading: false})
                setForm({...form, opts: res.data })
            }
        })()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])

    const visitor = ['GUEST', 'INSTALL'];
    const dey = ['DISCO', 'MAP'];
    const chk = visitor.includes(whoami.role) ? true : false;
    
    const { fname, lname, email, telephone1, projectTitle,
         identityID, identityType, fullName, abbrv,
         telephone2, supplyArea, franchiseStates } = form;


    const onChange = (name, value) => setForm({...form, [name]: value})



    const onSubmit = async e => {
        e.preventDefault();
        
        adjustMany({ loading: true })
        const res = await tryCatchLinkUpToServer({
            method:'POST', urlRoute: `user`,
            data: form
        });

        if(res.success){
            adjustMany({ Ponent: null, quickLink: false, loading: false });
            return cb && cb(true)
        }else adjustMany({ loading: false, errors: res.error, Ponent: null, quickLink: false })
    }

    const arr = (whoami.role === 'DISCO'? franchiseStates : supplyArea)



    return(
        <>
        <form onSubmit={onSubmit} style={{
                background: 'white',
                margin: '0 auto', width: '95%', padding: '1.3rem 1.2rem 2rem'
            }}>

                <div className='flex-side'>
                    <p style={{
                        fontSize: 20,
                        marginBottom: 20,
                    }}>User Information</p>
                    
                    <input 
                        type={'submit'} 
                        value={'Send'} 
                        className={'btn'} 
                    />
                </div>

                            
               {!dey.includes(whoami.role) && <div className="grid-2">
                   <InputIcon 
                        name={'fname'}
                        onChange={onChange}
                        value={fname}
                        title={'First Name'}
                        isRequired={chk ? true : false}
                   />

                   <InputIcon 
                        name={'lname'}
                        onChange={onChange}
                        value={lname}
                        title={'Last Name'}
                        isRequired={chk ? true : false}
                   />

                   
                </div>}

               {!chk && <div className='grid-2'>
                <InputIcon 
                        name={'fullName'}
                        onChange={onChange}
                        value={fullName}
                        title={'Full Name'}
                        isRequired={true}
                        isEditable={!chk ? false : true}
                   />

                <InputIcon 
                        name={'abbrv'}
                        onChange={onChange}
                        value={abbrv}
                        title={'Company\'s Acronyms'}
                        isRequired={true}
                        isEditable={!chk ? false : true}
                   />

               </div>}

                <div className="grid-2">
                    <InputIcon 
                        name={'projectTitle'}
                        onChange={onChange}
                        value={projectTitle}
                        title={'Select Active Project'}
                        select={true}
                        options={form.opts ? form.opts.map(d=>d.projectName): []}
                   />
                   <InputIcon 
                        name={'email'}
                        onChange={onChange}
                        value={email}
                        title={'Email Address'}
                        typed={'email'}
                        isRequired={true}
                   />
                                      
                </div>
                
                <div className="grid-2">

                <InputIcon 
                        name={'telephone1'}
                        onChange={onChange}
                        value={telephone1}
                        title={'Telephone Number 1'}
                        isRequired={true}
                   />

                   <InputIcon 
                        name={'telephone2'}
                        onChange={onChange}
                        value={telephone2}
                        title={'Telephone Number 2'}
                   />
                                      
                </div>

                <div className='grid-2'>
                    <InputIcon 
                            name={'identityType'}
                            onChange={onChange}
                            value={identityType}
                            title={'Preferred Identity Type'}
                            select={true}
                            options={['International Passport', 'Driver Licence',
                            'Permanent Voters Card', 'National ID Card']}
                            isRequired={true}
                    />

                    <InputIcon 
                            name={'identityID'}
                            onChange={onChange}
                            value={identityID}
                            title={'Identity Number'}
                            isRequired={true}
                    />
                </div>
                                
                
                {dey.includes(whoami.role) && <div className='flex'>
                    <InputIcon 
                            name={'em'}
                            onChange={onChange}
                            value={form.em}
                            title={whoami.role === 'DISCO' ?
                            'Select Franchise States': 'Select Supply Disco'}
                            select={true}
                            options={STATES.map(d=>d.state)}
                            isRequired={true}
                    />
                    <BTs title={'Add Item'} style={{ marginTop: 15, marginLeft: 10 }}
                        press={() => {
                            const RL = whoami.role === 'DISCO'? franchiseStates : supplyArea;
                            
                        if(form.em){
                                RL.push(form.em)                            
                            return setForm({...form, [
                                whoami.role === 'DISCO' ? 'franchiseStates' : 'supplyArea']: RL
                            })
                        }
                    }}
                    />
                </div>}

                {dey.includes(whoami.role) && <div style={{
                    ...styling.flexed({justify: 'flex-start'}),
                    padding: '0.8rem', border: '1px dotted #ccc',
                    marginTop: 10
                }}>
                    {arr && arr.map((d,i)=> <ItemAdds key={i} 
                        title={d} press={() => setForm({
                            ...form, [whoami.role === 'DISCO' ?
                             'franchiseStates' : 'supplyArea']: arr.filter(a=> a !== d )})
                        } />)}
                    
                </div>}

                <div className='grid-2'>
                    <InputIcon 
                            name={'role'}
                            onChange={onChange}
                            value={form.role}
                            title={'Assign Role'}
                            isRequired={true}
                            select={true}
                            isEditable={true}
                            options={['DISCO', 'ADMIN', 'MAP', 'GUEST', 'INSTALLER', 'OTHER']}

                    />
                    <InputIcon 
                            name={'password'}
                            onChange={onChange}
                            value={form.password}
                            title={'Create Default Password'}
                            typed={'password'}
                            isRequired={true}

                    />

                </div>

                

        </form>
        </>
    )
}

function ItemAdds({ press, title }){
    return <div className='flex'>
        <i className='fas fa-times-circle' 
        onClick={press} style={{ 
            cursor: 'pointer',
            color: 'red', margin: '0 0.5rem'
        }}></i>
        <h4>{title}</h4>
    </div>
}