import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import UseContext from '../../../../context/useContext';
import { styling } from '../../../../helpers/css/css';
import { tryCatchLinkUpToServer } from '../../../../options/JSDOWNLOADS';
import Structure from '../../Structure';
import RequestPagination from './RequestPagination';

function ACT() {
  const { adjustMany, href } = React.useContext(UseContext);
  const navigate = useNavigate();

  const [form, setForm] = React.useState({
    current: 0,
    arr: null,
    item: null,
    view: 10,
  });

  React.useEffect(() => {
    (async function () {
      adjustMany({ loading: true });
      const res = await tryCatchLinkUpToServer({
        method: 'GET',
        urlRoute: `form74`,
        params: `is_certified=false`,
      });

      if (res.success && res.data?.length > 0) {
        adjustMany({ loading: false });
        setForm({ ...form, item: res.data });
      } else
        adjustMany({ loading: false, errors: res.error || 'No content found' });
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onChange = (name, value) => setForm({ ...form, [name]: value });
  const { item, current, view } = form;

  const viewOne = (one) => {
    adjustMany({ singleItem: { success: one ? true : false, data: [one] } });
    return navigate(`/${href}/view`);
  };

  return (
    <>
      <div>
        <div className="flex-side">
          <p style={{ fontSize: '1.5rem', margin: '1rem 0' }}>
            List of Meter Applicant(s)
          </p>

          {item && (
            <RequestPagination
              onChange={onChange}
              arrayItem={form.item}
              parentState={setForm}
              parentChild={form}
              count={10}
              url={`form74`}
            />
          )}
        </div>

        <table className="tables">
          <thead>
            <tr>
              <th>Applicant FullName </th>
              <th>Telephone</th>
              <th>Building Address</th>
              {/* <th>Meter Request Type</th> */}
              <th>Request Status</th>
              <th>Verification Status</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {item &&
              item.slice(current, view).map((d, i) => {
                const {
                  lname,
                  fname,
                  telephone,
                  isCertified,
                  isCancelled,
                  streetName,
                  state,
                  hasAllocation,
                  meterNumber,
                } = d || {};

                let st, cl;
                if (isCertified && hasAllocation) {
                  st = `Property has been allocated: ${meterNumber}`;
                  cl = 'green';
                }
                if (isCertified && !hasAllocation) {
                  st = `Property verified but awaiting meter`;
                  cl = styling._primary_600;
                }
                if (isCancelled) {
                  st = `Request was cancelled`;
                  cl = 'red';
                }
                if (!isCertified) {
                  st = `Request Untreated`;
                  cl = 'orange';
                }

                return (
                  <tr key={i}>
                    <td>{fname && lname ? `${fname} ${lname}` : ''}</td>
                    <td>{telephone || ''}</td>
                    <td>{`${streetName}, ${state}`}</td>
                    {/* <td>{preferredMeterPhase || ''}</td> */}
                    <td style={{ color: cl || 'auto' }}>{st || ''}</td>
                    <td
                      className={isCertified ? '' : 'blink'}
                      style={{ color: isCertified ? 'green' : 'red' }}
                    >
                      {isCertified
                        ? 'Property verified'
                        : 'Property not verified'}
                    </td>

                    <td>
                      <button className="btx" onClick={() => viewOne(d)}>
                        <i className="fas fa-eye" />
                      </button>
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
    </>
  );
}

export default function CustomerTable() {
  return <Structure BodyPage={ACT} />;
}
