import * as React from 'react';
import UseContext from '../context/useContext';
import Absolutes from './alerts/Absolutes';
import { styling } from './css/css';
import { FilterInput } from './TypedInput';



export const InputIcon = ({ isRequired, isEditable, length, typed, style, bg,
    title, icon, name, value, onChange, select, options, onPress, focus, rightIcon }) => {
    const { adjustOne } = React.useContext(UseContext)
    
      const [onAction, setOnAction] = React.useState(false)
      const [onSelect, setOnSelect] = React.useState(false)
      const [onTyped, setOnTyped] = React.useState(false);
      


      const change = e => onChange && onChange([e.target.name], e.target.value);


      if(onSelect && select) return <Absolutes 
            Ponent={() => Tuchel({ title, onChange,
                setOnSelect, options, name})}
      />

      const _ide = (Math.random()*93940333333335489438).toString()
    
      const unique = () => {
        adjustOne('dark', 'absolutes-dark')
        return typed !== 'password' ? setOnSelect(true) 
        : setOnTyped(!onTyped)
    };

    return (

        <div className='input-ads' style={{
            borderBottom: onAction ? `2px solid ${styling._primary_700}`:
            `1px solid ${styling._primary_700}`, borderRadius: 5,
            ...bg, ...style,
        }}>

            <div className='input-icon' style={bg}>

                <i className={icon || 'fas fa-home'} style={{
                    fontSize: '1rem',
                }}></i>
            </div>
                <div style={{ flex: 4 }}>
                <label htmlFor={_ide} id={'label'}
                    style={((onAction || typed === 'date') || value) ? {
                    marginTop: 0, fontSize: '0.7rem', padding: '0 0.3rem', 
                    fontWeight: 600, color: `${styling._primary_700}`
                } : {
                    marginTop : '10px',
                    fontSize : '0.8rem',
                    padding : '0 0.3rem',
                    color : '#333',
                    fontWeight : '500', 
                }}>
                        {title || 'Title'}
                    </label>
                    <input 
                        name={name} 
                        value={value}
                        onChange={change}
                        className="input-txt"
                        id={_ide}
                        onFocus={() => setOnAction(true)}
                        onBlur={() => {
                            setOnAction(false)
                           return focus && focus(value);
                        }}
                        
                        required={isRequired}
                        maxLength={length}
                        type={onTyped ? 'text' : typed}
                        disabled={select ? true : isEditable}
                        // style={{background: bg || 'white'}}                        
                     />

                </div>
                    {(select || typed === 'password' || rightIcon) && <i 
                        className={ typed === 'password' ? 'fas fa-eye' 
                        :(rightIcon || 'fas fa-arrow-circle-down .brn')} 
                        
                        style={{cursor: 'pointer', 
                            marginTop: 10, 
                           padding: 10
                    }}
                        onClick={ !onPress ? unique : onPress }
                    ></i>}
                     
            </div>
    )
}


export const INPUTs = ({ name, value, typed, onChange, placeholder, onFocus, onBlur, style }) => {
    

    return<input 
        type={typed || 'text'} 
        name={name} 
        value={value}
        onChange={e => onChange && onChange(name, e.target.value)}
        style={{ 
            padding: '0.5rem', 
            width: '100%', 
            outline: 'none', 
            border: 'none',
            ...style
        }}
        placeholder={placeholder}
        onFocus={onFocus}
        onBlur={onBlur}
    />
}

export const InputLabelled = ({ name, value, typed, onChange, id_, title,
    placeholder, onFocus, onBlur, style, leftIcon,
     rightIcon, onPress, isEditable, isRequired }) => {

    const [active, setActive] = React.useState(false)
    const [switcIcon, setSwitchIcon] = React.useState(false)

    const _ide = (Math.random()*93940333333335489438).toString()

    return <div style={{ marginTop: 15 }}>
        <label htmlFor={id_ || _ide}>{title}</label>

        <div style={{ ...styling.flexed({gap: '0'}),
            padding: '0.5rem', 
            border: active ? `2px solid  ${styling._primary_500}`: 
                `1px solid  ${styling._primary_500}`,
            borderRadius: 5, marginTop: 5

        }}>
            {leftIcon && <i className={leftIcon} style={{padding: 10, flex: 0.3}}></i>}
        <input 
                type={(switcIcon ? 'text' : typed) || 'text'} 
                name={name} 
                id={id_ || name}
                value={value}
                onChange={e => onChange && onChange(name, e.target.value)}
                style={{ 
                    padding: '0.5rem', 
                    width: '100%', 
                    outline: 'none', 
                    border: 'none',
                    flex: 4,
                    ...style
                }}
                placeholder={placeholder}
                onFocus={() => {
                    if(onFocus)
                        return onFocus()
                    setActive(true)
                }}
                onBlur={() => {
                    if(onBlur)
                        return onBlur()
                    setActive(false)
                }}
                required={isRequired}
                disabled={isEditable}
            />
            {rightIcon && <i 
                        className={`${(!switcIcon ? rightIcon : 'fas fa-eye-slash')} brn`} 
                        
                        style={{cursor: 'pointer', 
                            // marginTop: 10, 
                           padding: 10
                    }}
                        onClick={ () => {
                            if(rightIcon && typed === 'password'){
                                setSwitchIcon(!switcIcon)
                            }
                            else if(onPress)
                                return onPress()
                        } }
                    ></i>}
        </div>
    </div>
    
    
}


function Tuchel({ title, setOnSelect, options, name, onChange }){
    const [form, setForm] = React.useState({ item: options });

    return <div className={ 'appear'} style={{
        width: '50%',  margin: '0 auto',
        borderRadius: 5, height: 300, overflow: 'auto'
    }}>
      
        <div style={{
            ...styling.flexed({}),
            borderBottom: '1px solid #f2f2f2',
            padding: 10
        }}>
            <h5 style={{
                textTransform: 'uppercase',
                color: styling._primary_400,
                paddingLeft: 10
            }}>
                {title || 'Authentication Box'}
            </h5>
            <p className='cancel'
                style={{ cursor: 'pointer' }}
                onClick={() => setOnSelect(false)}
            >x</p>
    </div>

    <div style={{ 
        width: '80%', 
        margin: '0 auto',
    }}>
          <FilterInput
            placeholder={'Filter item...'}
            original={options}
            parentData={form}
            parentState={setForm}
            justArray={true}
        />
       
       {form.item ? form.item.map((el, index) => {
           return <p style={{ 
               padding: 10, marginTop: 5,
               background: '#f2f2f2', cursor: 'pointer'
            }} key={index}

                onClick={() => {
                    setOnSelect(false)
                    return onChange(name, el)
                }}
            >
                {el || ''}
           </p>
       }) : ''}
    </div>

    </div>
}