import * as React from 'react';
import UseContext from '../context/useContext';
import { styling } from './css/css';


export const SearchInput = ({ goBack, title, onChange, name, 
    value, onPress, selectPress, blur, focus, placeholder }) => {


    return(
        <div style={{ flex: 1, width: '100%', background: 'white' }} className='flex'>
            <i className='fas fa-arrow-left'
                onClick={goBack}
                style={{ 
                    background: 'white',
                    padding: '0.6rem',
                    color: '#333',
                    cursor: 'pointer'
                }}
            > <span style={{ marginLeft: 10 }}>{title}</span></i>
            <input 
                onChange={e => onChange(name, e.target.value)} 
                name={name} 
                value={value} 
                style={{
                    outline: 'none',
                    height: '100%',
                    padding: '0.6rem',
                    border: 'none',
                    width: '100%',
                    backgroundColor:'transparent',
                    fontSize: '0.8rem'
                }}
                autoFocus={true}
                placeholder={placeholder || 'Search'}
                onFocus={focus}
                onBlur={blur}
                spellCheck={true}
            />
            <div className='flex'>
                <i className='fas fa-chevron-down'
                    style={{ 
                        background: 'white',
                        padding: '0.6rem',
                        color: '#333',
                        cursor: 'pointer'
                    }}
                    onClick={selectPress}
                ></i>
                <i className='fas fa-search hoves'
                    onClick={onPress}
                    style={{ 
                        background: '#f2f2f2',
                        padding: '0.6rem',
                        color: '#333',
                        cursor: 'pointer'
                    }}
                ></i>
            </div>
        </div>
    )
}


export const FilterInput = ({ parentState, parentData, justArray,
    value, onPress, blur, focus, placeholder, params, original }) => {

    const [text, setText ] = React.useState('');


    return(
        <div style={{ flex: 1, width: '100%', background: 'white' }} className='flex'>

            <input 
                onChange={e => {
                    const regex = new RegExp(e.target.value, 'i');
                    const fiText = original.filter(el => regex.test(justArray ? el : el[params]));
                    setText(e.target.value)
                    const u = fiText.length>0 ? fiText : (justArray ? [] : [{}])
                    parentState({ ...parentData, 
                        item: e.target.value !== '' ? u : original
                    })
                }} 
                value={text} 
                style={{
                    outline: 'none',
                    height: '100%',
                    padding: '0.8rem',
                    border: 'none',
                    width: '100%',
                    backgroundColor:'transparent',
                    fontSize: '0.8rem',
                    borderRadius: '.5rem'
                }}
                autoFocus={true}
                placeholder={placeholder || 'Filter items'}
                onFocus={focus}
                onBlur={blur}
                spellCheck={true}
            />
            <div className='flex'>
                <i className='fas fa-search hoves'
                    onClick={onPress}
                    style={{ 
                        background: '#f2f2f2',
                        padding: '0.8rem',
                        color: '#333',
                        cursor: 'pointer'
                    }}
                ></i>
            </div>
        </div>
    )
}


export const TTBox = ({ title, onChange, name, icon, size, onPress, value }) => {

    return<div className='fle' style={{ 
        width: size || 150, ...styling.flexed({ justify: 'center' }),
        padding: '0.8rem', boxShadow: styling._shadow_2,
        marginTop: 10, cursor: 'pointer', height: 100,
        background: 'white'
    }}
    
    onClick={() => {
        if(onPress)
            return onPress()
        return onChange && onChange(name, value)
    }}

    >
        {icon && <i className={icon} />}
        <div style={{ textAlign: 'center'}}>
            <h4>{title}</h4>
            <h2>{value}</h2>
        </div>
        
    </div>
}


export const HeaderItems = ({ title, onPress, name }) => {
    const { adjustOne } = React.useContext(UseContext);

    return(
        <div style={{ padding: '0.8rem'}} className="flex">
            <i className='fas fa-arrow-left' 
                onClick={() => {
                    if(onPress)
                        return onPress()
                    return name && adjustOne('nav', name)
                }}
                
            style={{ cursor: 'pointer'}}
            
            ></i>
            <h3 style={{
                marginLeft: 10, textTransform: 'uppercase'
            }}>{title || ''}</h3>
        </div>
    )
}


export const UploadFileContainer = ({ height, onChange, name, ext, value }) => {
   


    return(
        <>

        <input type={'file'} id='file' accept=''
            style={{ visibility: 'hidden'}} />

                    <div className='hoves' style={{
                        width: '60%',
                        padding: 20,
                        boxShadow: styling._shadow_4,
                        margin: '0 auto',
                        marginTop: 10,
                        cursor: 'pointer'
                    }}
                        id='file-upload'
                        onClick={() => {
                            if(onChange){
                                const file = document.getElementById('file');
                                file.click();
                                file.onchange = (e) => onChange(name,
                                document.getElementById('file').files[0])
                            }
                            
                        }}
                    >

                        <div style={{
                            border: '5px dotted #ccc',
                            textAlign: 'center',
                            padding: 20,
                            fontSize: '1rem'
                        }}> 

                            <i className={!value ? 'fa fa-cloud-upload' : 'fas fa-check-circle'}
                                style={{ fontSize: 30, 
                                    color: !value ? styling._primary_500 : 'green' }}
                            ></i>
                           {!value && <p> *File Extention should be  
                                <span style={{
                                    fontWeight: 'bold',
                                    color: styling._primary_500,
                                    fontSize: '1rem'
                                }}> {ext || '.CSV'}</span> format* </p>}

                           {!value && <p> *Max File Size 
                                <span style={{
                                    fontWeight: 'bold',
                                    color: styling._primary_500,
                                    fontSize: '1rem'
                                }}> 10MB</span> * </p>}

                            {value && <h4>File selected successfully...</h4>}
                            {value && <p>File name: {value.name}</p>}

                        </div>

                    </div>

        </>
       
    )

}

