

export const SETAUTH = () => {
    return{
        setToken: (token) => localStorage.setItem('_', JSON.stringify(token)),
        getToken: () => localStorage.getItem('_'),
        clearToken: () => localStorage.removeItem('_'),
        decode: () => {
            const token = localStorage.getItem('_');
            if(token !== null){
                const tk = window.atob(JSON.parse(token))
                const frs = tk.substring(9);
                return frs;
            }else return token
        }
    }

}