import React from 'react'
import { styling } from '../../../../helpers/css/css'
import { UREL } from '../../../../options/JSDOWNLOADS'



const InfoBox = ({ onPress, data }) => {
  return (
    <div style={{
        position: 'absolute',
        top: '5%', 
        width: '400px',
        overflow: 'auto',
        height: '90%',
        background: 'white',
        border: '1px solid #ccc',

    }} className="appear">

        <div style={{ width: '100%', ...styling.flexed({}), marginTop: 20,
            borderBottom: '1px solid #ccc', padding: '.5rem'}}>
            <h1>Additional Information </h1>
            <p className='cancel' onClick={onPress}>x</p>            
        </div>


        <div style={{ width: '100%'}}>
          <div style={{ 
                width: '100%', 
                height: '200px', 
                background: '#f2f2f2',
                border: '1px solid #ccc',
                objectFit: 'cover'
            }}>
            <img 
                  src={`${UREL}/uploads/${data.propertyImage}`}
                  alt={'Customer Property Portrait'}
                  style={{ width: '100%', height: '100%'}}
              />
          </div>
            
            <div style={{ marginTop: 10 }}>
                <Named title={'Full Name: '} sub={data.fullName || ''}/>
                <Named title={'Property Address: '} sub={data.buildingAddress || ''}/>
                <Named title={'Telephone Number: '} sub={data.telephone1 || ''}/>
                <Named 
                  title={'Schedulled For Meter: '} 
                  sub={data.hasAllocation ? 'TRUE' : 'FALSE'}
                  colored={data.hasAllocation ? 'green' : '#333'}
                />
                <Named 
                  title={'Metering Status: '} 
                  sub={(data.meterInstalled ? 'Metered' : 'Unmetered')}
                  colored={data.meterInstalled ? 'green' : 'red'}
                />
                <Named title={'Customer Owner:'} sub={data.disco || ''} />
            </div>

              {console.log(data)}
        </div>
    </div>
  )
}

export default InfoBox


function Named({ title, sub, colored }){

  return <div style={{ padding: '.8rem', borderBottom: '1px solid #ccc'}}>
      <p> {title} <strong style={{ color: colored || 'auto'}}>{sub}</strong></p>
  </div>
}