import { Icon } from '@iconify/react';
import Wallet from '@iconify/icons-mdi/house-floor-attic'

const LocationMaker = ({ lat, lng, onPress }) => {


  return (
    <div className='location-maker' onClick={onPress}>
        <Icon 
            icon={Wallet}
            style={{
                fontSize: '1.5rem',
                color: 'red'
            }}
        />
    </div>
  )
}





export default LocationMaker