import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import UseContext from '../../../../context/useContext';
import { styling } from '../../../../helpers/css/css';
import { TABLESTORE } from '../../../../helpers/TableItem';
import { Item3Box, Pagination } from '../../../../helpers/Utitlities';
import { tryCatchLinkUpToServer } from '../../../../options/JSDOWNLOADS';
import Structure from '../../Structure';
import { discoAllocationState } from './allocationLogics';
import DiscoSide from '../../DiscoSide';




function MA () {
    const { adjustOne, href,
        adjustMany, updateRelated, items } = React.useContext(UseContext);

    const navigate = useNavigate()

    const [form, setForm]=React.useState({ ...discoAllocationState, meterConfig: updateRelated })



    React.useEffect(() => {
        (async function (){
        
            !items && adjustOne('loading', true)
            const res = await tryCatchLinkUpToServer({
                method: 'GET', urlRoute: 'disco', 
                params: `destinationStore=${updateRelated}&search=ITEMS`, 
            });

            if(!items) adjustOne('items', res)

            if(res.success){
                const allStoreIDs = res.data.each;
                const { singlePhase, threePhase, md, total } = res.data.all || [{}]
              
                 adjustOne('loading', false)
 
                 setForm({ ...form, item: allStoreIDs, singlePhase, threePhase, md,
                     all: total,
                     dData: { all: res.data.each }
                 })
 
             }else adjustMany({ errors: res.error, loading: false })
        })()

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])


    const { item, arr, current, view, dData, 
        singlePhase, md, threePhase, all } = form;

    const onChange = (name, value) => setForm({...form, [name]: value})

    

    return(
        <>
        <div>

        <div style={{ ...styling.flexed({})}}>

        <p style={{
            padding: 10,
            width: '100%',
            fontSize: '1.5rem',
            }}
            >
                Meter Statistics in Store
            </p>

            {/* <p style={{fontWeight: 'bold'}}>Total: {0}</p> */}
        </div>

        <div style={{ 
            ...styling.flexed({}),
            padding: 10, background: 'white',
            border: '1px solid #ccc',
            boxShadow: styling._shadow_1,
            // marginTop: 10
        }}>
            <Item3Box 
                name={'Unallocated Meters'}
                btn={'Allocate'}
                value={all}
                disabled={true}
                
            />
            <Item3Box 
                name={'1-Phases (Non-MD)'} 
                btn={'Allocate'}
                value={singlePhase}
                disabled={true}
                cb={() => {
                    adjustMany({ singleItem: {
                    header: 'Allocation by Category: 1-Phase(Non-MD)',
                    data: singlePhase,
                    id: '1-Phase', storeName: updateRelated,
                    name: 'phase'
                }})
                
                return navigate(`/${href}/confirm-allocation`)
            }}
            />
            <Item3Box 
                name={'3-Phases (Non-MD)'}
                btn={'Allocate'}
                value={threePhase}
                disabled={true}
                cb={() => {
                    adjustMany({ singleItem: {
                    header: 'Allocation by Category: 3-Phase(Non-MD)',
                    data: threePhase,
                    id: '3-Phase', storeName: updateRelated,
                    name: 'phase'
                }})
                
                    return navigate(`/${href}/confirm-allocation`)
                }}
                
            />
            <Item3Box 
                name={'Maximum Demand'} 
                btn={'Allocate'}
                value={md}
                disabled={true}
            />
            <Item3Box 
                name={'DT Meters'} 
                btn={'Allocate'}
                value={0}
                disabled={true}
            />
        </div>

        <div style={{ marginBottom: 10}}>

        <div style={{
            padding: 10, width: '100%',
            ...styling.flexed({})
        }}> 
            <p style={{ fontSize: 35 }}>Unallocated Meter in Cartons </p>

            <div style={{ ...styling.flexed({}), padding: 10 }}>
                <Pagination
                    onChange={onChange}
                    arrayItem={item}
                    parentState={setForm}
                    parentChild={form}
                    count={4}
                />
            </div>
        </div>

        

        <TABLESTORE row={arr || item.slice(current, view)}                    
            head={['Store ID', 'Carton ID', 'Meter Phase',
            'Meter Provider', 'Manufacturer Name', 'Default Unit', 'Total Pieces', '']}
            onChange={onChange}
            name={'item'}
            item={dData.all}
            arrayItem={arr}
            onPress={(e, piece, name, ph) => {
                    adjustMany({ singleItem: {
                    header: `Allocation by Carton Ref: ${e}`,
                    data: piece || 0,
                    id: e, storeName: name, name: 'cartonID',
                    phase: ph
                }})
                
                return navigate(`/${href}/confirm-allocation`)
            }}
            btn={true}
            href={`/${href}/confirm-allocation`}
        />
        </div>


        </div>

        
        </>
    )
}





export default function DiscoAllocations(){

   return (
            <>
                <Structure 
                    Side={DiscoSide}
                    BodyPage={MA}
                />
            </>
        )
}


