

export const TABLE = function ({ head, row, btn, onPress, item, href }){
    // const { cartonID, meterType, meterModel, meterOwner, volt,
    //     phase, manufacturerName, accuracy, capacity, defaultUnit } = item;


    return (
        <table className='tables'>
            <thead>
                <tr>
                    {head && head.map((el, index) => <th key={index}>
                       {el}
                    </th>)}
                </tr>
           
            </thead>

            <tbody>
                {row && row.map((el, index) => {
                    const { owners, single, three, DT, MD,
                         installed, uninstalled, all } = el || {};
                        
                return <tr key={index}>
                    <td style={{ textAlign: 'left'}}>{owners}</td>
                    <td style={{ textAlign: 'center'}}>{single || 0}</td>
                    <td style={{ textAlign: 'center'}}>{three || 0}</td>
                    <td style={{ textAlign: 'center'}}>{MD || 0}</td>
                    <td style={{ textAlign: 'center'}}>{DT || 0}</td>
                    <td style={{ textAlign: 'center'}}>{all || 0}</td>
                    <td style={{ textAlign: 'center'}}>{installed || 0}</td>
                    <td style={{ textAlign: 'center'}}>{uninstalled || 0}</td>
                    {/* <td>
                        <button className="btx brn">Action</button>
                    </td> */}
                    </tr>
                })}
            </tbody>
        </table>
    )
}


export const TABLESTORE = function ({ head, row, btn, onPress, item, href }){
    // const { cartonID, meterType, meterModel, meterOwner, volt,
    //     phase, manufacturerName, accuracy, capacity, defaultUnit } = item;


    return (
        <table className='tables'>
            <thead>
                <tr>
                    {head && head.map((el, index) => <th key={index}>
                       {el}
                    </th>)}
                </tr>
           
            </thead>

            <tbody>
            {row && row.map((el, index) => {
                    const { cartonID, meterOwner, storeID, destinationStore,
                        phase, manufacturerName, defaultUnit, pop } = el || {};

                return <tr key={index}>
                    <td>{storeID}</td>
                    <td style={{ textAlign: 'center'}}>{cartonID}</td>
                    <td style={{ textAlign: 'center'}}>{phase}</td>
                    <td style={{ textAlign: 'center'}}>{meterOwner}</td>
                    <td style={{ textAlign: 'center'}}>{manufacturerName}</td>
                    <td style={{ textAlign: 'center'}}>{defaultUnit}</td>
                    <td style={{ textAlign: 'center'}}>{pop}</td>
                    {btn && <td style={{textAlign: 'center'}}>{row.length > 0 && 
                    <button className='btx'
                        onClick={() => onPress && onPress(cartonID, pop || 0, destinationStore, phase)}
                        style={{ fontSize: '0.7rem' }}>
                        Allocate</button>}</td>}
                </tr>})}
            </tbody>
        </table>
    )
}

export const CREATETABLE = function ({ head, row, btn, onPress, item, href }){
    
    return (
        <table className='tables'>
            <thead>
                <tr>
                    {head && head.map((el, index) => <th key={index}>
                       {el}
                    </th>)}
                </tr>
           
            </thead>

            <tbody>
                {row()}
            </tbody>
        </table>
    )
}



export const TABDASH = function ({ head, row }){

    return (
        <table className='tables'>
            <thead>
                <tr className='tr'>
                    {head && head.map((el, index) => <th key={index}>
                       {el}
                    </th>)}
                </tr>
           
            </thead>

            <tbody>
                {row && row.map((el, index) => <tr key={index} className="tr">
                    <td>{el.desc}</td>
                    <td>{el.replacement}</td>
                    <td>{el.factoryError}</td>
                    <td>{el.faulty}</td>
                    <td>{el.bad}</td>
                    <td>{el.malfunction}</td>
                </tr>)}
            </tbody>
        </table>
    )
}
