import React from 'react';
import UseContext from '../../../../context/useContext';
import { tryCatchLinkUpToServer } from '../../../../options/JSDOWNLOADS';
import Structure from '../../Structure';
import { BTX } from './helpers/CBCard';
import FirstRow from './helpers/FirstRow';
import GraphComponent from './helpers/GraphComponent';
import SecondRow from './helpers/SecondRow';
import ThirdRow from './helpers/ThirdRow';

function DashboardMainPage() {
  const { adjustMany, items } = React.useContext(UseContext);

  const [form, setForm] = React.useState({
    appUsers: null,
    map_: null,
    disco: null,
    people: null,
    aloMap: null,
    aloDisco: null,
    aloPeople: null,
    data: null,
    installation: null,
    mapActivities: null,
    discoActivities: null,
    un: [],
    metersOnTransitCount: 0,
    unAllocatedMeterCount: 0,
  });

  async function reload(clear) {
    adjustMany({
      loading: true,
      items,
      special: null,
      active: 'dashboard',
    });
    const { data, success, error } =
      (!clear && items) ||
      (await tryCatchLinkUpToServer({
        method: 'GET',
        urlRoute: 'disco',
        params: 'search=CBS',
      }));

    if (success) {
      const {
        map_,
        disco,
        disco_total_uploads,
        map_total_uploads,
        assets_exchange_match,
        disco_allocations,
        total_meter_applicant,
        installation_count,
        metersOnTransitCount,
        unAllocatedMeterCount,
      } = data || {};

      adjustMany({ items: { data, success } });

      setForm({
        ...form,
        map_,
        disco,
        people: total_meter_applicant,
        aloDisco: disco_total_uploads,
        aloMap: map_total_uploads,
        aloPeople: disco_allocations,
        installation: installation_count,
        discoActivities: assets_exchange_match,
        mapActivities: assets_exchange_match,
        // un: discoActivities
        //   ? [...new Set(discoActivities.map((b) => b.name))]
        //   : [],
        metersOnTransitCount,
        unAllocatedMeterCount,
      });
      adjustMany({ loading: false, items: { data, success } });
    } else adjustMany({ loading: false, errors: error || 'No content found' });
  }

  React.useEffect(() => {
    reload();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const {
    map_,
    disco,
    people,
    aloDisco,
    aloMap,
    aloPeople,
    installation,
    mapActivities,
    //   discoActivities
  } = form;

  return (
    <>
      <div
        style={{ fontSize: '1.5rem', marginBottom: '1rem' }}
        className="flex-side"
      >
        <p>Dashboard</p>
        <BTX
          title={'Reload'}
          icon={'fas fa-sync'}
          onPress={() => reload(true)}
          bg={'royalblue'}
        />
      </div>

      {/* GRIDs */}
      <FirstRow DATA={{ map_, disco, people }} />
      <SecondRow DATA={{ aloDisco, aloMap, aloPeople }} />
      <ThirdRow
        DATA={{
          difMap: form.unAllocatedMeterCount,
          difDisco: installation,
          difPeople: form.metersOnTransitCount,
          // ap: (aloMap>0 ? (aloDisco/aloMap) : 0),
          // ds: (aloDisco>0 ? (installation/aloDisco) : 0),
          // pe: (aloPeople>0 ? (installation/aloPeople) : 0)
        }}
      />

      {/* GRAPH */}
      <GraphComponent mapActivities={mapActivities} />
    </>
  );
}

export default function MainPage() {
  return <Structure BodyPage={DashboardMainPage} />;
}
