import React from 'react'
import UseContext from '../../context/useContext'
import BodyMain from '../../layout/body/BodyMain'
import HeaderConfig from '../../layout/header/HeaderConfig'
import SideMain from '../../layout/sidebar/SideMain'
import CBSide from '../cbs/CBSide'
import MapSide from './MapSide'



function Structure({ BodyPage, props }) {
    const { special } = React.useContext(UseContext)
  return (
        <>
            <HeaderConfig />
            <SideMain SideIcons={ special ? CBSide : MapSide} />
            <BodyMain BodyPage={BodyPage} props={props} />
        </>
  )
}

export default Structure
