import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import UseContext from '../../../../../context/useContext';
import { InputIcon } from '../../../../../helpers/FormInputs';
import { HeaderItems } from '../../../../../helpers/TypedInput';
import Headers from '../../../../../layout/Headers';

export const PersonalInfo = ({ onChange, form }) => {
  const { adjustOne, href } = React.useContext(UseContext);
  const navigate = useNavigate();
  const {
    fname,
    lname,
    email,
    telephone1,
    gender,
    identityID,
    identityType,
    telephone2,
  } = form;

  const onSubmit = (e) => {
    e.preventDefault();
    return adjustOne('skip', 'address');
  };

  return (
    <>
      <Headers
        HeaderItems={function () {
          return (
            <HeaderItems
              title={'CUSTOMER RECORD MODULE'}
              onPress={() => navigate(`/${href}/apply`)}
            />
          );
        }}
        SideIcons={function () {
          return (
            <div>
              <p>Page 1 of 5 </p>
            </div>
          );
        }}
      />

      <form
        onSubmit={onSubmit}
        style={{
          background: 'white',
          margin: '0 auto',
          width: '80%',
          padding: '1.3rem 1.2rem 2rem',
        }}
      >
        <p
          style={{
            fontSize: 20,
            marginBottom: 20,
          }}
        >
          Personal Information
        </p>

        <div className="grid-2">
          <InputIcon
            isEditable={true}
            name={'fname'}
            onChange={onChange}
            value={fname}
            title={'First Name'}
            isRequired={true}
          />

          <InputIcon
            isEditable={true}
            name={'lname'}
            onChange={onChange}
            value={lname}
            title={'Last Name'}
            isRequired={true}
          />
        </div>

        <div className="grid-2">
          <InputIcon
            isEditable={true}
            name={'gender'}
            onChange={onChange}
            value={gender}
            title={'Gender'}
            select={true}
            options={['Male', 'Female']}
          />
          <InputIcon
            isEditable={true}
            name={'email'}
            onChange={onChange}
            value={email}
            title={'Email Address'}
            typed={'email'}
          />
        </div>

        <div className="grid-2">
          <InputIcon
            isEditable={true}
            name={'telephone1'}
            onChange={onChange}
            value={telephone1}
            title={'Telephone Number 1'}
            isRequired={true}
          />

          <InputIcon
            isEditable={true}
            name={'telephone2'}
            onChange={onChange}
            value={telephone2}
            title={'Telephone Number 2'}
          />
        </div>

        <div className="grid-2">
          <InputIcon
            isEditable={true}
            name={'identityType'}
            onChange={onChange}
            value={identityType}
            title={'Preferred Identity Type'}
            select={true}
            options={[
              'International Passport',
              'Driver Licence',
              'Permanent Voters Card',
              'National ID Card',
            ]}
            isRequired={true}
          />

          <InputIcon
            isEditable={true}
            name={'identityID'}
            onChange={onChange}
            value={identityID}
            title={'Identity Number'}
            isRequired={true}
          />
        </div>

        <input
          type={'submit'}
          value={'Continue'}
          className={'btn'}
          style={{ marginTop: 20 }}
        />
      </form>
    </>
  );
};
