import React, { useEffect } from 'react'
import HelperContext from '../../context/helpers/helperContext';
import { pickEmitter } from '../../helpers/essential';
import { SideHeader, SideWrapper } from './sideHelper'


function SideMain({ SideIcons }) {

  const { adjustMany } = React.useContext(HelperContext);

  useEffect(() => {
    (function(){
        adjustMany({ side: pickEmitter('side').clientWidth})
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <>
        <SideWrapper>
            <SideHeader />
            {SideIcons && <SideIcons  />}
        </SideWrapper>
    </> 
  )
}

export default SideMain