import { CLEARITEM, LINKTOSERVER, LINKTOSERVERWITHERROR, 
    LOCALMOVEMENT, RESTOREALL, CLEARMANY } from "./types";


export default function HelperReducer (state, action) {
  
    switch (action.type){
        case LOCALMOVEMENT:
            return{
                ...state,
                localMovement: action.payload
            }
            
        case LINKTOSERVER:
            return { ...state, 
                [action.payload.name] : action.payload.value, 
                success: 'Event successfull...'
            }

        case CLEARITEM:
            return { ...state, [action.payload.name] : action.payload.value,
                errors: null, success: null }

        case CLEARMANY:
            return { ...state,  ...action.payload }
        
            
        case RESTOREALL:
            return{
                ...state, loading: false, user: null, items: null,
                isAuthenticated: false, token: null, updateRelated: false,
                localMovement: null, errors: null, success: null
            }
        case LINKTOSERVERWITHERROR:
            return{ ...state, errors: action.payload }

        default:
            return state;
    }
}