import * as React from 'react';
import { Link } from 'react-router-dom';
import UseContext from '../../../../context/useContext';
import CBSide from '../../CBSide';
import Structure from '../../Structure';
import { DISCO_VIEW, MAP_VIEW } from './tabl/CreateTables';






function View(){
    const { nav,href } = React.useContext(UseContext);



    // React.useEffect(() => {
    //     (function(){
    //         adjustOne('active', 'View')
    //     })()
    //     // eslint-disable-next-line react-hooks/exhaustive-deps
    // }, [])

    

   

    return(
        <>
            <Link to={`/${href}/view-summary`} style={{visibility: 'hidden'}} id={'summary'}></Link>
            {nav === 'DISCO-VIEW' && <DISCO_VIEW 
                title={'Registered DISCOs Overview'}
                
            />}
           {nav === 'MAP-VIEW' && <MAP_VIEW 
           title={'Registered MAP Overview'}/>}
          
        </>
    )
}


export default function GeneratTable(){

    return (
        <>
            <Structure 
                Side={CBSide}
                BodyPage={View}
            />
        </>
    )
}



