import * as React from 'react';
import UseContext from '../../../../../context/useContext';
import { InputIcon } from '../../../../../helpers/FormInputs';
import { HeaderItems } from '../../../../../helpers/TypedInput';
import Headers from '../../../../../layout/Headers';

export const AddressInfo = ({ onChange, form }) => {
  const { adjustMany } = React.useContext(UseContext);
  const {
    streetName,
    streetNumber,
    city,
    state,
    nationality,
    lga,
    landMark,
    busStop,
  } = form;

  const onSubmit = (e) => {
    e.preventDefault();
    adjustMany({ skip: 'property', updateRelated: { lga, state } });
  };

  return (
    <>
      <Headers
        HeaderItems={function () {
          return (
            <HeaderItems
              title={'CUSTOMER RECORD MODULE'}
              onPress={() => adjustMany({ skip: 'personal' })}
            />
          );
        }}
        SideIcons={function () {
          return (
            <div>
              <p>Page 2 of 5 </p>
            </div>
          );
        }}
      />

      <form
        onSubmit={onSubmit}
        style={{
          background: 'white',
          margin: '0 auto',
          width: '80%',
          padding: '1.3rem 1rem 2rem',
        }}
      >
        <p
          style={{
            fontSize: 20,
            marginBottom: 20,
          }}
        >
          Address Information
        </p>

        <div className="grid-2">
          <InputIcon
            isEditable={true}
            name={'streetNumber'}
            onChange={onChange}
            value={streetNumber}
            title={'Street Number'}
            // isRequired={true}
          />

          <InputIcon
            isEditable={true}
            name={'streetName'}
            onChange={onChange}
            value={streetName}
            title={'Street Name'}
            isRequired={true}
          />
        </div>

        <div className="grid-2">
          <InputIcon
            isEditable={true}
            name={'city'}
            onChange={onChange}
            value={city}
            title={'City'}
            isRequired={true}
          />

          <InputIcon
            isEditable={true}
            name={'state'}
            onChange={onChange}
            value={state}
            title={'State of Origin'}
            isRequired={true}
          />
        </div>

        <div className="grid-2">
          <InputIcon
            isEditable={true}
            name={'lga'}
            onChange={onChange}
            value={lga}
            title={'LGA'}
            isRequired={true}
          />

          <InputIcon
            isEditable={true}
            name={'nationality'}
            onChange={onChange}
            value={nationality}
            title={'Nationality'}
          />
        </div>

        <div className="grid-2">
          <InputIcon
            isEditable={true}
            name={'landMark'}
            onChange={onChange}
            value={landMark}
            title={'Nearest Landmark'}
          />

          <InputIcon
            isEditable={true}
            name={'busStop'}
            onChange={onChange}
            value={busStop}
            title={'Nearest Bus Stop'}
          />
        </div>

        <input
          type={'submit'}
          value={'Continue'}
          className={'btn'}
          style={{ marginTop: 20 }}
        />
      </form>
    </>
  );
};
