import React from 'react'
import circleSvg from '../svgs/circle.svg';



function Card({ Ponent, size, color }) {
  return (
    <div className='card-self flex-middle' style={{ 
          background: color || 'white',
          backgroundImage: `url(${circleSvg})`,
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'right center',
          width: size || '100%',
          height: '7.5rem'
    }}>
      {Ponent && <Ponent />}
    </div>
  )
}

export default Card
