import * as React from 'react';
import UseContext from '../../context/useContext';
import { styling } from '../css/css';





export default function AskMe({ onPress, msg, success, uu }){
    const { adjustMany } = React.useContext(UseContext)
    React.useEffect(() => {
        (function(){
            setTimeout(() => uu ? uu() : adjustMany({ loading: false, success: null, errors: null }), 8000)
        })()

        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])
    
    
    return (
        <div className='appear' style={{
              width: 350, padding: 10,
              zIndex: 10000000,
              position: 'absolute',
              left: '40%',
              marginTop: '60px',
              transform: 'translate(0, -40%)',
              border: `2px solid ${success ? styling.__green_dark : styling.__red_dark}`,
          }}>
              {/* {url && <Link to={url} style={{ visibility: 'hidden'}} id={'hide'}></Link>} */}
              <div style={{ ...styling.flexed({justify: 'flex-end'})}}>
                <i 
                    onClick={onPress}
                    className="fas fa-times-circle"
                    style={{ 
                        color: `${success ? styling.__green_dark : styling.__red_dark}`, 
                        cursor: 'pointer'
                    }}
                    
                ></i>
                
              </div>

                <div style={{ padding: 20, fontSize: '0.8rem' }}>
                    <h4 style={{
                        color: `${success ? styling.__green_dark : styling.__red_dark}`,
                    }}>{msg || ''}</h4>
                </div>

            </div>
        
    )
}