import * as React from 'react';
import UseContext from '../../../../../context/useContext';
import { Pagination } from '../../../../../helpers/Utitlities';
import { tryCatchLinkUpToServer } from '../../../../../options/JSDOWNLOADS';

export const DISCO_VIEW = ({ title }) => {
  const { adjustMany } = React.useContext(UseContext);

  const [form, setForm] = React.useState({
    current: 0,
    arr: null,
    item: null,
    view: 10,
  });

  React.useEffect(() => {
    (async function () {
      adjustMany({ loading: true });

      const res = await tryCatchLinkUpToServer({
        method: 'GET',
        urlRoute: 'user',
        params: 'search=VIEW&skip=DISCO',
      });

      if (res.data) {
        console.log(res);
        setForm({ ...form, item: res.data });
        adjustMany({ loading: false });
      } else adjustMany({ loading: false, errors: res.error });
    })();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onChange = (name, value) => setForm({ ...form, [name]: value });
  const { item, current, view } = form;

  return (
    <>
      <div className="flex-side">
        <p style={{ fontSize: '1.5rem', margin: '1rem 0' }}>{title || ''}</p>
        {item && (
          <Pagination
            onChange={onChange}
            arrayItem={form.item}
            parentState={setForm}
            parentChild={form}
            count={10}
          />
        )}
      </div>

      <table className="tables">
        <thead>
          <tr>
            <th>Disco Name</th>
            <th>Abbreviation</th>
            <th>Role</th>
            {/* <th>Last Login Time</th>
            <th>Last Login Date</th> */}
            {/* <th>Installation</th> */}
            {/* <th></th> */}
          </tr>
        </thead>

        <tbody>
          {item &&
            item.slice(current, view).map((d, i) => {
              return (
                <tr key={i}>
                  <td>{d.fullName}</td>
                  <td>{d.abbrv}</td>
                  <td>{d.role}</td>
                  {/* <td>{d.lastLogginTime}</td>
                  <td>{d.lastLogginDate}</td> */}
                  {/* <td>{d.installations}</td> */}

                  {/* <td>
                                <button className='btx'>More..</button>
                            </td> */}
                </tr>
              );
            })}
        </tbody>
      </table>
    </>
  );
};

