import * as React from 'react';
import UseContext from '../../../../context/useContext';
import { InputIcon } from '../../../../helpers/FormInputs';
import { csvDownload, tryCatchLinkUpToServer } from '../../../../options/JSDOWNLOADS';
import CBSide from '../../CBSide';
import Structure from '../../Structure';
import { BTX as BTN } from '../dashbaord/helpers/CBCard';





function Report(){
    const { token, adjustOne, adjustMany } = React.useContext(UseContext)

    const [form, setForm] = React.useState({
        discos: '', prepaidMeterType: '', installationDate: '',
        phase: '', cartonID: '', meterOwner: '', installationStatus: '',
        allocationDate: '', loading: false, error: null, category: ''
    });


    React.useEffect(() => {
        (function(){
            adjustOne('active', 'report')
        })()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const { discos, installationDate, prepaidMeterType, meterOwner,
        phase, cartonID, installationStatus, allocationDate, category } = form;

    const onChange = (name, value) => setForm({ ...form, [name]: value })

   

    return(
        <div>
             {/* <p style={{
                padding: '10px 0',
                marginLeft: 20,
                fontSize: 25,
                textDecoration: 'underline'
            }}>Metering Report</p> */}

            <form style={{ margin: '0 auto', width: '80%', marginTop: 15 }}>
            <p style={{
                padding: '10px 0',
                width: '100%',
                fontSize: 18,
            }}>SELECT ORGANISATION</p>
                
                <div>
                   <InputIcon 
                        name={'discos'}
                        select={true}
                        onChange={onChange}
                        value={discos}
                        options={['AEDC', 'YEDC', 'KEDC', 'EEDC', 'JEDC', 'BEDC', 'IBEDC', 'PHDC', 'EKO', 'IKEJA', 'KAD']}
                        title={'Select DISCOs'}
                   />

                   <InputIcon 
                        name={'meterOwner'}
                        select={true}
                        onChange={onChange}
                        value={meterOwner}
                        options={['MOJEC', 'MOMAS', 'ELVIC', 'MERON', 'SABRIUD', 'TUBOR', 'OTHERS']}
                        title={'Meter Provider'}
                   />
                   <InputIcon 
                        name={'prepaidMeterType'}
                        select={true}
                        onChange={onChange}
                        value={prepaidMeterType}
                        options={['Key meter', 'Smart meter']}
                        title={'Select Prepaid Meter Type'}
                   />
                   
                </div>

                <p style={{
                    padding: '10px 0',
                    marginTop: 10,
                    width: '100%',
                    fontSize: 18,
                }}>OTHER SEARCHING PARAMS</p>

                <div>
                   <InputIcon 
                        name={'phase'}
                        select={true}
                        onChange={onChange}
                        value={phase}
                        options={['1-Phase', '3-Phase']}
                        title={'Metering Phase'}
                   />

                   <InputIcon 
                        name={'allocationDate'}
                        onChange={onChange}
                        value={allocationDate}
                        title={'Installation Date (From)'}
                        typed={'date'}
                   />
                                      
                   <InputIcon 
                        name={'installationDate'}
                        onChange={onChange}
                        value={installationDate}
                        title={'Installation Date (To)'}
                        typed={'date'}
                   />
                                      
                </div>

                <div>

                <InputIcon 
                        name={'category'}
                        select={true}
                        onChange={onChange}
                        value={category}
                        options={['MD', 'Non-MD']}
                        title={'Select Meter Category'}
                   />

                <InputIcon 
                        name={'installationStatus'}
                        select={true}
                        onChange={onChange}
                        value={installationStatus}
                        options={['Installed', 'Allocated', 'In store']}
                        title={'Select Installation Status'}
                   />

                   <InputIcon 
                        name={'cartonID'}
                        onChange={onChange}
                        value={cartonID}
                        title={'Enter Carton ID'}
                   />
                                      
                </div>

               
                <BTN 
                    title={'SEND'}
                    style={{marginTop: 20, width: 200, padding: 15 }}
                    icon={'fas fa-cloud'}
                    onPress={async () => {
                        adjustOne('loading', true)
                        const data = {
                            ...(discos ? { discos } : ''),
                            ...(installationDate ? { installationDate } : ''),
                            ...(prepaidMeterType ? { prepaidMeterType } : ''),
                            ...(meterOwner ? { meterOwner } : ''),
                            ...(installationStatus ? { installationStatus } : ''),
                            ...(cartonID ? { cartonID } : ''),
                            ...(phase ? { phase } : ''),
                            ...(category ? { category } : ''),
                        };

                        const res = await tryCatchLinkUpToServer({
                            method: 'POST', urlRoute: 'map',
                            token, data, 
                            params: `search=FETCH&limit=10000`});

                        if(res.success && res.data?.length > 0){
                           adjustOne('loading', false);
                            csvDownload({
                                data: res.data,
                                structure: (row) => {
                                    const data = {
                                        allocationStatus: row.allocationStatus,
                                        capacity: row.capacity,
                                        cartonID: row.cartonID,
                                        category: row.category,
                                        createdBy: row.createdBy?.name,
                                        defaultUnit: row.defaultUnit,
                                        destinationStore: row.destinationStore,
                                        installer: row.installer?.name || "",
                                        manufacturerName: row.manufacturerName,
                                        meterDigit: row.meterDigit,
                                        meterModel: row.meterModel,
                                        meterNumber: row.meterNumber,
                                        meterOwner: row.meterOwner,
                                        meterSerialNumber: row.meterSerialNumber,
                                        meterSeries: row.meterSeries,
                                        meterType: row.meterType,
                                        phase: row.phase,
                                        prepaidMeterType: row.prepaidMeterType,
                                        storeAddress: row.storeAddress,
                                        
                                        storeManagerContact: row.storeManagerContact,
                                        storeManagerName: row.storeManagerName,
                                        uploadedBy: row.uploadedBy,
                                        vendorName: row.vendorName,
                                        volt: row.volt
                                    };
                                    delete data['_id']
                                    delete data['__v']
                                    delete data['meterCondition']
                                    return data
                                },
                                headings: [
                                    'Allocation Status', 'Capacity',
                                    'Carton ID', "Category", "Created By",
                                    "Default Unit", "Destination Store",
                                    'Installer Name', 'Manufacturer Name',
                                    'Meter Digit', 'Meter Model',
                                     'Meter Number','Meter Owner', 'Meter Serial Number',
                                    'Meter Series', 'Meter Type', 'Phase',
                                    'Prepaid Meter Type', 'Store Address',
                                    'Store Manager Contact', 'Store Manager Name',
                                     'Uploaded By', 'Vendor Name', 'Volt'
                                ],
                                title: `Metering Upload Template`
                            })
                        }else adjustMany({ loading: false, 
                            errors: !res.data?.length > 0 ? 'No result found' : res.error })
                    }}
                />
            </form>
        </div>
    )
}

export default function CBGenerateReport(){

    return (
        <>
            <Structure 
                Side={CBSide}
                BodyPage={Report}
            />
        </>
    )
}



