import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import UseContext from '../../../../context/useContext';
import { tryCatchLinkUpToServer } from '../../../../options/JSDOWNLOADS';
import MapSide from '../../MapSide';
import Structure from '../../Structure';

function GMr() {
  const [data, setData] = React.useState(null);
  const { adjustMany, href } = React.useContext(UseContext);
  const navigate = useNavigate();

  React.useEffect(() => {
    (async function () {
      adjustMany({ loading: true, active: 'notes' });
      const res = await tryCatchLinkUpToServer({
        method: 'GET',
        urlRoute: `map`,
        params: 'search=STORE',
      });

      if (res.success) {
        adjustMany({ loading: false });
        setData(res.data);
      } else {
        adjustMany({ loading: false, errors: res.error });
      }
    })();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      {data && (
        <div
          style={{
            margin: '0 auto',
            width: '90%',
            background: 'white',
            border: '1px solid #ccc',
            padding: '1rem',
            lineHeight: '1.8',
          }}
        >
          <div className="flex-side">
            <h1>Unallocated Meter Records</h1>
            <button
              className="btn"
              style={{ padding: '0.7rem' }}
              onClick={() => navigate(`/${href}/previous-allocation`)}
            >
              {' '}
              View Allocation Report
            </button>
          </div>

          <table className="tables">
            <thead>
              <tr>
                <th>#</th>
                <th>Store Owner</th>
                <th>Store Name</th>
                <th>Total Meter(s)</th>
                <th></th>
              </tr>
            </thead>

            {data &&
              data.map((ae, i) => {
                const { map, total, _id } = ae;
                return (
                  <tbody key={i}>
                    <tr>
                      <td>{i + 1}</td>
                      <td>{map || ''}</td>
                      <td>{_id || ''}</td>
                      <td>{total}</td>
                      <td>
                        <button
                          className="btx brn"
                          onClick={() => {
                            adjustMany({ updateRelated: _id });
                            return navigate(`/${href}/store`);
                          }}
                        >
                          Select
                        </button>
                      </td>
                    </tr>
                  </tbody>
                );
              })}
          </table>

          {/* <p style={{margin: '15px 0'}}>{msg}</p>
    
                    <div className='flex' style={{ justifyContent: 'flex-end' }}>
                        <BTN title={'Falling'} style={{ marginRight: 5}}/>
                        <BTN title={'Keep Falling not'} style={{ marginLeft: 5 }} />
                    </div>
     */}
        </div>
      )}
    </>
  );
}

export default function ScheduleMeter() {
  return (
    <>
      <Structure Side={MapSide} BodyPage={GMr} />
    </>
  );
}
