import * as React from 'react';
import UseContext from '../../../context/useContext';
import { tryCatchLinkUpToServer } from '../../../options/JSDOWNLOADS';
import Structure from '../Structure';
import AdminSide from './AdminSide';
import Strai8tUsers from './Strai8tUsers';
import TableUser from './TableUser';



 function Analytic(){
     const { adjustMany, items } = React.useContext(UseContext)
    const [data, setData] = React.useState(null)
    const [users, setUsers] = React.useState(null)

    async function reload(cl){
            adjustMany({ loading: true })
        
            const { data, error, success } = (!cl && items) || await tryCatchLinkUpToServer({
                method: 'GET', urlRoute: 'project' });

            if(success){
                adjustMany({ loading: false, items: {data, error, success} });
                setData(data)
                setUsers(data.users)
            }else adjustMany({ loading: false, errors: error })
    }
     



    React.useEffect(() => {
        reload()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[]);

   
       


    return(
        
        <>
          <div className='flex-side'>
                <p style={{ fontSize: '1.8rem', marginBottom: 10 }}> {'DASHBOARD'}</p>
                <button className='btx' onClick={() => reload(true)}>
                    <i className='fas fa-sync' style={{ marginRight: '5px'}}></i>
                    Reload
                </button>
            </div>
            {data && <Strai8tUsers DATA={data} />}
           
            {users && <TableUser data={users} reload={reload}/>}
        </>
    )
}

export default function AdminDashboard(){

    return (
        <>
            <Structure 
                Side={AdminSide}
                BodyPage={Analytic}
            />
        </>
    )
}

