import React from 'react'
import HelperContext from '../../context/helpers/helperContext'




function BodyMain({ BodyPage, props }) {
    const { top, hide } = React.useContext(HelperContext);


  return (
    <div className='body-main' style={{ 
        top, 
        position: 'relative',
        paddingLeft: hide ? '24%' : '6.5%',
        paddingRight: '1.5rem',
        paddingTop: '1rem',
        paddingBottom: '1rem',
        transition: 'padding-left 0.5s ease-in-out'
    }}>  

        { BodyPage && <BodyPage props={props} /> }
        
    </div>
  )
}

export default BodyMain