import React from 'react';
import UseContext from '../../../../context/useContext';
import SingleChart from '../../../../helpers/graph/GraphDrawing';
import {
  GetUnique,
  Sum,
  tryCatchLinkUpToServer,
} from '../../../../options/JSDOWNLOADS';
import { BTX } from '../../../cbs/pages/dashbaord/helpers/CBCard';
import Structure from '../../Structure';
import FirstRow from './helpers/FirstRow';
import SecondRow from './helpers/SecondRow';

function DashboardMainPage() {
  const { adjustMany, special, mapped } = React.useContext(UseContext);
  const [form, setForm] = React.useState({
    total: 0,
    installations: 0,
    allocations: 0,
    current: '',
    customer: 0,
    others: null,
    single: 0,
    un: [],
    three: 0,
    data: null,
    allocationDetail: [{}],
    office: [{}],
  });

  const reload = async (reloadAgain) => {
    adjustMany({ loading: true, active: 'dashboard' });
    const res =
      (!special && mapped && !reloadAgain) ||
      (await tryCatchLinkUpToServer({
        method: 'GET',
        urlRoute: 'map',
        params: special ? `search=DISK-MAP&skip=${special}` : `search=DISK-MAP`,
      }));

    if (res.success) {
      adjustMany({ loading: false, disco: res });
      const { data } = res;
      setForm({
        ...form,
        total: data.total || 0,
        installations: data.installations || 0,
        allocations: data.outOfStoreCount || 0,
        single: data.single || 0,
        three: data.three || 0,
        customer: data.customer,
        allocationDetail: data.allocations,
        un: [],
        office: [],
        data,
      });
    } else adjustMany({ loading: false });
  };

  React.useEffect(() => {
    (async function () {
      await reload();
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <div
        style={{ fontSize: '1.5rem', marginBottom: '1rem' }}
        className="flex-side"
      >
        <p>Dashboard</p>
        <BTX
          title={'Reload'}
          icon={'fas fa-sync'}
          onPress={() => reload(true)}
          bg={'royalblue'}
        />
      </div>

      {/* GRIDs */}
      <FirstRow
        DATA={{
          map_: form.total,
          disco: form.single,
          people: form.three,
        }}
      />
      <SecondRow
        DATA={{
          map_: form.allocations,
          people: form.total - form.allocations,
          disco: form.installations,
          total: form.total,
        }}
      />

      <SingleChart
        BODY={
          form.allocationDetail
            ? form.allocationDetail.map((a) => {
                return {
                  name: a.receiver,
                  view: a.total_allocated,
                };
              })
            : [{}]
        }
        LABEL={'ALLOCATION'}
        title={'Allocation to DISCOs'}
      />
    </>
  );
}

export default function MapDashboard() {
  return <Structure BodyPage={DashboardMainPage} />;
}
