import * as React from 'react';
import UseContext from '../../../../../context/useContext';
import { pickEmitter } from '../../../../../helpers/essential';
import { FilterInput } from '../../../../../helpers/TypedInput';
import { Pagination } from '../../../../../helpers/Utitlities';

export default function CBTableSpan({ title }) {
  const { items, nav, adjustMany } = React.useContext(UseContext);
  const { disco_provision, meters_transit } = items ? items.data || {} : {};

  const [form, setForm] = React.useState({
    current: 0,
    arr: null,
    item: null,
    view: 10,
    all: [{}],
  });

  React.useEffect(() => {
    (async function () {
      setForm({
        ...form,
        item: nav === 'MAP-VIEW' ? meters_transit : disco_provision,
        all: nav === 'MAP-VIEW' ? meters_transit : disco_provision,
      });
    })();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onChange = (name, value) => setForm({ ...form, [name]: value });
  const { item, current, view, all } = form;

  return (
    <>
      <div className="flex-side">
        <p style={{ fontSize: '1.5rem', margin: '1rem 0' }}>{title || ''}</p>
        {item && (
          <Pagination
            onChange={onChange}
            arrayItem={form.item}
            parentState={setForm}
            parentChild={form}
            count={1}
          />
        )}
      </div>

      <FilterInput
        placeholder={'Filter item...'}
        original={all}
        parentData={form}
        parentState={setForm}
        params={nav === 'MAP-VIEW' ? 'provider' : 'name'}
      />

      <table className="table-span">
        <thead>
          <tr>
            <th
              rowSpan={2}
              style={{
                fontSize: '1rem',
                // textAlign: 'center',
                width: '200px',
                background: '#f2f2f2',
                // color: 'white',
                border: '1px solid #ccc',
                padding: '0.5rem',
              }}
            >
              {nav === 'MAP-VIEW' ? 'Meter Provider' : 'Received From'}
            </th>
            <th
              colSpan={3}
              style={{
                background: '#f2f2f2',
                // color: 'white',
                padding: '0.5rem',
                fontSize: '1rem',
                textAlign: 'center',
                border: '1px solid #ccc',
              }}
            >
              {nav === 'MAP-VIEW'
                ? 'Total Meters Provided'
                : 'Total Meters Received'}
            </th>
            <th
              colSpan={3}
              style={{
                fontSize: '1rem',
                textAlign: 'center',
                width: '200px',
                background: '#f2f2f2',
                // color: 'white',
                border: '1px solid #ccc',
              }}
            >
              Total Allocated
            </th>
            <th
              colSpan={3}
              style={{
                fontSize: '1rem',
                textAlign: 'center',
                width: '200px',
                background: '#f2f2f2',
                // color: 'white',
                border: '1px solid #ccc',
              }}
            >
              {nav === 'MAP-VIEW' ? 'Meters on Transit' : 'Installation'}
            </th>
          </tr>
          <tr className="th-d">
            <th style={{ borderLeft: '1px solid #ccc' }}>1-Phase</th>
            <th>3-Phase</th>
            <th style={{ borderRight: '1px solid #ccc' }}>Total</th>

            <th style={{ borderLeft: '1px solid #ccc' }}>1-Phase</th>
            <th>3-Phase</th>
            <th style={{ borderRight: '1px solid #ccc' }}>Total</th>

            <th>1-Phase</th>
            <th>3-Phase</th>
            <th style={{ borderRight: '1px solid #ccc' }}>Total</th>
          </tr>
        </thead>

        <tbody>
          {item &&
            item.slice(current, view).map((a, i) => {
              const {
                uploadedBy,
                single_phase_sent,
                three_phase_sent,
                total_sent,
                single_phase_in_store,
                three_phase_in_store,
                total_in_store,
                single_phase_on_transit,
                three_phase_on_transit,
                total_on_transit,
                provider,
                receiver,
                single_phase_received,
                three_phase_received,
                total_received,
                single_phase_sent_and_install,
                three_phase_sent_and_install,
              } = a;

              return (
                <tr
                  key={i}
                  className="tr-d"
                  onClick={() => {
                    if (nav === 'MAP-VIEW') {
                      adjustMany({ special: uploadedBy });
                      return pickEmitter('map').click();
                    } else {
                      adjustMany({ special: receiver });
                      return pickEmitter('disco').click();
                    }
                  }}
                >
                  <td style={{ textAlign: 'left' }}>
                    {nav === 'MAP-VIEW'
                      ? uploadedBy
                      : `${provider}:${receiver}`}
                  </td>
                  <td>
                    {nav === 'MAP-VIEW'
                      ? single_phase_sent + single_phase_in_store
                      : single_phase_received}
                  </td>

                  <td>
                    {nav === 'MAP-VIEW'
                      ? three_phase_sent + three_phase_in_store
                      : three_phase_received}
                  </td>

                  <td>
                    {nav === 'MAP-VIEW'
                      ? total_sent + total_in_store
                      : total_received}
                  </td>

                  <td>{single_phase_sent || 0}</td>
                  <td>{three_phase_sent || 0}</td>
                  <td>
                    {nav === 'MAP-VIEW'
                      ? total_sent
                      : single_phase_sent + three_phase_sent}
                  </td>

                  <td
                    style={{
                      color: nav === 'MAP-VIEW' ? 'red' : 'auto',
                    }}
                  >
                    {nav === 'MAP-VIEW'
                      ? single_phase_on_transit || 0
                      : single_phase_sent_and_install || 0}
                  </td>
                  <td
                    style={{
                      color: nav === 'MAP-VIEW' ? 'red' : 'auto',
                    }}
                  >
                    {nav === 'MAP-VIEW'
                      ? three_phase_on_transit || 0
                      : three_phase_sent_and_install || 0}
                  </td>

                  <td
                    style={{
                      color: nav === 'MAP-VIEW' ? 'red' : 'auto',
                    }}
                  >
                    {nav === 'MAP-VIEW'
                      ? total_on_transit || 0
                      : three_phase_sent_and_install +
                        single_phase_sent_and_install}
                  </td>
                </tr>
              );
            })}

          <tr className="tr-d"></tr>
        </tbody>
      </table>
    </>
  );
}
