import React, { useState } from 'react'

function SearchBar({ onPress, icon }) {
    const [value, setValue] = useState('');
    const onChange = (e) => setValue(e.target.value)


  return (
    <div className='search-bar flex-side'>
        <input 
            type={'search'} 
            value={value} 
            onChange={onChange} 
            className="search-input"
            placeholder={'Search for Meter Item...'}
        />
        <i className={icon || 'fas fa-search'} onClick={() => onPress && onPress(value)} />
    </div>
  )
}

export default SearchBar