import * as React from 'react';
import UseContext from '../../../context/useContext';
import Absolutes from '../../../helpers/alerts/Absolutes';
import { styling } from '../../../helpers/css/css';
import { AppCard, IconWithName, IntIcons, SquareBox } from '../../../helpers/Utitlities';



export default function AppAlerts(){
    const { adjustOne } = React.useContext(UseContext);

    const [apps, setApps] = React.useState([])


    React.useEffect(() => {
        (function(){
            adjustOne('active', 'my_app')
        })()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])


    return(
        <Absolutes 
            Ponent={function(){

                return (
                    <div style={{
                        width: '88%',
                        height: '85%',
                        background: 'white',
                        overflow: 'auto',
                        boxShadow: styling._shadow_2
                    }}>

                    <div 
                        style={{  
                            justifyContent:'space-between', 
                            padding: '1rem', marginBottom: 10
                     }} 
                        className="flex"
                    >

                        <h1>App Stack</h1>
                        <IntIcons 
                            onPress={() => adjustOne('appTitles', false)}
                            name={'fas fa-times'}
                        />
                    </div>

                    {/* Header */}

                    <div style={{
                        borderBottom: '1px solid #ccc',
                        padding: '1rem 1rem 0', ...styling.flexed({}),

                    }}>

                        <div style={{ ...styling.flexed({}) }}>
                            <IconWithName 
                                icon={'fas fa-home'}
                                title={'My Applications'}
                                onPress={() => setApps([
                                    {
                                        appName: 'React.Dispatch<React.SetStateAction',
                                        appID: 'react-hooks/exhaustive4904309',
                                        icon: 'fas fa-home',
                                        vist: 'https://qu-essence.com/api/v1/metering/store',
                                        bg: styling._primary_500,
                                        semiColor: styling._primary_200
                                    },
                                ])}
                                name={'my_app'}
                            />

                            <IconWithName 
                                icon={'fas fa-home'}
                                title={'App Store'}
                                name={'market'}
                                onPress={() => setApps(null)}
                            />
                           
                        </div>

                        <SquareBox 
                            title={'Hellos'}
                            size={250}
                            icon={'fas fa-search'}
                            onPress={text => alert(text)}
                        />

                    </div>

                    {/* App Container */}
                    <div style={{ padding: '1rem'}} className={'grid-3'}>
                        {apps && apps.map((el, i) => {
                            const { appID, appName, semiColor, bg, icon, vist } = el
                            
                            return <AppCard key={i}
                                appID={appID}
                                appName={appName}
                                semicolor={semiColor}
                                bg={bg}
                                icon={icon}
                                onPress={() => {
                                    if(vist) window.location.href = vist
                                    else return false
                                }}
                            />
                        })}
                        
                    </div>
                    



                       

                    </div>
                )
            }}
        
        />
    )
}