import * as React from 'react';




export default function LogAbs({ msg, Ponent, fade }){
 

    return (
        <div className={'absolutes'} style={{ opacity: fade || ''}}>
            {!Ponent ? <div className="flex" style={{ 
                padding: '2rem', width: 300,
                flexDirection: 'column'
            }}>

                <div className='spinner' style={{
                    marginRight: '10px'
                }}> </div>

                    <p>{msg || 'Loading...'}</p>

            </div> : <Ponent />}
        </div>
        
    )
}