import * as React from 'react';
import UseContext from '../../../../context/useContext';
import { styling } from '../../../../helpers/css/css';
import CBSide from '../../CBSide';
import Structure from '../../Structure';
import { useNavigate } from 'react-router-dom';




 function GMr() {
     const { href, adjustOne } = React.useContext(UseContext);
     const navigate = useNavigate()

    React.useEffect(() => {
        adjustOne('active', 'exception')
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])



   return (
            <>
           
            <div style={{ 
              margin: '0 auto', 
              width: '90%',
              background: 'white', 
              border: '1px solid #ccc',
              padding: '1rem', lineHeight: '1.8'
            }}>

                <div className='flex-side'>
                    <h1>Exception Report</h1>
                </div>

                    <CONTAINER 
                        title={'Allocation: Meter match error'}
                        num={1}
                        onPress={() => navigate(`/${href}/exempt`)}
                    />

                    <CONTAINER 
                        title={'Allocation: Meters In transit'}
                        num={2}
                        onPress={() => navigate(`/${href}/transit`)}
                    />

                    </div>
            </>
        )
    }
        

export default function ExceptionReport(){
    

   

    return (
        <>
            <Structure 
                Side={CBSide}
                BodyPage={GMr}
            />
        </>
    )
}



function CONTAINER ({title, onPress, num }){
    
    return (
        <div className='gv' onClick={onPress} 
            style={{ ...styling.flexed({justify: 'flex-start'})}}
        >
            <h3>{`${num || 1}.`}</h3>
            <h3>{title || 'Hello'}</h3>
        </div>
    )
}