import * as React from 'react';
import { GBox } from '../design/Esses';




export default function Strai8tUsers({ DATA, changeRef, href }){
    // const { adjustMany } = React.useContext(UseContext)
    const { total, issueLogged, issueResolved } = DATA;


    return(
        <>
          <div className='grid-3' style={{ gap: 0 }}>
              <GBox 
                  title={'TOTAL USER'} 
                //   perf={'20%'}
                  comment={'Active'}
                  infoIcon={'fas fa-users'}
                //   onPress={}
                  size={"100%"}
                  value={total}
              />
        
                <GBox 
                    title={'ISSUE RAISED'} 
                    // perf={'20%'}
                    comment={'Active'}
                    infoIcon={'fas fa-users'}
                    // onPress={}
                    size={"100%"}
                    value={issueLogged}
                />
                

                <GBox 
                    title={'RESOLVED ISSUE(S)'} 
                    perf={((issueResolved && issueLogged) ? issueResolved / issueLogged : 0).toFixed(2)}
                    comment={'Active'}
                    infoIcon={'fas fa-user-tie'}
                    // onPress={}
                    size={"100%"}
                    value={issueResolved}
                    error={((issueResolved && issueLogged) ? issueResolved / issueLogged : 0)>=1 ? true : false}
                />
                
            </div>

           </>
        
    )
}