import React from 'react';
import { BarChart } from './CompareGraph';

export default function CompareChart({
  height,
  width,
  title,
  BODY_,
  OPT,
  LABEL,
  LABEL0,
  onGraphClick,
}) {
  const BODY = typeof BODY_ === 'function' ? BODY_() : BODY_;

  return (
    <div
      style={{
        background: 'white',
        border: '1px solid #ccc',
        // height: height || 300
      }}
    >
      <div
        className="flex-side"
        style={{
          marginBottom: 10,
          borderBottom: '1px solid #ccc',
          padding: '0.2rem 1rem',
        }}
      >
        <p style={{ padding: '0.8rem 0', fontSize: '1.2rem' }}>
          {title || 'Graph Title'}
        </p>
      </div>

      <div
        style={{
          padding: '0.7rem 1rem 1rem',
        }}
      >
        <BarChart
          WIDTH={height || 600}
          HEIGHT={width || 300}
          LABELS={BODY.map((e) => e.name)}
          LABEL={LABEL}
          LABEL0={LABEL0}
          DATA={BODY.map((e) => e.view)}
          DATA0={BODY.map((e) => e.value)}
          OPT={OPT}
          onGraphClick={onGraphClick}
        />
      </div>
    </div>
  );
}
