import React from 'react';
import GeneralReducer from './generalReducer';
import GeneralContext from './generalContext';
import { CLEARITEM, CLEARMANY, LOCALMOVEMENT, RESTOREALL } from './types';




export default function GeneralState ({ children }){

    const initialState = {
        contents: 'Welcome General content',
    };

    const [state, dispatch] = React.useReducer(GeneralReducer, initialState);

    // Function Decleration;
    const saveAndContinue = (data) => {
        delete data['name']
        delete data['isModel']
        delete data['arrayItem']
        delete data['original']

        dispatch({ type: LOCALMOVEMENT, payload: data })
    }
    const restoreAll = () => dispatch({ type: RESTOREALL, payload: {} })
    const adjustOne = (name, value) => dispatch({ type: CLEARITEM, payload: { name, value } });
    const adjustMany = (value) => dispatch({ type: CLEARMANY, payload: value });

   

    // Dispatch to User;

    return <GeneralContext.Provider
        value={{
            contents: state.contents,
            adjustOne, saveAndContinue, restoreAll,
            adjustMany
        }}
    >
        { children }
    </GeneralContext.Provider>



    
}