import * as React from 'react';
import UseContext from '../../context/useContext';
import AppAlerts from '../../layout/header/app_alerts/App_Alerts';
import AskMe from './AskMe';
import ClicksAndClick from './ClicksAndClick';
import LOADING from './Loading'
import Vkey from './Vkey';


export default function AlertConfig(){
    const { loading, errors, success, cancel, proceed,
        showAlerts, adjustMany, appTitles, quickLink, Ponent } = React.useContext(UseContext);


    const message = errors || success;
    const green = errors ? false : true;


    if(loading) return <LOADING />
    else if(message && !showAlerts) return <AskMe 
        msg={message} 
        success={green}
        onPress={() => adjustMany({
            errors: null, success: null,
            loading: false
        })}
    />

    else if(showAlerts) return <Vkey 
        proceed={proceed} 
        cancel={cancel} 
        onPress={() => adjustMany({ showAlerts: null, errors: null, success: null })}
        msg={message}
        errStatus={green}
    />

    else if(quickLink) return <ClicksAndClick 
        proceed={proceed} 
        cancel={cancel} 
        onPress={() => adjustMany({ quickLink: null })}
        Ponent={Ponent}
    />

    else if (appTitles) return <AppAlerts />
    else return <></>
}