import { styling } from '../../../../helpers/css/css';
import { tryCatchLinkUpToServer } from '../../../../options/JSDOWNLOADS';

const RequestPagination = ({
  arrayItem,
  parentState,
  parentChild,
  count,
  url,
}) => {
  const { current, view, item } = parentChild;

  const change = async (quest) => {
    if (arrayItem.length + 1 > view && quest >= 0) {
      const currentPos = current + (quest || 0);
      const currentView = quest + view;
      const res = await tryCatchLinkUpToServer({
        method: 'GET',
        urlRoute: url,
        params: `is_certified=false&offset=${currentPos}`,
      });

      return parentState({
        ...parentChild,
        arr: res.success ? (res.data.length > 0 ? res.data : [{}]) : [{}],
        current: currentPos,
        view: currentView,
      });
    }

    if (typeof quest === 'number' && quest < 0 && current >= count) {
      const currentPos = current + (quest || 0);
      const currentView = quest + view;

      const res = await tryCatchLinkUpToServer({
        method: 'GET',
        urlRoute: url,
        params: `sql=other_customer_info.is_certified=false&offset=${currentPos}`,
      });

      return parentState({
        ...parentChild,
        arr: res.success ? (res.data.length > 0 ? res.data : [{}]) : [{}],
        current: currentPos,
        view: currentView,
      });
    }
  };

  return (
    <div style={{ ...styling.flexed({}) }}>
      <i
        className="fas fa-angle-left pagi hoves"
        onClick={() => change(-count)}
        style={{ cursor: 'pointer' }}
      ></i>

      {Object.keys(item[0]).length > 0 && (
        <h6 style={{ fontSize: '0.7rem' }}>{`${current + 1}-${view}`}</h6>
      )}

      <i
        className="fas fa-angle-right pagi hoves"
        onClick={() => change(count)}
        style={{ cursor: 'pointer' }}
      ></i>
    </div>
  );
};

export default RequestPagination;
