import * as React from 'react';
import { styling } from '../helpers/css/css';





export default function Headers({ HeaderItems, SideIcons }) {
  
    
    return(
        <div className='header-back' style={{ marginBottom: 20, }}>
        <div style={{
            ...styling.flexed({ play: 'grid', mesurement: '2fr 1fr', gap: 0 }),
            width: '100%', paddingRight: 20, 
        }}>
            <div style={{
                ...styling.flexed({justify: 'flex-start'})
            }}>
                { HeaderItems && <HeaderItems /> }
            </div>
            <div style={{
                ...styling.flexed({justify: 'flex-end'}),
                fontSize: 14
            }}>
               
               {SideIcons && <SideIcons /> }
            </div>
        </div>
            
        </div>
    ) 
}