import React from 'react';
import UseContext from '../../../../context/useContext';
import CompareChart from '../../../../helpers/graph/BarWrapper';
import { Sum, tryCatchLinkUpToServer } from '../../../../options/JSDOWNLOADS';
import { BTX } from '../../../cbs/pages/dashbaord/helpers/CBCard';
import Structure from '../../Structure';
import FirstRow from './helpers/FirstRow';
import SecondRow from './helpers/SecondRow';

function DashboardMainPage() {
  const { adjustMany, special, disco, user } = React.useContext(UseContext);
  const [form, setForm] = React.useState({
    total: 0,
    installations: 0,
    allocations: 0,
    current: '',
    customer: 0,
    others: null,
    single: 0,
    opt: ['INSTALLER', 'FRANCHISE OFFICE'],
    three: 0,
    data: null,
    allocationDetail: [],
  });

  const reload = async (reloadAgain) => {
    adjustMany({ loading: true, active: 'dashboard' });

    const res =
      (!special && disco && !reloadAgain) ||
      (await tryCatchLinkUpToServer({
        method: 'GET',
        urlRoute: 'disco',
        params: `search=DISK&skip=${special || user.abbrv}`,
      }));

    if (res.success) {
      adjustMany({ loading: false, disco: res });
      const { data } = res;
      setForm({
        ...form,
        total: data.total || 0,
        installations: data.installations || 0,
        allocations: data.outOfStoreCount || 0,
        single: data.single || 0,
        three: data.three || 0,
        customer: data.customer_count,
        allocationDetail: data.allocations,
        data,
      });
    } else adjustMany({ loading: false });
  };

  React.useEffect(() => {
    (async function () {
      await reload();
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <div
        style={{ fontSize: '1.5rem', marginBottom: '1rem' }}
        className="flex-side"
      >
        <p>Dashboard</p>
        <BTX
          title={'Reload'}
          icon={'fas fa-sync'}
          onPress={() => reload(true)}
          bg={'royalblue'}
        />
      </div>

      <FirstRow
        DATA={{
          map_: form.total,
          disco: form.single,
          people: form.three,
        }}
      />
      <SecondRow
        DATA={{
          map_: form.customer,
          disco: form.allocations,
          people: form.installations,
        }}
      />

      <CompareChart
        BODY_={() => {
          const array = form.allocationDetail;
          const exs = array.length > 0;
          const bn = exs
            ? array.map((d) => {
                const {
                  installer: name,
                  total_allocated: view,
                  total_installed: value,
                } = d;
                return { name, view, value };
              })
            : [];

          return bn;
        }}
        LABEL={'ALLOCATION'}
        LABEL0={'INSTALLATION'}
        title={'Meters Allocated to Installer Vs Installation'}
      />
    </>
  );
}

export default function DiscoDashboard() {
  return <Structure BodyPage={DashboardMainPage} />;
}
