import * as React from 'react';
import { Link } from 'react-router-dom';
import UseContext from '../../../../context/useContext';
import { pickEmitter } from '../../../../helpers/essential';
import CBSide from '../../CBSide';
import Structure from '../../Structure';
import CBTableSpan from './tabl/TableSpans';



function View(){
    const { nav, href } = React.useContext(UseContext)


    // React.useEffect(() => {
    //     (function(){
    //         adjustOne('active', 'View')
    //     })()
    //     // eslint-disable-next-line react-hooks/exhaustive-deps
    // }, [])

    
   

    return(
        <>
         <Link to={`/${href}/view-summary`} style={{visibility: 'hidden'}} id={'summary'}></Link>
         <Link to={`/${href}/disco`} style={{visibility: 'hidden'}} id={'disco'}></Link>
         <Link to={`/${href}/map`} style={{visibility: 'hidden'}} id={'map'}></Link>
            {nav === 'DISCO-VIEW' && <CBTableSpan 
                title={'Meter(s) Received from Meter Provider'}
                move={() => pickEmitter('summary')}
            /> }
           {nav === 'MAP-VIEW' && <CBTableSpan 
                title={'Meter(s) Allocated by Meter Provider '}
                move={() => pickEmitter('summary')}
            />}

        </>
    )
}


export default function MeterInformation(){

    return (
        <>
            <Structure 
                Side={CBSide}
                BodyPage={View}
            />
        </>
    )
}



