export const csvDownload = ({ data, structure, headings, title }) => {
  if (data.length !== 0 && structure) {
    const dataStructure = data.map((row) => structure(row))
    const downloadableData = objectToCsv(dataStructure)
    download(downloadableData)
  }

  function objectToCsv(data) {
    const csvRow = []
    // Headers
    const headers = Object.keys(data[0])
    csvRow.push((headings || headers).join(','))

    // Loop through Data
    for (const row of data) {
      const value = headers.map((head) => {
        const escape = ('' + row[head]).replace(/"/g, '\\"')
        return `"${escape}"`
      })

      csvRow.push(value.join(','))
    }

    return csvRow.join('\n')
  }

  function download(data) {
    const blob = new Blob([data], { type: 'text/csv' })
    const url = window.URL.createObjectURL(blob)
    const a = document.createElement('a')
    a.setAttribute('hidden', '')
    a.setAttribute('href', url)
    a.setAttribute('download', `${title || 'Download'}.csv`)
    document.body.appendChild(a)
    a.click()
    document.body.removeChild(a)
  }
}

export const Sum = (array) => {
  if (!array) return 0
  if (array[0] == null) return 0

  let all = 0
  for (let i = 0; i < array.length; i++) {
    all += array[i]
  }

  return all
}

export const GetUnique = (array) => {
  if (!array) return []
  return [...new Set(array)]
}

const create_queries = (obj) => {
  if (!obj) return {}
  let keys = ''
  let aggs = '&'
  const key = Object.keys(obj)
  const value = Object.values(obj)

  for (let i = 0; i < key.length; i++) {
    if (i < key.length - 1) {
      aggs += `${key[i]}=${value[i]}&`
      keys += `${key[i]},`
    } else {
      aggs += `${key[i]}=${value[i]}`
      keys += `${key[i]}`
    }
  }
  return { keys, aggs }
}

export const UREL = 'https://goldfish-app-yfjsl.ondigitalocean.app'

export const tryCatchLinkUpToServer = async function ({
  data,
  urlRoute,
  params,
  image,
  method,
  token,
}) {
  const url = 'https://goldfish-app-yfjsl.ondigitalocean.app/api/v1'
  // const url = 'http://localhost:5530/api/v1';
  const apiKey = token || SETAUTH().decode()

  if (method === 'POST' && !image) {
    const withParams = params ? `${urlRoute}?${params}` : urlRoute
    try {
      const result = await axios.post(`${url}/${withParams}`, data, {
        headers: {
          'Content-Type': 'application/json',
          ...(apiKey ? { authorization: `Bearer ${apiKey}` } : ''),
        },
      })

      return result.data
    } catch (error) {
      return error.response.data
    }
  } else if (method === 'GET') {
    const queries = data ? create_queries(data).aggs : ''
    const withParams = params ? `${urlRoute}?${params}${queries}` : urlRoute
    try {
      const result = await axios.get(`${url}/${withParams}`, {
        headers: {
          ...(apiKey ? { authorization: `Bearer ${apiKey}` } : ''),
        },
      })

      return result.data
    } catch (error) {
      return error.response.data
    }
  } else if (method === 'PUT' && !image) {
    const withParams = params ? `${urlRoute}?${params}` : urlRoute
    try {
      const result = await axios.put(`${url}/${withParams}`, data, {
        headers: {
          'Content-Type': 'application/json',
          ...(apiKey ? { authorization: `Bearer ${apiKey}` } : ''),
        },
      })

      return result.data
    } catch (error) {
      return error.response.data
    }
  } else if (method === 'POST' && image) {
    const withParams = params ? `${urlRoute}?${params}` : urlRoute

    const formData = new FormData()
    formData.append('file', data)
    try {
      const result = await axios.post(`${url}/${withParams}`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
          ...(apiKey ? { authorization: `Bearer ${apiKey}` } : ''),
        },
      })

      return result.data
    } catch (error) {
      return error.response.data
    }
  } else if (method === 'PUT' && image) {
    const withParams = params ? `${urlRoute}?${params}` : urlRoute

    const formData = new FormData()
    formData.append('file', data)
    try {
      const result = await axios.put(`${url}/${withParams}`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
          ...(apiKey ? { authorization: `Bearer ${apiKey}` } : ''),
        },
      })

      return result.data
    } catch (error) {
      return error.response.data
    }
  } else return null
}

export const sum = (data) => {
  if (!data) return 0
  if (typeof data === 'number') return data
  else return data.reduce((a, b) => a + b, 0)
}
export const quickMap = (data, funnel) => data.map((el) => el[funnel])

export const pickEmitter = (e) => document.getElementById(e)

export const navigate = (e, cb) => {
  const nav = window.location
  if (e && !nav.pathname.includes(e)) {
    cb && cb()
    return (nav.href = `${nav.origin}/${e}`)
  }

  cb && cb()
}
