import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import UseContext from '../../../context/useContext';
import { InputIcon } from '../../../helpers/FormInputs';
import { tryCatchLinkUpToServer } from '../../../options/JSDOWNLOADS';
import Structure from '../Structure';







function Project(){
    const {  adjustMany, href } = React.useContext(UseContext);
    const navigate = useNavigate();

    const [form, setForm] = React.useState({
        projectName: '', projectDescription: '', 
        projectSetTarget: '', starts: '', ends: '',
    })
    
    
    const { projectName, projectDescription, projectSetTarget, starts, ends } = form;

    const onChange = (name, value) => setForm({...form, [name]: value })


        React.useEffect(() => {
        (function(){
            adjustMany({active: 'create-project' })
        })()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const onSubmit = async e => {
        e.preventDefault();
        adjustMany({ loading: true })
        const res = await tryCatchLinkUpToServer({
            method:'POST', urlRoute: 'project',
            data: { ...form, projectDuration: { starts, ends }}
        });

        if(res.success){
            adjustMany({ success: res.data, loading: false });
            return navigate(`/${href}`)
        }else adjustMany({ loading: false, errors: res.error })
    }



    return(
        <>

        <form onSubmit={onSubmit} style={{
                background: 'white',
                margin: '0 auto', width: '80%', 
                padding: '1.3rem 1.2rem 2rem',
                marginTop: 20
            }}>

            <h2 style={{
                // fontSize: 20,
                marginBottom: 20,
            }}>Create New Project</h2>
                
                <div className="grid-2">
                   <InputIcon 
                        name={'projectName'}
                        onChange={onChange}
                        value={projectName}
                        title={'Project Name'}
                        icon={'fas fa-project-diagram'}
                        isRequired={true}
                   />

                   <InputIcon 
                        name={'projectSetTarget'}
                        onChange={onChange}
                        value={projectSetTarget}
                        title={'Project Set Target'}
                        isRequired={true}
                        icon={'fas fa-bullseye'}
                   />
                   
                </div>

                <InputIcon 
                        name={'projectDescription'}
                        onChange={onChange}
                        value={projectDescription}
                        title={'Project Brief Description'}
                        isRequired={true}
                        icon={'fas fa-info-circle'}
                   />

                <h3 style={{ margin: '1rem 0'}}>PROJECT DURATION</h3>

                <div className="grid-2">

                   <InputIcon 
                        name={'starts'}
                        onChange={onChange}
                        value={starts}
                        title={'Project Start Date'}
                        typed={'date'}
                        isRequired={true}
                        icon={'fas fa-calendar'}
                   />

                    <InputIcon 
                        name={'ends'}
                        onChange={onChange}
                        value={ends}
                        title={'Project End Date'}
                        typed={'date'}
                        isRequired={true}
                        icon={'fas fa-calendar'}
                   />
                   
                                      
                </div>

               <input 
                    type={'submit'} 
                    value={'Continue'} 
                    className={'btn'} 
                    style={{ marginTop: 20 }}
                />


        </form>
        </>
    )
}




export default function AddProject(){

    return <Structure
        BodyPage={Project}
    />
}