import React from 'react'
import { useNavigate } from 'react-router-dom'
import UseContext from '../../context/useContext';
import { IonIcon } from '../../layout/sidebar/sideHelper'




export default function DiscoSide() {
     const { href } = React.useContext(UseContext)
     const navigate = useNavigate();

  return (
    <div className='side-icons'>
       <IonIcon
            title={'Dashboard'}
            icon={'fas fa-bar-chart'}
            onPress={() => navigate(`/${href}/dashboard`)}
            name={'dashboard'}
       />

       <IonIcon
            title={'Metering Request'}
            icon={'fa fa-external-link'}
          //   sideIcon={'fas fa-chevron-down'}
            onPress={() => navigate(`/${href}/apply`)}
          //   name={''}
       />

       <IonIcon
            title={'Meter Allocation'}
            icon={'fas fa-list'}
          //   sideIcon={'fas fa-chevron-down'}
            onPress={() => navigate(`/${href}/allocation`)}
       />

       <IonIcon
            title={'Upload Metering Data'}
            icon={'fas fa-book'}
            onPress={() => navigate(`/${href}/create-record`)}
       />

       <IonIcon
            title={'Upload Request Data'}
            icon={'fas fa-users'}
            onPress={() => navigate(`/${href}/create-customer`)}
       />


    </div>
  )
}
