import React from 'react'
import HelperContext from '../../context/helpers/helperContext'
import HeadLogo from '../../images/brand/logo-3.png';
import HeadLogo1 from '../../images/brand/logo-2.png';
import UseContext from '../../context/useContext';


export const IonIcon = ({ onPress, title, icon, sideClick, sideIcon, name }) => {
    const { hide } = React.useContext(HelperContext)
    const { active } = React.useContext(UseContext)


    return<div className={`ion-icon ${hide ? 'flex-side' : 'flex-middle'}`} 
        onClick={onPress}
        style={{ color: name === active && 'royalblue' }}
    >
        <div className='flex-start'>
            {icon && <i className={icon} />}
           {hide && <p>{title || ''}</p>}
        </div>

        { (hide && sideIcon) && <i className={sideIcon} onClick={sideClick} style={{ fontSize: '0.5rem'}} />}
        
    </div>
}


export const SideHeader = () => {
    const { hide } = React.useContext(HelperContext)

    return<div className='side-header flex-middle' style={{ padding: hide ? '1.11rem' : '1rem'}}>
        <img 
            src={ hide ? HeadLogo : HeadLogo1 } 
            alt={'Programme Titles'} 
            style={{ 
                width: hide ? 'auto' : '50px',
                height: hide ? 'auto' : '40px',
            }}
        />
    </div>
}



export const SideWrapper = ({ children }) => {
    const { hide } = React.useContext(HelperContext)

    return <div id='side' 
        className={'side-main'} 
        style={{ width: hide ? '22%' : '5%' }}
        >
            { children }
    </div>
}