import * as React from 'react';
import { styling } from '../helpers/css/css';
import { InputLabelled } from '../helpers/FormInputs';
import { pickEmitter, tryCatchLinkUpToServer } from '../options/JSDOWNLOADS';
import { SETAUTH } from '../util/SetAuth';
import LogAbs from '../helpers/alerts/LogAbs';
import signImage from '../images/sign.jpg';

export default function LOGIN({ name, change, value }) {
  // const { adjustMany, isAuthenticated } = React.useContext(UseContext)

  const [form, setForm] = React.useState({
    password: '',
    email: '',
    loading: false,
    route: false,
    error: null,
    register: false,
  });
  const onChange = (name, value) => setForm({ ...form, [name]: value });

  const { password, email, loading, error } = form;

  React.useEffect(() => {
    (async function () {
      const validCheck = SETAUTH().decode();
      setForm({ ...form, loading: true });
      if (validCheck !== null) {
        const res = await tryCatchLinkUpToServer({
          method: 'GET',
          urlRoute: 'user?search=DECODE',
          token: validCheck,
        });

        if (res.success) {
          setForm({ ...form, loading: false });
          return change(name, {
            success: true,
            data: { token: validCheck, data: res.data },
          });
        } else {
          SETAUTH().setToken(null);
          setForm({ ...form, loading: false });
        }
      } else setForm({ ...form, loading: false });
    })();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onSubmit = async (e) => {
    e.preventDefault();

    setForm({ ...form, loading: true });

    const password = pickEmitter('password').value;
    const email = pickEmitter('email').value;

    const res = await tryCatchLinkUpToServer({
      method: 'POST',
      urlRoute: 'user/login',
      data: { password, email },
    });

    if (res.success) {
      const resR = await tryCatchLinkUpToServer({
        method: 'GET',
        urlRoute: `user/${email}`,
        token: res.data,
      });

      if (resR.success) {
        const nb = `${Math.random() * 9695699}`.substring(0, 3);
        const encode = `${nb}:EPRO~${res.data}`;

        SETAUTH().setToken(window.btoa(encode));
        setForm({ ...form, loading: false, route: true });
        return change(name, {
          success: true,
          data: { token: res.data, data: resR.data },
        });
      } else setForm({ ...form, loading: false, error: resR.error });
    } else setForm({ ...form, loading: false, error: res.error });
  };

  if (form.register)
    return <Register name={name} change={change} value={value} />;

  if (loading) return <LogAbs msg={'Please wait...'} />;

  return (
    <>
      {!value && (
        <form
          className="absolutes"
          style={{
            backgroundImage: `url(${signImage})`,
            backgroundRepeat: 'no-repeat',
            backgroundAttachment: 'fixed',
            backgroundPosition: 'center',
            backgroundSize: 'cover',
          }}
          onSubmit={onSubmit}
        >
          <div
            className="appear login"
            style={{
              width: '30%',
              padding: '2rem',
              backgroundColor: 'white',
              borderRadius: '10px',
              boxShadow: `${styling._shadow_3},`,
              // textAlign:'center',
            }}
          >
            <h4 style={{ textAlign: 'center' }}>LOGIN INTO YOUR PROFILE</h4>

            {error && (
              <div
                style={{
                  padding: '1rem',
                  marginTop: 10,
                  ...styling.flexed({ justify: 'center' }),
                  borderRadius: 5,
                  background: styling.__red_light,
                  color: styling.__red_dark,
                  fontSize: '0.8rem',
                }}
              >
                <h5 style={{ textAlign: 'center' }}>{error}</h5>
              </div>
            )}

            <InputLabelled
              title={'Enter Username'}
              onChange={onChange}
              value={email}
              name={'email'}
              leftIcon={'fas fa-user-tie'}
              isRequired={true}
              id_={'email'}
            />

            <InputLabelled
              title={'Enter Password'}
              typed={'password'}
              onChange={onChange}
              value={password}
              name={'password'}
              leftIcon={'fas fa-lock'}
              isRequired={true}
              id_={'password'}
              rightIcon={'fas fa-eye'}
            />

            <input
              type={'submit'}
              value={'SIGN IN'}
              className={'btn'}
              style={{ marginTop: 20, width: '100%' }}
            />

            <p
              style={{ marginTop: 15, fontSize: '0.8rem', textAlign: 'center' }}
            >
              If you don't have account with us yet,{' '}
              <span
                className="bns"
                onClick={() => setForm({ ...form, register: true })}
                style={{
                  fontWeight: 'bolder',
                  color: 'orange',
                  textDecoration: 'underline',
                  cursor: 'pointer',
                  margin: '0 3px',
                  textTransform: 'uppercase',
                }}
              >
                Sign up
              </span>
              and register your property for meter
            </p>
          </div>
        </form>
      )}
    </>
  );
}

function Register({ name, change, value }) {
  const [form, setForm] = React.useState({
    fname: '',
    lname: '',
    email: '',
    password: '',
    telephone: '',
    password1: '',
    error: null,
    loading: false,
  });

  const {
    fname,
    lname,
    email,
    password,
    telephone,
    password1,
    error,
    loading,
  } = form;

  const onChange = (name, value) => setForm({ ...form, [name]: value });

  const onSubmit = async (e) => {
    e.preventDefault();
    onChange('loading', true);

    const lname = pickEmitter('lname').value;
    const fname = pickEmitter('fname').value;
    const email = pickEmitter('email').value;
    const password = pickEmitter('password').value;
    const password1 = pickEmitter('password1').value;

    if (password !== password1) {
      return setForm({
        ...form,
        error: 'Password: Match error',
        loading: false,
      });
    } else {
      const send = await tryCatchLinkUpToServer({
        method: 'POST',
        urlRoute: 'user',
        data: { fname, lname, email, password, telephone },
      });

      if (send.success) {
        const res = await tryCatchLinkUpToServer({
          method: 'POST',
          urlRoute: 'user/login',
          data: { password, email },
        });

        if (res.success) {
          const resR = await tryCatchLinkUpToServer({
            method: 'GET',
            urlRoute: 'user',
            token: res.data,
          });

          if (resR.success) {
            const nb = `${Math.random() * 9695699}`.substring(0, 3);
            const encode = `${nb}:EPRO~${res.data}`;

            SETAUTH().setToken(window.btoa(encode));
            setForm({ ...form, loading: false, route: true });
            return change(name, {
              success: true,
              data: { token: res.data, data: resR.data },
            });
          } else setForm({ ...form, loading: false, error: resR.error });
        } else setForm({ ...form, loading: false, error: res.error });
      } else setForm({ ...form, loading: false, error: send.error });
    }
  };

  if (loading) return <LogAbs msg={'Please wait...'} />;

  return (
    <>
      {!value && (
        <form
          className="absolutes"
          style={{
            backgroundImage: `url(${signImage})`,
            backgroundRepeat: 'no-repeat',
            backgroundAttachment: 'fixed',
            backgroundPosition: 'center',
            backgroundSize: 'cover',
          }}
          onSubmit={onSubmit}
        >
          <div
            className="appear"
            style={{
              width: '60%',
              padding: '2rem',
              backgroundColor: 'white',
              borderRadius: '10px',
              boxShadow: `${styling._shadow_3},`,
              // textAlign:'center',
            }}
          >
            <h2 style={{ textAlign: 'center', color: 'orange' }}>SIGN UP</h2>

            {error && (
              <div
                style={{
                  padding: '1rem',
                  marginTop: 10,
                  ...styling.flexed({ justify: 'center' }),
                  borderRadius: 5,
                  background: styling.__red_light,
                  color: styling.__red_dark,
                  fontSize: '0.8rem',
                }}
              >
                <h5 style={{ textAlign: 'center' }}>{error}</h5>
              </div>
            )}

            <div className="grid-2">
              <InputLabelled
                title={'First Name'}
                onChange={onChange}
                value={fname}
                name={'fname'}
                leftIcon={'fas fa-user-tie'}
                isRequired={true}
              />

              <InputLabelled
                title={'Last Name'}
                onChange={onChange}
                value={lname}
                name={'lname'}
                leftIcon={'fas fa-user-tie'}
                isRequired={true}
              />
            </div>

            <div className="grid-2">
              <InputLabelled
                title={'Telephone Number'}
                onChange={onChange}
                value={telephone}
                name={'telephone'}
                leftIcon={'fas fa-phone'}
                isRequired={true}
                length={11}
              />

              <InputLabelled
                title={'Email address'}
                onChange={onChange}
                value={email}
                name={'email'}
                leftIcon={'fas fa-user-tie'}
                isRequired={true}
                typed={'email'}
              />
            </div>

            <div className="grid-2">
              <InputLabelled
                title={'Create a password'}
                onChange={onChange}
                value={password}
                name={'password'}
                leftIcon={'fas fa-lock'}
                isRequired={true}
                rightIcon={'fas fa-eye'}
                typed={'password'}
              />

              <InputLabelled
                title={'Confirm Password'}
                onChange={onChange}
                value={password1}
                name={'password1'}
                leftIcon={'fas fa-lock'}
                isRequired={true}
                rightIcon={'fas fa-eye'}
                typed={'password'}
              />
            </div>

            <input
              type={'submit'}
              value={'SIGN UP'}
              className={'btn'}
              style={{ marginTop: 20, width: '100%' }}
            />
          </div>
        </form>
      )}
    </>
  );
}
