import React, { useEffect } from 'react';
import HelperContext from '../../context/helpers/helperContext';
import { pickEmitter } from '../../helpers/essential';
import { SayHello } from './HeaderLogic';
import RightDropDown from './RightDropDown';
import SearchBar from './SearchBar';
const icon1 = `fas fa-bars toogle-bar`;
const icon2 = `fas fa-times toogle-bar`;




function HeaderConfig() {
    const { hide, ...helper } = React.useContext(HelperContext);

  useEffect(() => {
    (function(){
        helper.adjustMany({ top: pickEmitter('app-header').clientHeight})
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])


  return (
    <div id={'app-header'}
      className={`app-header flex-side`}>
        <div 
          className='flex-side' 
          style={{ width: '100%'}}>

            <div className='flex-start app-child-main' style={{ 
                left: hide ? '23%' : '5%', 
                transition: 'left 0.5s ease-in-out',
                position: 'relative'
            }}>
                <i className={hide ? icon1 : icon2} onClick={() => helper.adjustMany({ hide: !hide })}/>
                <SearchBar onPress={SayHello}/>
            </div>
            <RightDropDown />
        </div>

    </div>
  ) 
}

export default HeaderConfig