import * as React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import UseContext from '../../context/useContext';
import DiscoAllocations from './pages/allocation/DiscoAllocation';
import MainAloPage from './pages/allocation/MainAloPage';
import ShowAllocation from './pages/allocation/ShowAllocation';
import StoreDetails from './pages/allocation/StoreDetails';
import DiscoCreateCSV from './pages/csvUploads/DiscoCreateCSV';
import DiscoCreateCustomerCSV from './pages/csvUploads/DiscoCreateCustomerCSV';
import DiscoDashboard from './pages/dashbaord/DiscoDashoard';
import CustomerTable from './pages/request/CustomerTable';
import ViewCustomerProfile from './pages/request/details/MainApcn';
import Structure from './Structure';

export default function Discos() {
  const { href } = React.useContext(UseContext);

  const [url, setUrl] = React.useState(null);

  React.useEffect(() => {
    (function () {
      if (href) {
        setUrl(href);
      }
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const DG = () => {
    return (
      <Structure
        BodyPage={function () {
          return <div>{/* <h1>Finally</h1> */}</div>;
        }}
      />
    );
  };

  return (
    <>
      <Router>
        <Routes>
          <Route path={`${url}`} element={<DG />} />
          <Route path={`/${url}/dashboard`} element={<DiscoDashboard />} />
          <Route path={`/${url}/allocation`} element={<StoreDetails />} />
          <Route path={`/${url}/create-record`} element={<DiscoCreateCSV />} />
          <Route
            path={`/${url}/confirm-allocation`}
            element={<MainAloPage />}
          />
          <Route path={`/${url}/apply`} element={<CustomerTable />} />
          <Route path={`/${url}/store`} element={<DiscoAllocations />} />
          <Route
            path={`/${url}/previous-allocations`}
            element={<ShowAllocation />}
          />
          <Route path={`/${url}/view`} element={<ViewCustomerProfile />} />
          <Route
            path={`/${url}/create-customer`}
            element={<DiscoCreateCustomerCSV />}
          />
        </Routes>
      </Router>
    </>
  );
}
