import * as React from 'react';
import UseContext from '../../../../context/useContext';
import { styling } from '../../../../helpers/css/css';
import { UploadFileContainer } from '../../../../helpers/TypedInput';
import { tryCatchLinkUpToServer, csvDownload, uploadTemplate } from '../../../../options/JSDOWNLOADS';
import DiscoSide from '../../DiscoSide';
import Structure from '../../Structure';



 function CSV() {
    const { adjustOne, adjustMany } = React.useContext(UseContext);

   const [form, setForm]=React.useState({ edit: true,
     path: '', loading: false, error: null, success: null })


     React.useEffect(() => {
         (function(){
            adjustOne('active', 'create-customer')
         })();
         
         // eslint-disable-next-line react-hooks/exhaustive-deps
     }, [])
   
   const { path } = form;

   const onChange = (name, value) => setForm({ ...form, [name]: value })

    
    return (
        <>
            <div  style={{ 
                boxShadow: styling._shadow_4, 
                width: '85%', 
                backgroundColor: 'white',
                margin: '0 auto',
                marginBottom: 10,
                padding: 20,
                borderRadius: 10,
                fontSize: '0.9rem',
            }}>
                <div>
                    <div style={{padding: 5}}>
                        <div>
                            <h3 style={{marginBottom: 10, fontSize: '1rem'
                                }}>
                                Create Many Customer Records
                            </h3>
                            
                        </div>
                        

                    <div style={{ color: 'red', lineHeight: 1.7}}>
                        {/* <p>*File Extention should be .csv format*</p> */}
                        <p>*Make sure you use camelCase for your headers e.g "firstName"*</p>
                        <p>*Headers much contain: ['fname', 'lname', 'streetName', 'city', 'busStop',
                            'state', 'lga', 'nationality','landMark',
                            'buildingType','email', 'identityType', 'identityID',
                            'preferredMeterPhase' and 'requestType'
                            ] 
                            as they're all required fields*</p>
                    </div>

                    <button className={'btn'} 
                        style={{
                            margin: '10px 0 10px', 
                            width: '100%',
                            // height: '40px',
                        }}

                        onClick={() => csvDownload({
                            data: uploadTemplate.CUSTOMER,
                            structure: (row) => row,
                            headings: uploadTemplate.CUSTOMER0,
                            title: `Customer Record Upload Template`
                        })}
                    >
                        Download Template
                        <i className='fas fa-book' style={{marginLeft: 10}}></i>
                    </button>

                    {/* BTN WITH DOTTED*/}
                    <UploadFileContainer 
                        name={'path'}
                        value={path}
                        onChange={onChange}
                    
                    />

                    <div style={{ 
                        width: '100%', 
                        textAlign: 'center',
                        padding: 15
                    }}>
                        <h5>Don't delete any row title in the template before file upload?</h5>
                       
                
                    </div>

                    <button className={'btn'} 
                        style={{
                            margin: '10px 0 10px', 
                            width: '100%',
                            height: '40px',
                        }}
                        onClick={async() => {
                            adjustOne('loading', true)
                          const res = await tryCatchLinkUpToServer({
                            method:'POST', data: path, params: 'search=CUSTOMER',
                            urlRoute: `form74/csv-upload`, image: true
                        });

                        if(res.success){
                            adjustMany({ loading: false, success: res.data })
                        }else adjustMany({ loading: false, errors: res.error })
                    }}
                    >
                        Upload File
                        <i className='fa fa-cloud-upload' style={{marginLeft: 10}}></i>
                    </button>
                    

                        
                    </div>
                </div>
            </div>
        </>
    )
}



export default function DiscoCreateCustomerCSV(){

    return (
        <>
            <Structure 
                Side={DiscoSide}
                BodyPage={CSV}
            />
        </>
    )
}




