import * as React from 'react';
import UseContext from '../../../../../context/useContext';
import { InputIcon } from '../../../../../helpers/FormInputs';
import { HeaderItems } from '../../../../../helpers/TypedInput';
import Headers from '../../../../../layout/Headers';




export const PropertyInfo = ({onChange, form}) => {
    const { adjustOne } = React.useContext(UseContext)
    const { buildingType, buildingStructure, occupantRole, propertyOwnerName,
         buildingUse, occupantContact, occupantName, propertyOwnerContact } = form;


    

    const onSubmit = e => {
        e.preventDefault();
        return adjustOne('skip', 'image')
    }



    return(
        <>

        <Headers HeaderItems={
            function(){
                return <HeaderItems 
                    title={'CUSTOMER RECORD MODULE'}
                    onPress={() => adjustOne('skip', 'address')}
                />
            }
        }

        SideIcons={function (){
            return<div>
                <p>Page 3 of 5 </p>
            </div>
        }}
        
        />

        <form onSubmit={onSubmit} style={{
                background: 'white',
                margin: '0 auto', width: '80%', padding: '1.3rem 1.2rem 2rem'
            }}>

            <p style={{
                fontSize: 20,
                marginBottom: 20,
            }}>Property Information</p>
                
                <div className="grid-3">
                   <InputIcon
                        isEditable={true} 
                        name={'buildingType'}
                        onChange={onChange}
                        value={buildingType}
                        title={'Building Type'}
                        isRequired={true}
                   />

                   <InputIcon
                        isEditable={true} 
                        name={'buildingStructure'}
                        onChange={onChange}
                        value={buildingStructure}
                        title={'Building Age'}
                        isRequired={true}
                   />

                    <InputIcon
                            isEditable={true} 
                        name={'buildingUse'}
                        onChange={onChange}
                        value={buildingUse}
                        title={'Building Use'}
                        isRequired={true}
                   />

                   
                </div>

                <p style={{
                    fontSize: 15,
                    margin: `20px 0 10px`,
                }}>Occupant Information</p>

                <div className="grid-3">
                    <InputIcon
                            isEditable={true} 
                            name={'occupantName'}
                            onChange={onChange}
                            value={occupantName}
                            title={'Occupant Full Name'}
                            isRequired={true}
                            
                    />

                    <InputIcon
                            isEditable={true} 
                        name={'occupantContact'}
                        onChange={onChange}
                        value={occupantContact}
                        title={'Occupant Contact / Telephone'}
                        isRequired={true}
                   />

                    <InputIcon
                            isEditable={true} 
                        name={'occupantRole'}
                        onChange={onChange}
                        value={occupantRole}
                        title={'Occupant Role'}
                   />
                </div>


                <div className="grid-2">
                    <InputIcon
                            isEditable={true} 
                            name={'propertyOwnerName'}
                            onChange={onChange}
                            value={propertyOwnerName}
                            title={'Property Owner Names'}
                            isRequired={true}
                            
                    />

                    <InputIcon
                            isEditable={true} 
                        name={'propertyOwnerContact'}
                        onChange={onChange}
                        value={propertyOwnerContact}
                        title={'Property Owner Contact'}
                        isRequired={true}
                   />

                    
                </div>


                <input 
                    type={'submit'} 
                    value={'Continue'} 
                    className={'btn'} 
                    style={{ marginTop: 20 }}
                />


                
        </form>
        </>
    )
}