import * as React from 'react';
import UseContext from '../../context/useContext';





export default function Absolutes({ msg, Ponent, fade }){
    const { dark } = React.useContext(UseContext);

    return (
        <div className={dark || 'absolutes'} style={{ opacity: fade || ''}}>
            {!Ponent ? <div className="flex" style={{ 
                padding: '2rem', width: 300,
                flexDirection: 'column'
            }}>

                <div className='spinner' style={{
                    marginRight: '10px'
                }}> </div>

                    <p>{msg || 'Loading...'}</p>

            </div> : <Ponent />}
        </div>
        
    )
}