import * as React from 'react';
import UseContext from '../../../../../context/useContext';
import {
  csvDownload,
  tryCatchLinkUpToServer,
} from '../../../../../options/JSDOWNLOADS';
import CBSide from '../../../CBSide';
import Structure from '../../../Structure';

function GMr() {
  const [data, setData] = React.useState(null);
  const { adjustMany, updateRelated } = React.useContext(UseContext);

  React.useEffect(() => {
    (async function () {
      adjustMany({ loading: true, active: 'track' });
      const res =
        updateRelated ||
        (await tryCatchLinkUpToServer({
          method: 'GET',
          urlRoute: `user?search=VIEW`,
        }));

      if (res.success) {
        adjustMany({ loading: false, updateRelated: res });
        setData(res.data);
      } else {
        adjustMany({ loading: false, errors: res.error });
      }
    })();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      {data && (
        <div
          style={{
            margin: '0 auto',
            width: '90%',
            background: 'white',
            border: '1px solid #ccc',
            padding: '1rem',
            lineHeight: '1.8',
          }}
        >
          <div className="flex-side">
            <h1>Exception Report</h1>
          </div>

          <table className="tables">
            <thead>
              <tr>
                <th>#</th>
                <th>Meter From</th>
                <th>Meter To</th>
                <th>Total Sent</th>
                <th>Total Received</th>
                <th>Failed</th>
                <th></th>
              </tr>
            </thead>

            {data &&
              data.map((ae, i) => {
                const { from, to, sent, receive, diff } = ae;

                return (
                  <tbody key={i}>
                    <tr>
                      <td>{i + 1}</td>
                      <td>{from || ''}</td>
                      <td>{to || ''}</td>
                      <td>{sent}</td>
                      <td>{receive}</td>
                      <td style={{ color: 'red', fontWeight: 'bold' }}>
                        {diff || 0}
                      </td>
                      <td>
                        <button
                          className="btx"
                          onClick={async () => {
                            adjustMany({ loading: true });
                            const res = await tryCatchLinkUpToServer({
                              method: 'GET',
                              urlRoute: `map?from=${from}&to=${to}&search=VIEW`,
                            });

                            if (res.success) {
                              adjustMany({ loading: false });
                              csvDownload({
                                data: res.data,
                                structure: (row) => {
                                  delete row['_id'];
                                  delete row['createdBy'];
                                  delete row['modifiedBy'];
                                  delete row['meterCondition'];
                                  delete row['__v'];

                                  return row;
                                },

                                title: `Exception`,
                              });
                            }
                          }}
                        >
                          Download
                        </button>
                      </td>
                    </tr>
                  </tbody>
                );
              })}
          </table>
        </div>
      )}
    </>
  );
}

export default function TransitReport() {
  return (
    <>
      <Structure Side={CBSide} BodyPage={GMr} />
    </>
  );
}
