import * as React from 'react';
import { styling } from '../css/css';





export default function ClicksAndClick({ name, uu,
     value, onPress, title, errStatus, Ponent, proceed, cancel }){

    return (
        <>
            <div className='absolutes'>
                <AV 
                    name={name}
                    title={title}
                    onPress={onPress}
                    uu={uu}
                    cancel={cancel}
                    proceed={proceed}
                    value={value}
                    Ponent={Ponent}
                />
            </div>
            
        </>
    )
}


function AV({ title, proceed, cancel, Ponent, onPress }){

    return(
        <>
           <div className={'appear'} style={{
                width: '50%',  margin: '0 auto',
                borderRadius: 5
            }}>
                {/* TITLE */}
                <div style={{
                    ...styling.flexed({}),
                    borderBottom: '1px solid #f2f2f2',
                    padding: 10
                }}>
                    <h3 style={{
                        textTransform: 'uppercase',
                        color: styling._primary_400,
                        paddingLeft: 10
                    }}>
                        {title|| ''}
                    </h3>
                    {<p className='cancel'
                        style={{ cursor: 'pointer' }}
                        onClick={onPress}
                    >x</p>}
                </div>

                {/* Alert */}
                {Ponent && <Ponent />}

                {/* Body */}

                <div style={{ 
                    width: '80%', 
                    margin: '0 auto',
                    ...styling.flexed({justify: 'flex-end'}),
                    marginTop: 20
                }}>

                    {cancel && <button className='btn' 
                        style={{marginTop: 10, marginRight: '10px', background: 'red' }}
                        onClick={cancel}
                        
                    >
                        <i className='fas fa-thumbs-up' style={{ marginRight: 10 }}></i>
                       {title || 'Cancel'}
                    </button>}

                    {proceed && <button className='btn' 
                        style={{
                            marginTop: 10, marginLeft: '10px', 
                            background: styling.__green_dark 
                        }}
                        onClick={proceed}
                        
                    >
                        <i className='fas fa-thumbs-up' style={{ marginRight: 10 }}></i>
                       {title || 'Process'}
                    </button>}

                </div>
            </div>
        </>
    )
}