import { Line, Polar, Bar, HorizontalBar, Doughnut, Pie } from 'react-chartjs-2';
import { BACKGROUND, BORDERCOLORS } from './GraphUtils';



export const LineChart = ({ DATA0, LABEL0, bg, style,
     DATA, LABELS, LABEL, OPT, HEIGHT, WIDTH }) => {
    
        const bo0 = BORDERCOLORS.slice(0,DATA.length)
        const bd0 = BACKGROUND.slice(0, DATA.length)
    
        const bo = BORDERCOLORS.slice(DATA.length, DATA.length + DATA.length)
        const bd = BACKGROUND.slice(DATA.length, DATA.length + DATA.length)


    return(
       <div style={{ background: bg || 'transparent',}}>
           <Line
                height={HEIGHT || 600}
                width={WIDTH || 300}
                data={{
                    labels: LABELS,
                    datasets: [{
                        label: LABEL,
                        data: DATA,
                        backgroundColor: bd0,
                        borderColor: bo0,
                        borderWidth: 1,
                        borderSkipped: false,
                        ...style,
                    },{
                        label: LABEL0,
                        data: DATA0,
                        backgroundColor: bd,
                        borderColor: bo,
                        borderWidth: 1,
                        borderSkipped: false,
                        ...style,
                    }],
                }}
                options={{
                    maintainAspectRatio: false,
                    scales: {
                        yAxes: [{
                            ticks: { beginAtZero: true },
                        }]
                    },
                    legend: { display: false, labels: true,  },
                    ...OPT,
                }}

           />
       </div>
    )
}


