import React from 'react';
import CompareChart from '../../../../../helpers/graph/BarWrapper';
import { UTILS } from '../../../../../helpers/graph/GraphUtils';

function GraphComponent({ mapActivities }) {
  const array = mapActivities || [{}];

  const onGraphClick = (data) => {
    console.log(mapActivities[data]);
  };

  return (
    <>
      <CompareChart
        title={
          'Individual Meter Provider Allocations Vs Meter Received by DISCOs'
        }
        BODY_={() => {
          const exs = array.length > 0;
          const bn = exs
            ? array.map((d) => {
                const {
                  provider: name,
                  total_sent: view,
                  total_received: value,
                } = d;
                return { name, view, value };
              })
            : [];

          return bn;
        }}
        UTILS={UTILS}
        LABEL={'Meter Provider'}
        LABEL0={'DISCO'}
        onGraphClick={onGraphClick}
      />
    </>
  );
}

export default GraphComponent;
