import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import UseContext from '../../../../../context/useContext';
import { styling } from '../../../../../helpers/css/css';
import { InputIcon } from '../../../../../helpers/FormInputs';
import { HeaderItems } from '../../../../../helpers/TypedInput';
import Headers from '../../../../../layout/Headers';
import { tryCatchLinkUpToServer } from '../../../../../options/JSDOWNLOADS';




export const UploadSavedData = () => {
    const { adjustMany, adjustOne, singleItem } = React.useContext(UseContext);
    const [uref, setUref] = React.useState(null)

    const navigate = useNavigate()



    const makeCall = async ({ customerId, telephone, LAT, LONG, locationCoords,
         uniqueID, name, designation, e}) => {
        setUref('/')
        adjustMany({ loading: true });
        const res = await tryCatchLinkUpToServer({
            method: 'PUT', urlRoute: `form74/${customerId}`,
            data: { 
                LAT, LONG, locationCoords,
                isCertified: e === 're-asign' ? true : false, 
                ...(e === 're-asign' ? {certification: {
                    telephone, uniqueID, 
                    name, designation 
                }}: { isCancelled: true })
            },  params: 'name=customerId&search=skip'
        });
        if(res.success){
            
            const resR = await tryCatchLinkUpToServer({
                method: 'PUT', urlRoute: `order/${customerId}`,
                data: { orderStatus: e ==='re-asign' ? 'Processing': 'Cancelled', 
                ...(e === 're-asign' ? {certification: {
                    telephone, uniqueID, 
                    name, designation 
                }}: ''), autoCreate: e === 're-asign' ? true : false},  
                params: 'name=customerId&search=skip'
            });
            
            if(resR.success){
                adjustMany({ loading: false, quickLink: false, 
                    success: 'Resolved Successfully..'});
                return navigate(uref || '/')
            }
        }else adjustMany({ 
            loading: false, 
            errors: res.error 
        })
    }



    return(
        <>

        <Headers HeaderItems={
            function(){
                return <HeaderItems 
                    title={'CUSTOMER RECORD MODULE'}
                    onPress={() => adjustOne('nav', 'image')}
                />
            }
        }

        SideIcons={function (){
            return<div>
                <p>Page 5 of 5 </p>
            </div>
        }}
        
        />

       
        <div style={{
                background: 'white',
                margin: '0 auto', width: '80%', padding: '1.3rem 1rem 2rem'
            }}>

            <p style={{
                fontSize: 20,
                marginBottom: 20,
            }}>Click "Accept" to verify this property</p>

             
             <div style={{ ...styling.flexed({ justify: 'flex-end'})}}>

             <button 
                style={{ background: 'red'}}
                className="btn"
                onClick={() => makeCall({
                            customerId: singleItem.customerId,
                            telephone:{}, uniqueID:{},
                            e: 'decline', name: {}, designation: {}
                        })
                }>
                    Decline             
              </button>

                
                <button 
                    className='btn'
                    style={{ background: 'green'}}
                    onClick={() => 
                        adjustMany({
                            Ponent: () => {
                            const [certify, setCertify] = React.useState({
                                name: '', telephone: '', LAT: '', LONG: '',
                                meterPhase: '',
                                uniqueID: '', designation: ''
                            });
                            const { name, telephone, uniqueID,
                                meterPhase, LAT, LONG, designation } = certify;

                            const change = (name, value) => setCertify({...certify,
                                [name]: value
                            })


                            return <div style={{ padding: '1rem' }}>
                                <p style={{ fontSize: '1.3rem', marginBottom: '10px'}}>
                                    Site Verification
                                </p>

                                <div style={{ padding: '1rem', background: '#f2f2f2'}}>
                                
                                <InputIcon 
                                    title={'Select suitable meter phase'}
                                    onChange={change}
                                    name={'meterPhase'}
                                    value={meterPhase}
                                    style={{ borderRadius: 5, flex: 1, marginTop: 10}}
                                    select={true}
                                    options={['1-Phase', '3-Phase']}
                                />
                                <div>
                                <InputIcon 
                                    title={'Enter Property Longitude'}
                                    onChange={change}
                                    name={'LONG'}
                                    value={LONG}
                                    style={{ borderRadius: 5, flex: 1, marginTop: 10}}
                                />

                                <InputIcon 
                                    title={'Enter Property Latitude'}
                                    onChange={change}
                                    name={'LAT'}
                                    value={LAT}
                                    style={{ borderRadius: 5, flex: 1, marginTop: 10}}
                                />

                                </div>

                                

                                <InputIcon 
                                    title={'Verify By (Name)'}
                                    onChange={change}
                                    name={'name'}
                                    value={name}
                                    style={{ borderRadius: 5, flex: 1, marginTop: 10}}
                                />

                                <InputIcon 
                                    title={'Telephone'}
                                    onChange={change}
                                    name={'telephone'}
                                    value={telephone}
                                    style={{ borderRadius: 5, flex: 1, marginTop: 10}}
                                />

                                <InputIcon 
                                    title={'Selet Role'}
                                    onChange={change}
                                    name={'designation'}
                                    value={designation}
                                    style={{ borderRadius: 5, flex: 1, marginTop: 10}}
                                    select={true}
                                    options={['Lecan Officer', 'Disco Official']}
                                />
                                
                                <InputIcon 
                                    title={'Certificate ID'}
                                    onChange={change}
                                    name={'uniqueID'}
                                    value={uniqueID}
                                    style={{ borderRadius: 5, flex: 1, marginTop: 10}}
                                />


                                </div>

                                
                                <button className='btx'
                                    style={{ marginTop: 15 }}
                                    onClick={() => makeCall({
                                        customerId: singleItem.customerId,
                                        telephone, uniqueID, 
                                        name, designation,
                                        LAT, LONG, locationCoords: `${LAT},${LONG}`,
                                        e: 're-asign',
                                    })}
                                >
                                    Send
                                </button>

                                </div>
                            },
                            quickLink: true,   
                        })}>Accept</button>

             </div>
             
              


        </div>
        </>
    )
}