import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import UseContext from '../../../context/useContext';
import { styling } from '../../../helpers/css/css';
import { InputIcon } from '../../../helpers/FormInputs';
import { FilterInput } from '../../../helpers/TypedInput';
import { Pagination } from '../../../helpers/Utitlities';
import { STATES } from '../../../options/coutries';
import { tryCatchLinkUpToServer } from '../../../options/JSDOWNLOADS';
import { BTs } from '../design/Esses';






export default function TableUser({ data }){
    const { adjustMany, href } = React.useContext(UseContext)
    const [form, setForm] = React.useState({
        current: 0, arr: null, item: data, view:4, all:data
    });


    const onChange = (name, value) => setForm({...form, [name]: value })
    const { item, current, view, all } = form;

    const navigate = useNavigate();

    const remove = async (id, action) => {
        adjustMany({ loading: true });
        const res = await tryCatchLinkUpToServer({
            method: `PUT`, urlRoute: `user/${id}`,
            data: { isDisabled: action === 'enable' ? false : true }
        })

        if(res.success){
            adjustMany({ loading: false, Ponent: null })
            return navigate(`/${href}`)
        }else adjustMany({ loading: false, errors: res.error })
    }
    

    return(
        <>

            <div className='flex-side'>
                <p style={{ fontSize: '1.5rem', margin: '1rem 0'}}>User Profiles</p>
                {item && <Pagination 
                     onChange={onChange}
                     arrayItem={form.item}
                     parentState={setForm}
                     parentChild={form}
                     count={4}
                />}
            </div>

            <FilterInput
                placeholder={'Filter item...'}
                original={all}
                parentData={form}
                parentState={setForm}
                params={'fullName'}
            />

            <table className='admin-user'>
                <thead>
                    <tr className='admin-tr'>
                        <th>Name</th>
                        <th>Email</th>
                        <th>Telephone</th>
                        <th>Role</th>
                        <th>Status</th>
                        <th style={{ textAlign: 'right'}}>Action</th>
                    </tr>
                </thead>

                <tbody>
                    {data && item.slice(current, view).map((d,i)=>{
                        const { fullName, email, telephone1, role, isDisabled } = d || {};
                        return (
                            <tr key={i}>
                                <td>{fullName || ''}</td>
                                <td>{email || ''}</td>
                                <td>{telephone1 || ''}</td>
                                <td>{role || ''}</td>
                                <td style={{
                                    color: isDisabled ? 'red' : 'green'
                                }}>{isDisabled ? 'Disabled' : 'Active'}</td>
                                {fullName && <td style={{ textAlign: 'right'}}>
                                    <div className='user-td-icon'>
                                        <i 
                                            className='fas fa-edit' 
                                            onClick={() => {
                                                adjustMany({
                                                    quickLink: true,
                                                    Ponent: () => Edit({ whoami: d,
                                                         cb: () => navigate(`/${href}`) })
                                                })
                                            }}></i>

                                        <i className='fas fa-user'
                                            onClick={() => remove(d.email, 'enable')}
                                        ></i>

                                        <i 
                                            className='fas fa-user-alt-slash'
                                            onClick={() => remove(d.email, 'disable')}
                                            ></i>
                                    </div>
                                </td>}
                            </tr>
                        )
                    })}
                </tbody>
            </table>
        </>
    )
};


function Edit({ whoami, cb }) {
    const { adjustMany } = React.useContext(UseContext)
    const [form, setForm] = React.useState({
        fname:whoami.fname || '', lname: whoami.lname || '', 
        email: whoami.email || '', telephone1: whoami.telephone1 || '', 
        role: whoami.role || '', identityID: whoami.identityID || '', 
        identityType:whoami.identityType || '', telephone2: whoami.telephone2 ||'',
        em: '', supplyArea: whoami.supplyArea || [], abbrv: whoami.abbrv || '',
        franchiseStates: whoami.franchiseStates || [], fullName: whoami.fullName || ''
    });

    const visitor = ['GUEST', 'INSTALL'];
    const dey = ['DISCO', 'MAP'];
    const chk = visitor.includes(whoami.role) ? true : false;
    
    const { fname, lname, email, telephone1, role,
         identityID, identityType, fullName,
         telephone2, supplyArea, franchiseStates } = form;


    const onChange = (name, value) => setForm({...form, [name]: value})



    const onSubmit = async e => {
        e.preventDefault();
        
        adjustMany({ loading: true })
        const res = await tryCatchLinkUpToServer({
            method:'PUT', urlRoute: `user/${whoami.email}`,
            data: form
        });

        if(res.success){
            adjustMany({ Ponent: null, quickLink: false, loading: false });
            return cb && cb(true)
        }else adjustMany({ loading: false, errors: res.error, Ponent: null, quickLink: false })
    }

    const arr = (whoami.role === 'DISCO'? franchiseStates : supplyArea)



    return(
        <>
        <form onSubmit={onSubmit} style={{
                background: 'white',
                margin: '0 auto', width: '95%', padding: '1.3rem 1.2rem 2rem'
            }}>

            <p style={{
                fontSize: 20,
                marginBottom: 20,
            }}>User Information</p>
                
               {!dey.includes(whoami.role) && <div className="grid-2">
                   <InputIcon 
                        name={'fname'}
                        onChange={onChange}
                        value={fname}
                        title={'First Name'}
                        isRequired={chk ? true : false}
                   />

                   <InputIcon 
                        name={'lname'}
                        onChange={onChange}
                        value={lname}
                        title={'Last Name'}
                        isRequired={chk ? true : false}
                   />

                   
                </div>}

                <div className={!chk ? 'grid-2' : ''}>
                     <InputIcon 
                        name={'fullName'}
                        onChange={onChange}
                        value={fullName}
                        title={'Full Name'}
                        isRequired={true}
                        isEditable={!chk ? false : true}
                   />
                     {!chk && <InputIcon 
                        name={'abbrv'}
                        onChange={onChange}
                        value={form.abbrv}
                        title={'Company\'s Acronyms'}
                        isRequired={true}
                        isEditable={!chk ? false : true}
                   />}

                </div>

                <div className="grid-2">
                    <InputIcon 
                        name={'role'}
                        onChange={onChange}
                        value={role}
                        title={'role'}
                        // select={true}
                        // options={}
                   />
                   <InputIcon 
                        name={'email'}
                        onChange={onChange}
                        value={email}
                        title={'Email Address'}
                        typed={'email'}
                        isRequired={true}
                   />
                                      
                </div>
                
                <div className="grid-2">

                <InputIcon 
                        name={'telephone1'}
                        onChange={onChange}
                        value={telephone1}
                        title={'Telephone Number 1'}
                        isRequired={true}
                   />

                   <InputIcon 
                        name={'telephone2'}
                        onChange={onChange}
                        value={telephone2}
                        title={'Telephone Number 2'}
                   />
                                      
                </div>

                <div className='grid-2'>
                    <InputIcon 
                            name={'identityType'}
                            onChange={onChange}
                            value={identityType}
                            title={'Preferred Identity Type'}
                            select={true}
                            options={['International Passport', 'Driver Licence',
                            'Permanent Voters Card', 'National ID Card']}
                            isRequired={true}
                    />

                    <InputIcon 
                            name={'identityID'}
                            onChange={onChange}
                            value={identityID}
                            title={'Identity Number'}
                            isRequired={true}
                    />
                </div>

                
                {dey.includes(whoami.role) && <div className='flex'>
                    <InputIcon 
                            name={'em'}
                            onChange={onChange}
                            value={form.em}
                            title={whoami.role === 'DISCO' ?
                            'Select Franchise States': 'Select Supply Disco'}
                            select={true}
                            options={STATES.map(d=>d.state)}
                            isRequired={true}
                    />
                    <BTs title={'Add Item'} style={{ marginTop: 15, marginLeft: 10 }}
                        press={() => {
                            const RL = whoami.role === 'DISCO'? franchiseStates : supplyArea;
                            
                        if(form.em){
                                RL.push(form.em)                            
                            return setForm({...form, [
                                whoami.role === 'DISCO' ? 'franchiseStates' : 'supplyArea']: RL
                            })
                        }
                    }}
                    />
                </div>}

                {dey.includes(whoami.role) && <div style={{
                    ...styling.flexed({justify: 'flex-start'}),
                    padding: '0.8rem', border: '1px dotted #ccc',
                    marginTop: 10
                }}>
                    {arr && arr.map((d,i)=> <ItemAdds key={i} 
                        title={d} press={() => setForm({
                            ...form, [whoami.role === 'DISCO' ?
                             'franchiseStates' : 'supplyArea']: arr.filter(a=> a !== d )})
                        } />)}
                    
                </div>}

               <input 
                    type={'submit'} 
                    value={'Continue'} 
                    className={'btn'} 
                    style={{ marginTop: 20 }}
                />


        </form>
        </>
    )
}




function ItemAdds({ press, title }){
    return <div className='flex'>
        <i className='fas fa-times-circle' 
        onClick={press} style={{ 
            cursor: 'pointer',
            color: 'red', margin: '0 0.5rem'
        }}></i>
        <h4>{title}</h4>
    </div>
}


