import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import UseContext from '../../../../context/useContext';
import { styling } from '../../../../helpers/css/css';
import { InputIcon } from '../../../../helpers/FormInputs';
import { tryCatchLinkUpToServer } from '../../../../options/JSDOWNLOADS';
import { BTX as BTN } from '../../../cbs/pages/dashbaord/helpers/CBCard';
import MapSide from '../../MapSide';
import Structure from '../../Structure';

function GMr() {
  const { singleItem, adjustMany, href } = React.useContext(UseContext);
  const navigate = useNavigate();

  const [form, setForm] = React.useState({
    allocateAll: false,
    owner: '',
    opt: [],
    currentStore: '',
    piece: '',
    ed: false,
    recipient: '',
    destinationStore: '',
  });

  React.useEffect(() => {
    (async function () {
      if (!singleItem) return navigate(`/${href}/dashboard`);

      const res = await tryCatchLinkUpToServer({
        method: 'GET',
        urlRoute: 'user',
        data: { role: 'DISCO' },
        params: `search=selection&skip=ALLOCATE`,
      });

      if (res.success) {
        const np = [...new Set(res.data.map((a) => a.abbrv))];
        setForm({ ...form, opt: np });
      } else
        adjustMany({ errors: 'No registered DISCO found', loading: false });
    })();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const {
    allocateAll,
    piece,
    ed,
    owner,
    recipient,
    destinationStore,
    currentStore,
  } = form;
  const onChange = (name, value) => setForm({ ...form, [name]: value });

  return (
    <>
      <div
        style={{
          width: '85%',
          margin: '0 auto',
          background: 'white',
          border: '1px dotted #333',
          padding: '1rem',
          marginTop: '1rem',
        }}
      >
        <h3
          style={{
            padding: 10,
            width: '100%',
            fontSize: '1.5rem',
          }}
        >
          {singleItem ? singleItem.header : ''}
        </h3>
        <p
          style={{
            padding: 10,
            width: '100%',
            fontSize: '1.3rem',
          }}
        >
          Total number in this category:
          {singleItem ? singleItem.data : ''} Meter(s)
        </p>

        <div
          className={recipient === 'CUSTOMIZE' ? 'grid-3' : 'grid-2'}
          style={{
            background: '#f2f2f2',
            padding: '0.5rem',
          }}
        >
          <InputIcon
            title={'Recipient'}
            onChange={onChange}
            name={'recipient'}
            value={recipient}
            select={true}
            options={['DISCO']}
            style={{ borderRadius: 5, flex: 1, marginTop: 1 }}
          />

          <InputIcon
            title={recipient ? `Select ${recipient} Name` : 'Recipient Name'}
            onChange={onChange}
            name={'owner'}
            value={owner}
            select={true}
            options={form.opt}
            style={{ borderRadius: 5, flex: 1, marginTop: 1 }}
          />
        </div>

        <div
          className={'grid-2'}
          style={{
            background: '#f2f2f2',
            padding: '0.5rem',
          }}
        >
          <InputIcon
            title={'Current Store'}
            onChange={onChange}
            name={'currentStore'}
            value={singleItem ? singleItem.storeName : currentStore}
            style={{ borderRadius: 5, flex: 1, marginTop: 1 }}
            isEditable={true}
          />

          <InputIcon
            title={'Destination Store'}
            onChange={onChange}
            name={'destinationStore'}
            value={destinationStore}
            isRequired={true}
            options={form.opt}
            style={{ borderRadius: 5, flex: 1, marginTop: 1 }}
          />
        </div>

        <div
          style={{
            ...styling.flexed({}),
            background: '#f2f2f2',
            padding: '0.5rem',
            margin: '1rem 0',
          }}
        >
          <InputIcon
            title={'Enter number to be allocated'}
            onChange={onChange}
            name={'piece'}
            value={piece}
            style={{ borderRadius: 5, flex: 1, marginTop: 1 }}
            isEditable={ed}
            typed={'number'}
          />

          <div style={{ ...styling.flexed({ justify: 'center' }), flex: 1 }}>
            <input
              type={'checkbox'}
              name={'allocateAll'}
              onChange={() => {
                if (piece !== '' || !allocateAll) {
                  setForm({ ...form, allocateAll: true, piece: '', ed: true });
                } else if (allocateAll) {
                  setForm({ ...form, allocateAll: false, ed: false });
                } else onChange('allocateAll', !allocateAll);
              }}
            />
            <p>Allocate all</p>
          </div>
        </div>

        <BTN
          onPress={() =>
            adjustMany({
              showAlerts: true,
              cancel: () =>
                adjustMany({ showAlerts: false, error: null, success: null }),
              proceed: async () => {
                const num = allocateAll ? singleItem.data : piece;
                adjustMany({ loading: true });
                const res = await tryCatchLinkUpToServer({
                  method: 'PUT',
                  urlRoute: allocateAll
                    ? `map/${singleItem.id}`
                    : `map/${singleItem.id}`,
                  params: `name=${singleItem.name}&limit=${num}&search=ALLOCATE`,
                  data: {
                    allocatedTo: owner,
                    piece: num,
                    allocationStatus: 'Allocated',
                    destinationStore,
                    currentStore,
                  },
                });

                if (res.success) {
                  adjustMany({ loading: false, showAlerts: false });
                  return navigate(`/${href}/allocation`);
                } else {
                  adjustMany({
                    loading: false,
                    errors: res.error,
                    showAlerts: false,
                  });

                  return navigate(`/${href}/allocation`);
                }
              },
              success: 'Are you sure you want to continue?',
            })
          }
          title={
            owner !== '' && destinationStore !== '' && (piece || allocateAll)
              ? 'Proceed'
              : 'Processing...'
          }
          style={{
            pointerEvents:
              owner !== '' && destinationStore !== '' && (piece || allocateAll)
                ? 'visible'
                : 'none',
          }}
        />
      </div>
    </>
  );
}

export default function MapAloPage() {
  return (
    <>
      <Structure Side={MapSide} BodyPage={GMr} />
    </>
  );
}
