import * as React from 'react';
import {BrowserRouter as Router, Routes, Route} from "react-router-dom";
import UseContext from '../../context/useContext';
import MapSide from './MapSide';
import MapAllocations from './pages/allocation/MapAllocation';
import MapAloPage from './pages/allocation/MapAloPage';
import ScheduleMeter from './pages/allocation/SheduleMeter';
import ShowAllocationActivities from './pages/allocation/ShowAllocationActivities';
import MapCreateCSV from './pages/csvUploads/MapCreateCSV';
import MapDashboard from './pages/dashbaord/MapDashoard';
import Structure from './Structure';
// import CreateUserMap from '../pages/map/CreateInstallerUser';




export default function Maps() {
    const { href } = React.useContext(UseContext);


    const [url, setUrl] = React.useState(null);

    React.useEffect(() => {
      (function(){
        if(href){
          setUrl(href)
        }

      })()
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    
 

  const DG = () => {
      return <Structure 
            BodyPage={function(){
            return<div>
              {/* <h1>Finally</h1> */}
            </div>
          }}
          Side={MapSide}
      />
  }

  


  return (
    <>
    <Router>
        <Routes>
            <Route path={`${url}`} element={<DG />} />
            <Route path={`/${url}/dashboard`} element={<MapDashboard />} />
            <Route path={`/${url}/allocation`} element={<ScheduleMeter  />}/>
            <Route path={`/${url}/create-record`} element={<MapCreateCSV />} />
            <Route path={`/${url}/store`} element={<MapAllocations />} />
            <Route path={`/${url}/confirm-allocation`} element={<MapAloPage />} />
            <Route path={`/${url}/previous-allocation`} element={<ShowAllocationActivities />} />
            {/* <Route path={`/${url}/create-installer`} element={<CreateUserMap />} /> */}
          </Routes>
    </Router>
  </>
  );
}


