import React from 'react';
import { COLORS } from '../../../../../helpers/colors';
import UseContext from '../../../../../context/useContext';
import { useNavigate } from 'react-router-dom';
import { CBCard } from './CBCard';

function ThirdRow({ DATA }) {
  const { difMap, difDisco, difPeople } = DATA || {};
  const { adjustMany, href } = React.useContext(UseContext);
  const navigate = useNavigate();

  return (
    <div className="grid-3" style={{ marginBottom: '1.5rem' }}>
      <CBCard
        title={'Unallocated Meters'}
        icon={'fas fa-list'}
        right={true}
        info={difMap || 0}
        txtColor={'white'}
        iconSize={'1.5rem'}
        color={COLORS._primary}
      />

      <CBCard
        title={'Meters on Transit'}
        info={difPeople || 0}
        txtColor={'white'}
        icon={'fas fa-users'}
        right={true}
        color={COLORS._danger}
        iconSize={'1.5rem'}
        onPress={() => {
          adjustMany({ nav: 'MAP-VIEW' });
          return navigate(`/${href}/view-meter`);
        }}
      />

      <CBCard
        title={'Meter Installed by Installer/DISCOs'}
        right={true}
        color={COLORS._teal}
        icon={'fas fa-lightbulb'}
        info={difDisco || 0}
        iconSize={'1.5rem'}
        txtColor={'white'}
      />
    </div>
  );
}

export default ThirdRow;
