import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import UseContext from '../../../../context/useContext';
import { tryCatchLinkUpToServer } from '../../../../options/JSDOWNLOADS';
import CBSide from '../../CBSide';
import Structure from '../../Structure';






function ACT(){
    const { adjustMany, href, justItem } = React.useContext(UseContext);
    const [data, setData] = React.useState(null)
    const navigate = useNavigate()
    
    React.useEffect(() => {
        (async function(){
            adjustMany({ loading: true });
            const res = justItem || await tryCatchLinkUpToServer({
                method: 'GET', urlRoute: 'user', params: 'search=VIEW&skip=APPLICANT'
            });

            if(res.data){
               
                setData(res.data)
                adjustMany({ loading: false, justItem: res })
            }else adjustMany({ loading: false, errors: res.error})
        })()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <>
        

        <div>

            <p style={{ fontSize: '1.5rem', margin: '10px 0'}}>
                    List of Meter Applicant(s)
                </p>

            <table className='tables'>
                <thead>
                    <tr>
                        <th>Disco Name </th>
                        <th>Total Meter Request</th>
                        <th>Registered Status</th>
                        <th>Total Being Processed</th>
                        <th>Total Request Cancelled</th>
                        <th>No. of allocation</th>
                        <th></th>
                       
                    </tr>
                </thead>
            <tbody>
                {data && data.map((d, i) => {
                    const { registered, isCancelled, total, 
                        allocation, disco, beingProcessed } = d;
                    
                    

                    return <tr key={i}>
                        <td>{disco||0}</td>
                        <td>{total||0}</td>
                        <td>{registered||0}</td>
                        <td>{beingProcessed||0}</td>
                        <td>{isCancelled||0}</td>
                        <td>{allocation||0}</td>
                        <td>
                        <button className='btx'
                            onClick={() => {
                                adjustMany({ singleItem: disco })
                                return navigate(`/${href}/applicant-info`)
                            }}>
                            More..
                            </button>
                        </td>
                    </tr>
                })}

            </tbody>

            </table>

        </div>
            
        
        </>
    )
}





export default function ApplicantionDiscos(){
    return <Structure 
        Side={CBSide}
        BodyPage={ACT}
    />
}