import React, { useState, useEffect } from 'react'
import UseContext from '../../context/useContext';
import { pickEmitter } from '../../helpers/essential';
import { navigate, tryCatchLinkUpToServer } from '../../options/JSDOWNLOADS';
import { SETAUTH } from '../../util/SetAuth';


function RightDropDown() {
    const [toogle, setToogle] = useState(false)
    const [notification, setNotification] = useState(false);
    const [notPosition, setNotPosition] = useState(null);
    const { adjustMany, user } = React.useContext(UseContext);
    
    
    // const getPosition = e => console.log(e)

    useEffect(() => {
        (function(){
            const { clientWidth } = pickEmitter('notify');
            setNotPosition(clientWidth*2)
            
        })()
    }, [])

    

    const logout = async () => {
        adjustMany({ loading: true })
        const res = await tryCatchLinkUpToServer({ 
            method: 'GET', urlRoute: 'user/logout'
        });
        SETAUTH().clearToken()
        adjustMany({ loading: false })
        if(res){
            return window.location.reload()
        }
       return navigate('/')
    }
    
return (
    <div className='right-nav-panel flex-side'>
        <i className='fas fa-moon panel-icon please-hide' />
        <i className='fas fa-expand panel-icon please-hide' onClick={(e) => {
               if (!document.fullscreenElement) {
                    document.documentElement.requestFullscreen();
                } else {
                if (document.exitFullscreen) {
                    document.exitFullscreen();
                }
                }
        }} />
       
       <div>
            <i 
                className={`far fa-bell panel-icon ${ notification && 'blink'}`} 
                onClick={() => setNotification(!notification)}
                id={'notify'}
            />
            {notification && <Notification title={'Notification'} pos={notPosition}/> }
       </div>

        <div>
            <Avatar onPress={() => setToogle(!toogle)} />
            {toogle && <AlertBox onPress={logout} user={user} /> }
        </div>
        
    </div>
  )
}

export default RightDropDown



function Avatar({color, img, onPress }){

    return<>
        <div 
            className='avatar' 
            style={{ background: color || 'white' }}
            onClick={onPress}
        >
            {img ? <img src={img} alt={'Avatar'} /> : <h3>AV</h3>}
        </div>

    </>
    
}

function AlertBox({ onPress, user }){
    const { fullName, role } = user || {};
    return <>
        <div className='nav-alert-box mt-05'>
            <div className='user-names'>
                <h3>{fullName || ''}</h3>
                <p>Role: {role || ''}</p>
            </div>
            <p className='sign'
                onClick={onPress}
            >
                <i className='fa fa-sign-out sign-out' />
                Sign Out
            </p>
    </div>
    </>
}


function Notification({ title, pos }){
    return<div className="nav-alert-info mt-05" style={{ right: pos || 0  }}>
        <div className='info-h'>
            <h3>{title || ''}</h3>
        </div>
        
        <InfoCard 
            title={'New Allocation Received'} 
            info={'3 days ago'}
            icon={'fas fa-envelope'}
            color={'royalblue'}
        />
    </div>
}


function InfoCard({ title, info, icon, color, iconColor }){

    return <div className='info-card flex-start'>
        <div 
            style={{ 
                background: color || '', 
                padding: '0.7rem 0.8rem', 
                borderRadius: '50%',
                boxShadow: 'rgba(149, 157, 165, 0.2) 0px 8px 24px',
                marginRight: '1rem'
            }}>

            <i className={icon || ''}  style={{ color: iconColor || 'white' }}/>

        </div>

        <div>
            <h4>{title || ''}</h4>
            <p>{info || ''}</p>
        </div>
        
    </div>
}