import React from 'react';
import { useNavigate } from 'react-router-dom';
import UseContext from '../../../../../context/useContext';
import { COLORS } from '../../../../../helpers/colors';
import { CBCard } from './CBCard';

function FirstRow({ DATA }) {
  const { map_, disco, people } = DATA || {};
  const { adjustMany, href } = React.useContext(UseContext);
  const navigate = useNavigate();

  const onPress = () => {
    adjustMany({ nav: 'APPLICANT-VIEW', Ponent: null, quickLink: false });
    return navigate(`/${href}/applicant`);
  };

  const onclick = () => {
    adjustMany({ Ponent: null, quickLink: false });
    return navigate(`/${href}/google`);
  };

  return (
    <div className="grid-3" style={{ marginBottom: '1.5rem' }}>
      <CBCard
        title={'No. of Registered Meter Provider'}
        icon={'fas fa-list'}
        left={true}
        info={map_ || 0}
        txtColor={'#333'}
        iconBg={COLORS._primary_300}
        onPress={() => {
          adjustMany({ nav: 'MAP-VIEW' });
          return navigate(`/${href}/active-view`);
        }}
      />
      <CBCard
        title={'No. of Registered DISCO'}
        left={true}
        iconBg={COLORS._primary_400}
        icon={'fas fa-lightbulb'}
        info={disco || 0}
        txtColor={'#333'}
        onPress={() => {
          adjustMany({ nav: 'DISCO-VIEW' });
          return navigate(`/${href}/active-view`);
        }}
      />
      <CBCard
        title={'No. of Registered Meter Request'}
        info={people || 0}
        txtColor={'#333'}
        icon={'fas fa-users'}
        left={true}
        iconBg={COLORS._primary_700}
        //  onPress={() => adjustMany({ Ponent: () => Appear({ onPress, onclick }), quickLink: true })}
        onPress={() => adjustMany({ errors: 'Google map is deactivated' })}
      />
    </div>
  );
}

function Appear({ onPress, onclick }) {
  return (
    <>
      {/* <h3 style={{ textAlign: 'center' }}>Notification Box</h3> */}
      <div className="flex" style={{ padding: '2rem' }}>
        <button
          className="btx"
          onClick={onPress}
          style={{
            padding: '2rem',
            margin: '0 10px',
          }}
        >
          View upto 50 Meter Request Details only
        </button>

        {/* <button className='btx' onClick={onclick} style={{ padding: '2rem' , margin: '0 10px'}}>
                  View Meter Request Locations on Google
              </button> */}
      </div>
    </>
  );
}

export default FirstRow;
