import * as React from 'react';
import UseContext from '../context/useContext';
import { navigate, tryCatchLinkUpToServer } from '../options/JSDOWNLOADS';
import Admins from '../users/admin/page/Admins';
import CBs from '../users/cbs/CBs';
import Discos from '../users/disco/Disco';
import Maps from '../users/map/Maps';
import { SETAUTH } from '../util/SetAuth';

export default function PageLoadRender(props) {
  const { adjustMany } = React.useContext(UseContext);
  const [loadUser, setLoadUser] = React.useState(null);

  React.useEffect(() => {
    (async function () {
      const res = await tryCatchLinkUpToServer({
        method: 'GET',
        urlRoute: 'user?search=DECODE',
        token: SETAUTH().decode(),
      });

      if (res.success) {
        setLoadUser(res.data);
        adjustMany({ user: res.data, href: `${window.btoa(res.data._id)}` });
        return navigate(`${window.btoa(res.data._id)}`);
      } else adjustMany({ errors: res.error, loading: false });
    })();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (!loadUser) return <div></div>;

  const TAG = ['CBN', 'NERC'];

  const DG = () => {
    return <></>;
  };

  console.log(loadUser);

  if (loadUser.role === 'DISCO') return <Discos />;
  else if (loadUser.role === 'MAP') return <Maps />;
  else if (TAG.includes(loadUser.role)) return <CBs />;
  else if (loadUser.role === 'ADMIN') return <Admins />;
  //    else if(loadUser.role === 'GUEST') return <Guest />
  //    else if(loadUser.role === 'INSTALLER') return <Installer />
  else return <DG />;
}
