import { Line, Polar, Bar, HorizontalBar, Doughnut, Pie } from 'react-chartjs-2';
import { BACKGROUND, BORDERCOLORS } from './GraphUtils';



export const LineChart = ({ BGs, BOs, bg, style,
     DATA, LABELS, LABEL, OPT, HEIGHT, WIDTH }) => {
    

    return(
       <div style={{ background: bg || 'transparent',}}>
           <Line
                height={HEIGHT || 600}
                width={WIDTH || 300}
                data={{
                    labels: LABELS,
                    datasets: [{
                        label: LABEL,
                        data: DATA,
                        backgroundColor: BACKGROUND.slice(0,DATA.length),
                        borderColor: BORDERCOLORS.slice(0,DATA.length),
                        borderWidth: 1,
                        borderSkipped: false,
                        ...style,
                    }],
                }}
                options={{
                    maintainAspectRatio: false,
                    scales: {
                        yAxes: [{
                            ticks: { beginAtZero: true },
                        }]
                    },
                    legend: { display: false, labels: true,  },
                    ...OPT,
                }}

           />
       </div>
    )
}


