import React from 'react'
import { COLORS } from '../../../../../helpers/colors'
import { CBCard } from '../../../../cbs/pages/dashbaord/helpers/CBCard'



function SecondRow({ DATA }) {
  const { map_, disco, people } = DATA || {}


  return (
    <div className='grid-3' style={{ marginBottom: '1.5rem'}}>
        <CBCard 
            title={'Meter Request Received'}
            icon={'fas fa-list'}
            right={true}
            info={map_ || 0 }
            txtColor={'white'}
            iconSize={'1.5rem'}
            color={COLORS._primary}
        />
        <CBCard 
             title={'Total Allocation'}
             right={true}
             color={COLORS._teal}
             icon={'fas fa-lightbulb'}
             info={disco || 0}
             iconSize={'1.5rem'}
             txtColor={'white'}
        />
        <CBCard
             title={'Total Installation'}
             info={people || 0}
             txtColor={'white'}
             icon={'fas fa-users'}
             right={true}
             color={COLORS._yellow}
             iconSize={'1.5rem'}
             
        />
    </div>
  )
}

export default SecondRow