import * as React from 'react';
import UseContext from '../../../../../context/useContext';
import { csvDownload, tryCatchLinkUpToServer } from '../../../../../options/JSDOWNLOADS';
import CBSide from '../../../CBSide';
import Structure from '../../../Structure';






 function GMr() {

    const [data, setData] = React.useState(null);
    const { adjustMany, updateSingle } = React.useContext(UseContext);



    React.useEffect(() => {
        (async function(){
            adjustMany({ loading: true, active: 'track' })
            const res = updateSingle || await tryCatchLinkUpToServer({
                method: 'GET', urlRoute: `user?skip=EXCEPTION&search=VIEW`,
            });

            
            if(res.success){
                console.log(res)
                adjustMany({ loading: false, updateSingle: res })
                setData(res.data)
                
                
            }else {
                adjustMany({ loading: false, errors: res.error })
            }

        })()

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])



   return (
            <>
          {(data) && <div style={{ 
              margin: '0 auto', 
              width: '90%',
              background: 'white', 
              border: '1px solid #ccc',
              padding: '1rem', lineHeight: '1.8'
            }}>

                <div className='flex-side'>
                    <h1>Exception Report</h1>
                    {/* <button className='btn' 
                        style={{ padding: '0.7rem'}}
                        onClick={() => pickEmitter('dg').click()}
                    > Go back to Store</button> */}
                </div>
                
                


                <table className='tables'>
                        <thead>
                            <tr>
                                <th>#</th>
                                <th>Meter From</th>
                                <th>Meter To</th>
                                <th>Count</th>
                                <th>Status</th>
                                <th>Commentary</th>
                                <th></th>
                            </tr>
                    </thead>
                    
                {data && data.map((ae, i) => {
                    
                            const { uploaderName, recipientName, pop } = ae;

                    return <tbody key={i}>
                            <tr>
                                <td>{i+1}</td>
                                <td>{uploaderName || ''}</td>
                                <td>{recipientName || ''}</td>
                                <td>{pop}</td>
                                <td style={{ color: 'red', fontWeight: 'bold'}}>Failed</td>
                                <td>{`Allocation decrepancy between: ${uploaderName} to ${recipientName}`}</td>
                                <td>
                                <button className='btx'
                                    onClick={async () => {
                                        const res = await tryCatchLinkUpToServer({
                                            method: 'GET', 
                                            urlRoute: `order/${recipientName}`,
                                            params: `name=recipientName`,
                                        });      
                                        
                                        if(res.success){
                                           adjustMany({loading: false});
                                            csvDownload({
                                                data: res.data,
                                                structure: (row) => {
                                                    const { orderID, orderStatus, 
                                                        createdAt, orderType, 
                                                        recipientName, comments,
                                                        meterOwnerName
                                                    } = row;
                                                    return {
                                                        orderID, orderStatus, 
                                                        createdAt, orderType, 
                                                        recipientName, 
                                                        meterOwnerName,
                                                        comments,
                                                        count: 1
                                                    }
                                                },
                                                headings: [
                                                    'Exception ID',
                                                    "Status",
                                                    "Creation Date",
                                                    "Type",
                                                    "Sender",
                                                    "Recipient",
                                                    "Comments",
                                                    "Count"
                                                ],
                                                title: `Exception`
                                            })
                                        }
                                    }}>
                                    Download
                                </button>
                                </td> 
                               
                            </tr>
                        </tbody>
                })}
                </table>
                

            </div>}

            
            </>
        )
    }
        

export default function ExemptReport(){
    

   

    return (
        <>
            <Structure 
                Side={CBSide}
                BodyPage={GMr}
            />
        </>
    )
}