


export const BTs = ({title, press, style}) => {

    return (
        <button className="btx" style={style} onClick={press} type="button">
            {title}
        </button>
    )
}

export const AskMeQuestion = ({ Press1, Press2, title1, title2 }) => {

    return(
        <div style={{ background: 'white', padding: '1rem'}}>
            {/* <h3 style={{ textAlign: 'center' }}>Notification Box</h3> */}
            <div className='flex' style={{ padding: '2rem'}}>
                <button className='btx' onClick={Press1} style={{ 
                    padding: '2rem', margin: '0 10px'
                 }}>
                    {title1}
                </button>

                <button className='btx' onClick={Press2} style={{ padding: '2rem' , margin: '0 10px'}}>
                    {title2}
                </button>

            </div>
        </div>
    )
}




export const GBox = ({ size, title, icon, infoIcon, perf,
    value, comment, onPress, error, touchable, bg }) => {

   // const b = BORDERCOLORS.slice()

   return<div 
       className='gbx'
       style={{ width: size || '50%', cursor: 'pointer', background: bg || 'auto'}} 
       onClick={onPress}
   >

       <div className='flex-side'>
           <h3>{title || ''}</h3>
           <i className={icon || 'fas fa-info-circle'} style={{ 
               color: '#ccc'
           }} onClick={touchable}></i>
       </div>

       <h1 style={{ fontSize: '1.8rem', padding: '0.8rem 0'}}>{value || '0'}</h1>

       <div>
          <h4 style={{ color: error ? 'red' : 'green' }} >
              <span> <i className={infoIcon || ''}
               style={{ color: error ? 'red' : 'green', marginRight: 5 }}
               ></i>
           </span>
           {perf}
          </h4>
           <p style={{ color: 'grey'}}>{comment}</p>
       </div>
   </div>
}