import Card from "../../../../../helpers/card/Card"
import CardIcon from "../../../../../helpers/card/CardIcon"
import Cardio from "../../../../../helpers/card/Cardio"
import { COLORS } from "../../../../../helpers/colors"


export const CBCard = ({ iconBg, color, title, info, icon, iconColor, iconSize, txtColor, left, right, onPress  }) => {

    const Cs = () => {

        return <div 
                className="flex-side bns" onClick={onPress}
                style={{ padding: '.5rem', lineHeight: 1.6, width: '100%', cursor:'pointer'}}>

                 {left && <CardIcon color={iconBg} icon={icon} iconColor={iconColor}/>}

                {left ? 
                    <div style={{ color: txtColor || 'white'}}>
                        <h4>{title}</h4>
                        <h1>{info}</h1>
                    </div>
                
                : <div style={{ color: txtColor || 'white'}}>
                    <h1>{info}</h1>
                    <h4>{title}</h4>
                </div>}

               {right && <i 
                    className={icon || ''}  
                    style={{ 
                        fontSize: iconSize || '1.2rem',
                        color: iconColor || 'white',
                        textAlign: 'right'
                    }}/>}
        </div>
    }

    return <Card 
        Ponent={Cs} 
        color={color}
    />
}



export const BTX = ({ onPress, bg, title, icon, style }) => {


    return <button 
        className="btx flex-middle" 
        style={{ backgroundColor: bg || '', ...style }}
        onClick={onPress}
    >
        {icon && <i className={icon || ''} style={{ marginRight: '.5rem'}} />}
        {title || ''}
    </button>
}


export const BTN = ({ onPress, bg, title, icon, style }) => {


    return <button 
        className="btn flex-middle" 
        style={{ backgroundColor: bg || '', ...style }}
        onClick={onPress}
    >
        {icon && <i className={icon || ''} style={{ marginRight: '.5rem'}} />}
        {title || ''}
    </button>
}


export const BigCard = ({ bg, iconBg, title, info, icon, iconColor, iconSize, txtColor }) => {

    return<div className="big-Card flex-middle" style={{ 
            padding: '1.5rem', 
            background: bg || 'white', 
            boxShadow: COLORS._shadow_2,
            flexDirection: 'column',
            borderRadius:'.5rem',
            paddingBottom: '2rem'
        }}>
        <Cardio
            bg={iconBg}
            icon={icon}
            iconColor={iconColor}
            iconSize={iconSize}
        />

      
            <h1 style={{ color: txtColor || '#333'}}>{title || ''}</h1>
            <h4 style={{ color: txtColor || '#333'}}>{info || ''}</h4>

        
    </div>
}