import * as React from 'react';
import {BrowserRouter as Router, Routes, Route} from "react-router-dom";
import UseContext from '../../../context/useContext';
import { tryCatchLinkUpToServer } from '../../../options/JSDOWNLOADS';
import AddProject from './AddProject';
import AdminDashboard from './AdminDashboard';
import CreateUserAdmin from './CreatUserAdmin';
// import UseContext from '../context/useContext';
// import AlertConfig from '../helpers/alerts/AlertConfig';
// import AppHeader from '../layouts/headers/AppHeader';
// import AdminDashboard from '../admin/page/AdminDashboard';
// import AddProject from '../admin/page/AddProject';
// import { tryCatchLinkUpToServer } from '../options/JSDOWNLOADS';
// import CreateUserAdmin from '../admin/page/CreatUserAdmin';






export default function Admins() {
  const { href, adjustMany } = React.useContext(UseContext);
 
  const [url, setUrl] = React.useState(null);

  React.useEffect(() => {
    (function(){
      
      if(href){
        setUrl(href)
      }

      adjustMany({ filterClick: async (log) => {
          adjustMany({ loading: true })
          const res = await tryCatchLinkUpToServer({
            method: 'GET', urlRoute: `user/${log}`
          });

          if(res.success){
            adjustMany({ loading: false})
          }else adjustMany({ loading: false, errors: res.error})
      }})

    })()
    
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])



  return (
    <>
       
        <Router>
            <Routes>
                <Route path={`${url}`} element={<AdminDashboard />} />
                <Route path={`${url}/add-project`} element={<AddProject />} />
                <Route path={`${url}/create-user`} element={<CreateUserAdmin />} />
            </Routes>
        </Router>
    </>
  );
}


