import React from 'react'
import { COLORS } from '../../../../../helpers/colors'
import { BigCard } from '../../../../cbs/pages/dashbaord/helpers/CBCard'



function FirstRow({ DATA }) {
    const { map_, disco, people } = DATA || {}

  return (
    <div className='grid-3' style={{ marginBottom: '1.5rem'}}>
        <BigCard 
            info={'Total Meter Received'}
            icon={'fas fa-list'}
            left={true}
            title={map_ || 0}
            txtColor={'white'}
            iconBg={COLORS._dark_primary_hover}
            bg={COLORS._darkprimary_transparentcolor}
        />
        <BigCard 
             info={'1-Phase Received'}
             left={true}
             iconBg={COLORS._cyan}
             icon={'fas fa-lightbulb'}
             title={disco || 0}
             txtColor={'white'}
             bg={COLORS._cyan}
        />
        <BigCard
             info={'3-Phase Received'}
             title={people || '0' }
             txtColor={'white'}
             icon={'fas fa-users'}
             left={true}
             iconBg={COLORS._primary_700}
             bg={COLORS._indigo}
        />
    </div>
  )
}

export default FirstRow