import React from 'react';
import UseReducer from './useReducer';
import UseContext from './useContext';
import { CLEARITEM, CLEARMANY, LINKTOSERVER, LINKTOSERVERWITHERROR, 
    LOCALMOVEMENT, RESTOREALL } from './types';
import axios from 'axios';



export default function UseState({ children }){
    const url = 'https://qu-essence.com/api/v1'
    const initialState = {
        loading: false,
        user: null,
        items: null,
        disco: null,
        mapped: null,
        singleItem: null,
        isAuthenticated: false,
        updateRelated: null,
        updateSingle: null,
        showAlerts: null,
        token: null,
        filter: null,
        filterClick: null,
        localMovement: null,
        errors: null,
        success: null,
        skip: false,
        special: null,
        nav: null,
        quickLink: false,
        dark: null,
        appTitles: null,
        cancel: null,
        proceed: null,
        active: null,
        current: null,
        href: null,
        Ponent: null,
        keepItem: null,
        justItem: null,
        winksItem: null
    };

    const [state, dispatch] = React.useReducer(UseReducer, initialState);

    // Function Decleration;
    const saveAndContinue = (data) => {
        delete data['name']
        delete data['isModel']
        delete data['arrayItem']
        delete data['original']

        dispatch({ type: LOCALMOVEMENT, payload: data })
    }
    const restoreAll = () => dispatch({ type: RESTOREALL, payload: {} })
    const adjustOne = (name, value) => dispatch({ type: CLEARITEM, payload: { name, value } });
    const adjustMany = (value) => dispatch({ type: CLEARMANY, payload: value });

    const linkToServer = async ({ method, urlRoute, data,
         params, apiKey, search, isImage }) => {
        // REMOVING HELPERS
        delete data['name']
        delete data['isModel']
        delete data['arrayItem']
        delete data['original']

        // ENABLE LOADING
        adjustOne('loading', true)
        
        // ACTION
        const withParams = params ? `${urlRoute}/${params}`: urlRoute;
        const headers = { 
            'Content-Type': 'application/json',
            ...(apiKey ? {authorization: `Bearer ${apiKey}`} : '') 
        };

        if(method === 'GET' && !isImage){
            await axios.get(`${url}/${withParams}`, { headers })
            .then(response => dispatch({ 
                type: LINKTOSERVER,
                payload: { 
                    name: search === 'multiple' ? 'items' : 'singleItem',
                    value: response.data.data
                } 
             }))
             .catch(error => dispatch({ 
                 type: LINKTOSERVERWITHERROR, 
                 payload: error.response.data.error 
            }));
        }

        if(method === 'POST' && !isImage){
        
            await axios.post(`${url}/${withParams}`, data , { headers })
            .then(response => dispatch({ 
                type: LINKTOSERVER,
                payload: { 
                    name: search === 'multiple' ? 'items' : 'singleItem',
                    value: response.data.data
                } 
             }))
             .catch(error => dispatch({ 
                 type: LINKTOSERVERWITHERROR, 
                 payload: error.response.data.error 
            }));;
        }

        if(method === 'PUT' && !isImage){
            await axios.put(`${url}/${withParams}`, data, { headers })
            .then(response => dispatch({ 
                type: LINKTOSERVER,
                payload: { 
                    name: search === 'multiple' ? 'items' : 'singleItem',
                    value: response.data.data
                } 
             }))
             .catch(error => dispatch({ 
                 type: LINKTOSERVERWITHERROR, 
                 payload: error.response.data.error 
            }));;
        }

        if(method === 'PUT' && isImage){
            const localuri = data.uri
            let filename = localuri.split("/").pop();
            let match = /\.(\w+)$/.exec(filename);
            let type = match ? `image/${match[1]}` : "image";

            const formData = new FormData()
           formData.append( 'file', { uri: localuri, name: filename, type})

            await axios.post(`${url}/${withParams}`, formData, { 
                headers: { "Content-Type": "multipart/form-data"}
            })
            .then(response => dispatch({ 
                type: LINKTOSERVER,
                payload: { 
                    name: search === 'multiple' ? 'items' : 'singleItem',
                    value: response.data.data
                } 
             }))
             .catch(error => dispatch({ 
                 type: LINKTOSERVERWITHERROR, 
                 payload: error.response.data.error 
            }));;
        }
    }
    

    // Dispatch to User;

    return <UseContext.Provider
        value={{
            loading: state.loading,
            user: state.user,
            items: state.items,
            singleItem: state.singleItem,
            isAuthenticated: state.isAuthenticated,
            updateRelated: state.updateRelated,
            updateSingle: state.updateSingle,
            token: state.token,
            localMovement: state.localMovement,
            errors: state.errors,
            success: state.success,
            showAlerts: state.showAlerts,
            skip: state.skip,
            special: state.special,
            nav: state.nav,
            mapped: state.mapped,
            disco: state.disco,
            dark: state.dark,
            quickLink: state.quickLink,
            appTitles: state.appTitles,
            cancel: state.cancel,
            proceed: state.proceed,
            active: state.active,
            current: state.current,
            href: state.href,
            keepItem: state.keepItem,
            justItem: state.justItem,
            winksItem: state.winksItem,
            Ponent: state.Ponent,
            filter: state.filter,
            filterClick: state.filterClick,
            adjustOne, saveAndContinue, restoreAll,
            linkToServer, adjustMany
        }}
    >
        { children }
    </UseContext.Provider>

    
}