import * as React from 'react';
import {BrowserRouter as Router, Routes, Route} from "react-router-dom";
import UseContext from '../../context/useContext';
import DiscoDashboard from '../disco/pages/dashbaord/DiscoDashoard';
import ShowAllocationActivities from '../map/pages/allocation/ShowAllocationActivities';
import MapDashboard from '../map/pages/dashbaord/MapDashoard';
import CBGenerateReport from './pages/csvReport/CBGenerateReport';
import MainPage from './pages/dashbaord/MainPage';
import ExceptionReport from './pages/exception/Exception';
import GoogleMapLoader from './pages/googleRep/GoogleMapLoader';
import GeneratTable from './pages/otherpage/ActiveUser';
import ApplicantionDiscos from './pages/otherpage/ApplicationDiscos';
import Applicant from './pages/otherpage/ApplicatsInfo';
import MeterInformation from './pages/otherpage/MeterInformation';
import ExemptReport from './pages/otherpage/tabl/Exempt';
import TransitReport from './pages/otherpage/tabl/Transit';







export default function CBs() {
    const { href } = React.useContext(UseContext);


    const [url, setUrl] = React.useState(null);

    React.useEffect(() => {
      (function(){
        if(href){
          setUrl(href)
        }

      })()
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    
 

  


  return (
    <>

    <Router>
        <Routes>
            <Route path={`${url}`} element={ <MainPage />} />
            <Route path={`${url}/active-view`} element={<GeneratTable />} />
            <Route path={`${url}/view-meter`} element={<MeterInformation />} />
            <Route path={`${url}/applicant`} element={<ApplicantionDiscos />} />
            <Route path={`${url}/applicant-info`} element={<Applicant />} />
            <Route path={`${url}/view-summary`} element={<ShowAllocationActivities />} />
            <Route path={`${url}/exception`} element={<ExceptionReport />} />
            <Route path={`${url}/exempt`} element={<ExemptReport />} />
            <Route path={`${url}/transit`} element={<TransitReport />} />
            <Route path={`${url}/report`} element={<CBGenerateReport />} />
            <Route path={`${url}/disco`} element={<DiscoDashboard />} />
            <Route path={`${url}/map`} element={<MapDashboard />} />
            <Route path={`${url}/google`} element={<GoogleMapLoader />} />
          </Routes>
    </Router>
  </>
  );
}


