import * as React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import UseContext from '../../../../../context/useContext';
import DiscoSide from '../../../DiscoSide';
import Structure from '../../../Structure';
import { AddressInfo } from './AddressInfo';
import { PersonalInfo } from './PersonalInfo';
import { PropertyImageA } from './PropertyImageA';
import { PropertyInfo } from './PropertyInfo';
import { UploadSavedData } from './UploadSavedData';

function Apcn() {
  const { skip, adjustMany, href, singleItem } = React.useContext(UseContext);
  const [form, setForm] = React.useState(null);
  const navigate = useNavigate();

  React.useEffect(() => {
    (async function () {
      adjustMany({ loading: true });
      const { data, success, error } = singleItem || {};

      if (success && data.length > 0) {
        setForm(data[0] || {});
        adjustMany({ loading: false, skip: 'personal' });
      } else {
        console.log(error);
        return navigate(`/${href}`);
      }
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onChange = (name, value) => setForm(name, value);

  return (
    <>
      <Link
        to={`/${href}/apply`}
        style={{ visibility: 'hidden' }}
        id="confirm"
      ></Link>
      <Link to={`/${href}`} style={{ visibility: 'hidden' }} id="gohome"></Link>
      {skip === 'personal' && form && (
        <PersonalInfo onChange={onChange} form={form} />
      )}
      {skip === 'address' && form && (
        <AddressInfo onChange={onChange} form={form} />
      )}
      {skip === 'property' && form && (
        <PropertyInfo onChange={onChange} form={form} />
      )}
      {skip === 'image' && form && (
        <PropertyImageA onChange={onChange} form={form} />
      )}
      {skip === 'send' && form && (
        <UploadSavedData onChange={onChange} form={form} />
      )}
    </>
  );
}

export default function ViewCustomerProfile() {
  return <Structure Side={DiscoSide} BodyPage={Apcn} />;
}
