import * as React from 'react';
import { Link } from 'react-router-dom';
import UseContext from '../context/useContext';
import { pickEmitter } from '../options/JSDOWNLOADS';
import { styling } from './css/css';
import { INPUTs } from './FormInputs';

export const IconWithName = ({
  icon,
  onPress,
  title,
  styles,
  name,
  iconStyle,
  titleStyles,
  cls,
  align,
  narrow,
  pd,
  href,
  disconnect,
}) => {
  const { active, adjustMany } = React.useContext(UseContext);

  return (
    <div
      style={{
        ...styling.flexed({ justify: align || 'center', gap: '5px' }),
        cursor: 'pointer',
        background: active === name ? styling._primary_300 : '#f2f2f2',
        padding: pd || '0.8rem',
        borderRadius: 5,
        pointerEvents: disconnect ? 'none' : 'visible',
        ...styles,
      }}
      onClick={() => {
        adjustMany({ active: name, current: null });
        href && pickEmitter(href).click();
        return onPress && onPress();
      }}
      className={cls || 'bn'}
    >
      <i
        className={icon || ''}
        style={{ fontSize: narrow ? '1.3rem' : '1rem', ...iconStyle }}
      ></i>

      {!narrow && <p style={titleStyles}>{title || ''}</p>}

      <Link to={href || '/'} style={{ visibility: 'hidden' }} id={href}></Link>
    </div>
  );
};

export const IntIcons = ({ name, onPress, color, cls, show }) => {
  return (
    <div
      style={{
        padding: '0.5rem 1rem',
        background: 'darkred',
        color: 'white',
        cursor: 'pointer',
      }}
      onClick={onPress}
      className={cls || 'bns'}
    >
      {!show && (
        <i className={name || 'fas fa-times-circle'} style={{ color }}></i>
      )}
    </div>
  );
};

export const SquareBox = ({
  Child,
  name,
  cls,
  onPress,
  size,
  value,
  icon,
  onChange,
  cb,
}) => {
  const [focus, setFocus] = React.useState(false);
  const [text, setText] = React.useState('');

  return (
    <div
      className={cls || ''}
      onClick={cb}
      style={{
        width: size || '150px',
        ...styling.flexed({ gap: '1px' }),
        borderRadius: 5,
        border: !focus ? '1px solid #ccc' : `1px solid ${styling._primary_400}`,
        cursor: 'pointer',
        padding: 5,
      }}
    >
      {Child ? (
        <Child />
      ) : (
        <INPUTs
          typed={'search'}
          name={'text'}
          value={text}
          onChange={(nm, value) => setText(value)}
          placeholder={'Type here...'}
          onFocus={() => setFocus(true)}
          onBlur={() => setFocus(false)}
        />
      )}

      <i
        className={icon || 'fas fa-chevron-down'}
        onClick={() => onPress && onPress(text)}
        style={{
          cursor: 'pointer',
          padding: '0.5rem',
        }}
      ></i>
    </div>
  );
};

export const AppCard = ({
  size,
  height,
  bg,
  onPress,
  appName,
  appID,
  icon,
  semicolor,
}) => {
  return (
    <div
      className="bk"
      style={{
        backgroundColor: 'white',
        height: `${height || 150}px`,
        marginBottom: '1rem',
        width: `${size || 250}px`,
        boxShadow: styling._shadow_6,
        cursor: 'pointer',
        ...styling.flexed({ justify: 'center', gap: '0', direction: 'column' }),
      }}
      onClick={onPress}
    >
      <div
        style={{ flex: 1, padding: '0.5rem', height: '100%', width: '100%' }}
      >
        <h5 style={{ color: '#333' }}>APP ID: {appID || ''}</h5>
        <h4>{appName || ''}</h4>
      </div>

      <div
        style={{
          flex: 2,
          padding: '1rem',
          height: '100%',
          width: '100%',
          background: bg || styling._primary_500,
        }}
        className="flex"
      >
        <div
          className="flex"
          style={{
            padding: 10,
            borderRadius: '50%',
            width: '30%',
            background: semicolor || styling._primary_200,
          }}
        >
          <i
            className={icon || 'fa fa-tachometer'}
            style={{
              fontSize: '1.5rem',
              padding: '0.5rem',
              color: bg || styling._primary_500,
            }}
          ></i>
        </div>
      </div>
    </div>
  );
};

export const Item3Box = ({ name, value, btn, bg, disabled, size, cb }) => {
  return (
    <div
      style={{
        width: size || '20%',
        // height: 60,
        ...styling.flexed({
          justify: 'center',
          item: 'center',
          direction: 'column',
        }),
        backgroundColor: bg || styling._primary_500,
        color: 'white',
        margin: '10px 0 10px',
      }}
    >
      <h5
        style={{
          textAlign: 'center',
          width: '100%',
          padding: 10,
          borderBottom: '2px solid white',
        }}
      >
        {name || 'Item Name'}
      </h5>

      <h2>{value || 0}</h2>

      <button
        className={disabled ? '' : 'bns'}
        style={{
          padding: '0.7rem 1.5rem',
          backgroundColor: styling._primary_200,
          outline: 'none',
          border: 'none',
          cursor: !disabled ? 'pointer' : 'default',
          width: '90%',
          margin: '0 auto',
          marginBottom: 7,
          color: disabled ? 'grey' : '',
        }}
        onClick={() => cb && cb()}
      >
        {btn || 'Button'}
      </button>
    </div>
  );
};

export const Pagination = ({ arrayItem, parentState, parentChild, count }) => {
  const { current, view, item } = parentChild;

  const change = (quest) => {
    if (arrayItem.length > view && quest >= 0) {
      return parentState({
        ...parentChild,
        arr: item.slice(current + quest, current + quest + count),
        current: current + (quest || 0),
        view: quest + view,
      });
    }

    if (typeof quest === 'number' && quest < 0 && current >= count) {
      return parentState({
        ...parentChild,
        arr: item.slice(current + quest, current + count + quest),
        current: current + (quest || 0),
        view: view + quest,
      });
    }
  };

  return (
    <div style={{ ...styling.flexed({}) }}>
      <i
        className="fas fa-angle-left pagi hoves"
        onClick={() => change(-count)}
        style={{ cursor: 'pointer' }}
      ></i>

      {Object.keys(item[0]).length > 0 && (
        <h6 style={{ fontSize: '0.7rem' }}>{`${current + 1}-${view}`}</h6>
      )}

      <i
        className="fas fa-angle-right pagi hoves"
        onClick={() => change(count)}
        style={{ cursor: 'pointer' }}
      ></i>
    </div>
  );
};

export const DashBox = ({ title, value, perf, icon }) => {
  return (
    <div
      style={{
        width: 250,
        padding: 15,
        borderRadius: 10,
        backgroundColor: 'white',
      }}
    >
      <i
        className={icon || 'fas fa-pager'}
        style={{ fontSize: '2rem', color: styling._primary_700 }}
      ></i>
      <p style={{ fontWeight: 'bold' }}>{title || 'Box title'}</p>

      <div style={{ ...styling.flexed({}) }}>
        <h1 style={{ color: styling._primary_500 }}>
          {perf ? `${(perf * 100).toFixed(1)}%` : 0}
        </h1>
        <p style={{ color: '#333', fontSize: '0.8rem', fontWeight: 'bold' }}>
          {value || '0'}
        </p>
      </div>
      <div
        style={{
          background: styling._primary_200,
          width: '100%',
          height: 15,
          borderRadius: 5,
        }}
      >
        <div
          style={{
            background: styling._primary_400,
            width: `${perf ? `${perf * 100}%` : 0}`,
            height: 15,
            color: 'white',
            borderRadius: 5,
            fontSize: '0.8rem',
            transition: 'width 0.7s',
            ...styling.flexed({ justify: 'center' }),
          }}
        ></div>
      </div>
    </div>
  );
};

export const LEGEND = ({ label, data, sub, title }) => {
  return (
    <table className="tab" style={{ fontSize: '0.7rem' }}>
      <thead>
        {title && (
          <tr>
            <th>{title || ''}</th>
            <th>{'Allocation'}</th>
            <th>{'Installation'}</th>
          </tr>
        )}
      </thead>
      <tbody>
        {label.length === data.length &&
          label.map((el, index) => {
            return (
              <tr key={index}>
                <td style={{ width: '50%' }}>{el}</td>
                <td style={{ textAlign: 'right' }}>
                  <div
                    style={{
                      ...styling.flexed({
                        justify: 'flex-start',
                        gap: '5px',
                      }),
                    }}
                  >
                    <div
                      style={{
                        width: 7,
                        height: 4,
                        backgroundColor: styling._primary_100,
                      }}
                    ></div>
                    <p>{data[index] || 0}</p>
                  </div>
                </td>
                <td style={{ textAlign: 'right' }}>
                  <div
                    style={{
                      ...styling.flexed({
                        justify: 'flex-start',
                        gap: '5px',
                      }),
                    }}
                  >
                    <div
                      style={{
                        width: 7,
                        height: 4,
                        backgroundColor: styling._primary_500,
                      }}
                    ></div>
                    <p>{sub[index] || 0}</p>
                  </div>
                </td>
              </tr>
            );
          })}
      </tbody>
    </table>
  );
};
